import React from 'react'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'

// Styles
import classes from './Approve.module.scss'

const SuccessDialog = props => {
  const { page, openSuccessDialog, setOpenSuccessDialog, submitFunction } = props

  const handleOnCloseSuccessDialog = () => {
    setOpenSuccessDialog(false)
  }

  const handleOnSuccessDialog = () => {
    submitFunction()
  }

  return (
    <Dialog
      fullWidth
      maxWidth='xs'
      onClose={handleOnCloseSuccessDialog}
      aria-labelledby='customized-dialog-title'
      open={openSuccessDialog}
      className={classes.successroot}
      PaperProps={{ style: { display: 'flex' } }}
    >
      <DialogTitle disableTypography className={classes['header-container']}>
        <div className={classes.header}>
          <IconButton
            aria-label='close'
            id='success-close-button'
            className={classes['close-button']}
            onClick={handleOnCloseSuccessDialog}
          >
            <CloseIcon />
          </IconButton>
          <Typography variant='h6' id={`${page}-title`}className={classes['title']}>
            {page === 'soa'
              ? 'Statement of Account (SOA) Successfully Approved'
              : page === 'credit'
                ? 'Approve Credit Memo'
                : page === 'debit'
                  ? 'Approve Debit Memo'
                  : page === 'invoice'
                    ? 'Approve Invoice Transaction'
                    : ''}
          </Typography>
        </div>
      </DialogTitle>
      <DialogContent>
        {page === 'soa'
          ? 'Statement of Account (SOA) was successfully approved and will be included in the scheduled bill run for release.'
          : page === 'credit'
            ? 'Credit Memo was successfully approved by the Supervisor and Manager.'
            : page === 'debit'
              ? 'Debit Memo was successfully approved by the Supervisor and Manager.'
              : page === 'invoice'
                ? 'Invoice Transaction was successfully approved by the Supervisor and Manager.'
                : ''}
        <div className={classes.button}>
          <Button
            variant='contained'
            id={`${page}-ok-button`}
            className={classes['button-positive']}
            onClick={handleOnSuccessDialog}
          >
            OK
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  )
}

export default function CustomizedDialogs (props) {
  const { page, openDialog, setOpenDialog, submitFunction, setActiveStep } = props
  const [openSuccessDialog, setOpenSuccessDialog] = React.useState(false)

  const handleOnClose = () => {
    if (page === 'credit' || page === 'debit' || page === 'invoice') {
      setOpenDialog(false)
    } else {
      setActiveStep(0)
      setOpenDialog(false)
    }
  }

  const onShowSuccessDialog = () => {
    if (openDialog === true) {
      setOpenSuccessDialog(true)
      if (openSuccessDialog === true) setOpenDialog(false)
    }
  }

  return (
    <React.Fragment>
      <Dialog
        fullWidth
        maxWidth='xs'
        onClose={handleOnClose}
        aria-labelledby='customized-dialog-title'
        open={openDialog}
        className={classes.root}
        // style={page === 'soa' ? { 'max-height': '460px' } : {}}
        PaperProps={{ style: { height: '400px', display: 'flex' } }}
      >
        {page === 'soa' ? (
          <React.Fragment>
            <DialogTitle disableTypography className={classes['header-container']}>
              <div className={classes.header}>
                <IconButton
                  aria-label='close'
                  id='confirm-close-button'
                  className={classes['close-button']}
                  onClick={handleOnClose}
                >
                  <CloseIcon />
                </IconButton>
                <Typography variant='h6' id='confirm-title' className={classes['title']}>
                Approve Statement of Account
                </Typography>
              </div>
            </DialogTitle>

            <DialogContent id='debit-content'>
            Are you sure you want to approve the Statement of Account?
              <div className={classes.button}>
                <Button
                  variant='outlined'
                  id='soa-cancel-button'
                  className={classes['button-negative']}
                  onClick={handleOnClose}
                >
                No
                </Button>
                <Button
                  variant='contained'
                  id='soa-submit-button'
                  className={classes['button-positive']}
                  onClick={onShowSuccessDialog}
                >
                Yes
                </Button>
              </div>
            </DialogContent>
          </React.Fragment>
        ) : (
          ''
        )}
        {page === 'credit' ? (
          <React.Fragment>
            <DialogTitle disableTypography className={classes['header-container']}>
              <div className={classes.header}>
                <IconButton
                  aria-label='close'
                  id='confirm-close-button'
                  className={classes['close-button']}
                  onClick={handleOnClose}
                >
                  <CloseIcon />
                </IconButton>
                <Typography variant='h6' id='confirm-title' className={classes['title']}>
                  Approve Credit Memo
                </Typography>
              </div>
            </DialogTitle>

            <DialogContent id='debit-content'>
              Are you sure you want to approve the credit memo entry?
              <div className={classes.button}>
                <Button
                  variant='outlined'
                  id='soa-cancel-button'
                  className={classes['button-negative']}
                  onClick={handleOnClose}
                >
                  No
                </Button>
                <Button
                  variant='contained'
                  id='soa-submit-button'
                  className={classes['button-positive']}
                  onClick={onShowSuccessDialog}
                >
                  Yes
                </Button>
              </div>
            </DialogContent>
          </React.Fragment>
        ) : (
          ''
        )}
        {page === 'debit' ? (
          <React.Fragment>
            <DialogTitle disableTypography className={classes['header-container']}>
              <div className={classes.header}>
                <IconButton
                  aria-label='close'
                  id='confirm-close-button'
                  className={classes['close-button']}
                  onClick={handleOnClose}
                >
                  <CloseIcon />
                </IconButton>
                <Typography variant='h6' id='confirm-title' className={classes['title']}>
                  Approve Debit Memo
                </Typography>
              </div>
            </DialogTitle>

            <DialogContent id='debit-content'>
              Are you sure you want to approve the debit memo entry?
              <div className={classes.button}>
                <Button
                  variant='outlined'
                  id='soa-cancel-button'
                  className={classes['button-negative']}
                  onClick={handleOnClose}
                >
                  No
                </Button>
                <Button
                  variant='contained'
                  id='soa-submit-button'
                  className={classes['button-positive']}
                  onClick={onShowSuccessDialog}
                >
                  Yes
                </Button>
              </div>
            </DialogContent>
          </React.Fragment>
        ) : (
          ''
        )}
        {page === 'invoice' ? (
          <React.Fragment>
            <DialogTitle disableTypography className={classes['header-container']}>
              <div className={classes.header}>
                <IconButton
                  aria-label='close'
                  id='confirm-close-button'
                  className={classes['close-button']}
                  onClick={handleOnClose}
                >
                  <CloseIcon />
                </IconButton>
                <Typography variant='h6' id='confirm-title' className={classes['title']}>
                  Approve Invoice
                </Typography>
              </div>
            </DialogTitle>

            <DialogContent id='invoice-content'>
              Are you sure you want to approve the invoice entry?
              <div className={classes.button}>
                <Button
                  variant='outlined'
                  id='soa-cancel-button'
                  className={classes['button-negative']}
                  onClick={handleOnClose}
                >
                  No
                </Button>
                <Button
                  variant='contained'
                  id='soa-submit-button'
                  className={classes['button-positive']}
                  onClick={onShowSuccessDialog}
                >
                  Yes
                </Button>
              </div>
            </DialogContent>
          </React.Fragment>
        ) : (
          ''
        )}
      </Dialog>
      <SuccessDialog
        page={page}
        openSuccessDialog={openSuccessDialog}
        setOpenSuccessDialog={setOpenSuccessDialog}
        submitFunction={submitFunction}
      />
    </React.Fragment>
  )
}
