import Joi from '@hapi/joi'

// Services
import ExtractValidationResult from 'Services/ExtractValidationResult'

export const Validate = _data => {
  const schema = Joi.object({
    contact_person: Joi.string()
      .trim()
      .label('Contact Person')
      .required()
      .messages({ 'string.empty': `"Contact Person" is required.` }),
    contact_number: Joi.string()
      .trim()
      .label('Contact Number')
      .required()
      .messages({ 'string.empty': `"Contact Number" is required.` }),
    collection_date_start: Joi.date()
      .label('Collection Date Start')
      .min('now')
      .required()
      .messages({
        'date.base': `"Collection Date Start" is required.`,
        'date.min': `"Collection Date Start" should be today or future`
      }),
    collection_date_end: Joi.date()
      .label('Collection Date End')
      .required()
      .min(Joi.ref('collection_date_start'))
      .messages({
        'date.base': `"Collection Date End" is required.`,
        'date.min': `"Collection Date End" should be after Collection Date Start`
      }),
    collection_location: Joi.string()
      .trim()
      .label('Collection Location')
      .required()
      .messages({ 'string.empty': `"Collection Location" is required.` }),
    collection_site: Joi.string()
      .trim()
      .label('Collection Site')
      .required()
      .messages({ 'string.empty': `"Collection Site" is required.` }),
  }).options({ abortEarly: false })

  return ExtractValidationResult(schema.validate(_data))
}
