import React, { useState, useEffect } from 'react'
import { Button } from '@material-ui/core'
import TableCell from '@material-ui/core/TableCell'
import TableBody from '@material-ui/core/TableBody'
import TableRow from '@material-ui/core/TableRow'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import IconButton from '@material-ui/core/IconButton'
import ViewListRoundedIcon from '@material-ui/icons/ViewListRounded'
import _ from 'lodash'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'

// Hooks
import { useHistory, Link } from 'react-router-dom'
import useForm from 'Hooks/useForm'

// Components
import PageFooter from '../../../Components/PageFooter/PageFooter'
import TextField from '../../../Components/FormComponents/TextField/CustomTextField'
import SearchTextComponent from 'SharedComponents/SearchTextComponent/SearchTextComponent'
import CustomEnhancedTable from './BillingTaxChargingCustomTable'
import CustomDialog from '../../../Components/BillingMaintenanceDialog/CustomizedDialogs'
// Utilities
import get from 'lodash/get'

// MuiStyles
import { withStyles } from '@material-ui/core/styles'

// Styles
import classes from './BillingTaxChargingSetup.module.scss'

const header_items = [
  {
    id: 'tax-code-id',
    numeric: false,
    disablePadding: false,
    label: 'Tax Code*',
    center: false,
    search: true,
    sort: true,
    accessor: 'tax_code'
  },
  {
    id: 'tax-type-id',
    numeric: false,
    disablePadding: false,
    label: 'Tax Type*',
    center: false,
    search: true,
    sort: true,
    accessor: 'tax_type'
  },
  {
    id: 'description-id',
    numeric: false,
    disablePadding: false,
    label: 'Description',
    search: true,
    sort: true,
    accessor: 'description'
  },
  {
    id: 'percentage-id',
    numeric: false,
    disablePadding: false,
    label: 'Percentage',
    search: true,
    sort: true,
    accessor: 'percentage'
  }
]

const StyledButton = withStyles({
  root: {
    background: '#3ab77d',
    '&:hover': {
      background: '#0b4930'
    },
    borderRadius: 3,
    boxShadow: 'none',
    border: 0,
    color: 'white',
    height: 45,
    width: '100%'
  },
  label: {
    textTransform: 'capitalize'
  }
})(Button)

export default function BillingTaxChargingSetup () {
  const history = useHistory()

  const form = useForm({
    tax_code: '',
    tax_type: '',
    description: '',
    percentage: ''
  })

  /** USE ONLY FOR SAMPLE DATA DELETE IF SERVICES IS AVAILABLE */
  const results = [
    {
      tax_code: 'TX01',
      tax_type: 'VatTable',
      description: 'Sample Description',
      percentage: 0.12
    },
    {
      tax_code: 'TX02',
      tax_type: 'VatTable',
      description: 'Sample Description',
      percentage: 0.12
    },
    {
      tax_code: 'TX03',
      tax_type: 'VatTable',
      description: 'Sample Description',
      percentage: 0.12
    }
  ]

  const initialValue = {
    show: false,
    count: 1,
    search: '',
    limit: 10,
    page: 1,
    filters: null,
    body: [],
    orderBy: 'tax_code',
    order: true,
    sort: '',
    accessors: ['tax_code', 'tax_type', 'description', 'percentage']
  }
  const [list, setList] = useState(initialValue)
  const [params, setParams] = useState({})
  const [openDialog, setOpenDialog] = useState(false)
  const [index, setIndex] = useState()
  const [edit, setEdit] = useState(false)
  const [page, setPage] = useState('')

  const storeBillingCode = () => {
    try {
      const error = {}
      error.tax_type = !form.values.tax_type ? 'Please enter tax type' : ''
      error.description = !form.values.description ? 'Please enter description' : ''
      error.percentage = !form.values.percentage ? 'Please enter percentage' : ''
      form.setErrors(error)
      if (!error.tax_type && !error.description && !error.percentage) {
        if (edit) {
          /** ENTER YOUR EDIT CODE IN SERVICES */
          setOpenDialog(true)
          setPage('update')
          setEdit(false)
          form.reset()
        }
      }
    } catch (error) {
      console.log(`Error storing template image attachment: ${error}`)
    }
  }

  const handleDeactivate = () => {
    // setPage('deactivate')
    setOpenDialog(true)
  }

  const editBillingCode = detailIndex => {
    setEdit(true)
    const filtered = list.body[detailIndex]
    setIndex(filtered.tax_code)
    form.setErrors(null)
    const formValues = {
      tax_code: filtered.tax_code,
      tax_type: filtered.tax_type,
      description: filtered.description,
      percentage: filtered.percentage
    }
    form.setValues(formValues)
  }

  useEffect(() => {
    BreadcrumbsService.setSelectedItems(['collectionMaintenance'])
    fetchData()
  }, [])

  const fetchData = (params = {}) => {
    console.log(params.search)
    const _params = {
      sort: `${list.orderBy}|${list.order ? 'asc' : 'desc'}`,
      filters: list.filters,
      search: list.search,
      accessors: list.accessors,
      ...params
    }
    setParams(_params)
    console.log(_params)
    let count = 0
    for (let i = 0; i > results.length; i++) {
      count = i
    }
    /** ONLY FOR UI DATA SAMPLE PURPOSES ONLY */
    const orderByValue = _params.orderBy ? _params.orderBy : list.orderBy
    const orderValue = typeof _params.order === 'boolean' ? _params.order : list.order
    setList(i => ({ ...i, body: results, count, sort: _params.sort, orderBy: orderByValue, order: orderValue }))
    /** USE ONLY IF THERE IS A SERVICES APPLIED */
    // InvoiceServices.getInvoices(_params).then(({ data: res }) => {
    //     const { results, count, limit, page } = res.data
    //     const orderByValue = _params.orderBy ? _params.orderBy : list.orderBy
    //     const orderValue = typeof _params.order === "boolean" ? _params.order : list.order
    //     setList(i => ({ ...i, body: results, count, limit, page, sort: _params.sort, orderBy: orderByValue, order: orderValue }))
    //     setIsFetchingState(false)
    //   })
}

  const mainSearchChange = _.debounce(e => {
    const params = {
      search: e.target.value
    }
    fetchData(params)
  }, 500)

  return (
    <div className={classes.root}>
      <Grid container spacing={2} style={{ marginTop: '1rem', padding: '0 0 10px 10px' }}>
        <Grid item container spacing={4} xs={12}>
          <Grid item xs={2}>
            <Typography variant='subtitle2' style={{ padding: '15px 0 0 15px', fontWeight: 'bold' }}>
              Tax Type *
            </Typography>
          </Grid>

          <Grid item xs={3}>
            <TextField
              variant='outlined'
              size='small'
              name='tax_type'
              placeholder='Enter Tax Type'
              value={form.values.tax_type}
              onChange={form.onChange}
              TextFieldStyle={{marginBottom: '0px', borderColor: '#c7c7c7'}}
              error={get(form.errors, 'tax_type')}
            />
          </Grid>
        </Grid>

        <Grid item container spacing={2} xs={12}>
          <Grid item xs={2}>
            <Typography variant='subtitle2' style={{ padding: '15px 0 0 15px', fontWeight: 'bold' }}>
              Description *
            </Typography>
          </Grid>

          <Grid item xs={9}>
            <TextField
              variant='outlined'
              size='small'
              name='description'
              placeholder='Enter Tax Type Description'
              value={form.values.description}
              onChange={form.onChange}
              TextFieldStyle={{marginBottom: '0px', borderColor: '#c7c7c7'}}
              error={get(form.errors, 'description')}
            />
          </Grid>
        </Grid>

        <Grid item container spacing={4} xs={12}>
          <Grid item xs={2}>
            <Typography variant='subtitle2' style={{ padding: '15px 0 0 15px', fontWeight: 'bold' }}>
              Percentage *
            </Typography>
          </Grid>

          <Grid item xs={3}>
            <TextField
              variant='outlined'
              size='small'
              name='percentage'
              placeholder='Enter Percentage'
              value={form.values.percentage}
              onChange={form.onChange}
              TextFieldStyle={{marginBottom: '0px', borderColor: '#c7c7c7'}}
              error={get(form.errors, 'percentage')}
            />
          </Grid>
          <Grid item xs={0}>
            <Typography variant='subtitle2' style={{ padding: '15px 0 0 15px', fontWeight: 'bold' }}>
              %
            </Typography>
          </Grid>

          <Grid item xs={2}>
            <StyledButton style={{ marginTop: '5px' }} onClick={() => storeBillingCode()} >{edit ? 'Update Tax Charging' : 'Add Tax Charging'}</StyledButton>
          </Grid>
        </Grid>

        <Grid item container spacing={2} xs={12} style={{margin: '10px 10px 0 0'}}>
          <Grid item xs={9}>
            <SearchTextComponent
              placeholder='Search from the list of Tax Charging'
              onChange={e => {
                e.persist()
                mainSearchChange(e)
              }}
            />
          </Grid>
          <Grid item xs={3}>
            <Link id='display-settings-link' className={classes['display-settings']} to='/' color='primary' size='medium'>
              <IconButton id='display-settings-icon'>
                <ViewListRoundedIcon className={classes['display-settings-icon']} />
                <Typography className='text-link underline' style={{color: '#3ab77d'}}>Display Settings</Typography>
              </IconButton>
            </Link>
          </Grid>
        </Grid>
      </Grid>

      <div style={{ padding: '0 0 20px 20px' }}>
        <CustomEnhancedTable count={list.count} tableHeaders={header_items} search sort fetchData={fetchData} params={params} >
          <TableBody>
            {list &&
                list.body.map((detail, detailIndex) => (
                  <TableRow style={{ backgroundColor: 'red' }}hover tabIndex={-1} key={detailIndex}>
                    <TableCell
                      key={detailIndex}
                      align='left'
                    >
                      {detail.tax_code}
                    </TableCell>
                    <TableCell
                      key={detailIndex}
                      align='left'
                    >
                      {detail.tax_type}
                    </TableCell>
                    <TableCell
                      key={detailIndex}
                      align='left'
                    >
                      {detail.description}
                    </TableCell>
                    <TableCell
                      key={detailIndex}
                      align='left'
                    >
                      {detail.percentage}
                    </TableCell>
                    <TableCell
                      key={detailIndex}
                      align='cetner'
                    >
                      <Link onClick={() => editBillingCode(detailIndex)}>
                        <Typography variant='caption' className='text-link underline'>
                          Edit
                        </Typography>
                      </Link>
                    </TableCell>
                    <TableCell
                      key={detailIndex}
                      align='center'
                    >
                      <Link onClick={() => handleDeactivate()}>
                        <Typography variant='caption' className='text-link underline'>
                          Deactivate
                        </Typography>
                      </Link>
                    </TableCell>
                  </TableRow>
                ))}
          </TableBody>
        </CustomEnhancedTable>
      </div>
      <PageFooter isControl handleOnCloseClicked={() => history.push('/')} />
      {openDialog === true ? (
        <CustomDialog
          page={page}
          openDialog={openDialog}
          setOpenDialog={setOpenDialog}
        />
      ) : ''}
    </div>
  )
}
