/** React Libraries */
import React from 'react'
import { Link, useHistory } from 'react-router-dom'
/** Material UI Libraries */
import { Typography, Backdrop, CircularProgress, Box } from '@material-ui/core'
import Button from '@material-ui/core/Button'
import RemoveCircleIcon from '@material-ui/icons/RemoveCircle'
import ReplyIcon from '@material-ui/icons/Reply'
import VisibilityIcon from '@material-ui/icons/Visibility'
import TableCell from '@material-ui/core/TableCell'
import TableBody from '@material-ui/core/TableBody'
import TableRow from '@material-ui/core/TableRow'
/** RBAC */
/** Utilities */
import * as moment from 'moment'
import { CurrencyFormatter } from 'Utilities/TextFormatter'
/** Standard Components */
import ContentHeader from 'StandardComponents/ContentHeader/ContentHeader'
/** Shared Components */
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import SALES_CHANNEL from 'Pages/Protected/BillingAndCollection/Constant/SalesChannelTypes'
/** Page Components */
import HeaderDetails from '../../Components/HeaderDetails/HeaderDetails'
import FormDrawer from '../../Components/FormDrawer/FormDrawer'
import CustomEnhancedTable from '../../Components/CustomEnhancedTable/CustomEnhancedTable'
import Approve from 'StandardComponents/Approve/Approve'
import Disapprove from 'StandardComponents/Disapprove/Disapprove'
import Void from '../../Components/Void/Void'
/** CSS */
import classes from './ClientDebitMemosToValidatePage.module.scss'
/** Services */
// import BillingTransactionService from 'Services/Api/Billing/Transactions/BillingTransactionService'
import BillingDebitMemoServices from 'Services/Api/Billing/Transactions/DebitMemo/BillingDebitMemoServices'

// Security
import RBAC from 'Security/RBAC'

const tableHeaders = [
  {
    id: 'pending-debit-transaction-type',
    numeric: false,
    disablePadding: false,
    label: 'Choose Billing Code & Type *',
    center: true
  },
  {
    id: 'pending-debit-member-account-id',
    numeric: false,
    disablePadding: false,
    label: 'Enter Member ID',
    center: true
  },
  {
    id: 'pending-debit-member-name',
    numeric: false,
    disablePadding: false,
    label: 'Enter Member Name',
    center: true
  },
  {
    id: 'pending-debit-description',
    numeric: false,
    disablePadding: false,
    label: 'Enter Description *',
    center: true
  },
  {
    id: 'pending-debit-amount',
    numeric: false,
    disablePadding: false,
    label: 'Enter Taxable Amount *',
    center: true
  },
  {
    id: '',
    numeric: false,
    disablePadding: false,
    label: '',
    center: true
  }
]

export default function DebitMemoToBeRevised (props = {}) {
  PageTitleService()
  const history = useHistory()
  const [openDialogVoid, setOpenDialogVoid] = React.useState(false)
  const [openDialogApprove, setOpenDialogApprove] = React.useState(false)
  const [openDialogDisapprove, setOpenDialogDisapprove] = React.useState(false)
  const transaction_number = props.match.params.transaction_number
  const type = props.match.params.type

  const [pendingDebitMemos, setPendingDebitMemos] = React.useState({
    data: []
  })

  const [disabledSave, setDisabledSave] = React.useState(false)

  const [backdrop, setBackdrop] = React.useState(false)

  const [debitDetail, setDebitMemoDetail] = React.useState({})
  React.useEffect(() => {
    BreadcrumbsService.setSelectedItems(['dashboard', 'validateDebitMemoList', `validateDebitMemo`])
    Promise.all([BillingDebitMemoServices.getTobeRevisedDebitMemo({ transaction_number })]).then(values => {
      console.log(values)
      setPendingDebitMemos(prevState => ({
        ...prevState,
        data: values[0].data.data
      }))
    })

    // TODO API CALL FOR CURRENT  DEBIT MEMO TRANSACTIONS
    fetchData({})
  }, [])

  const initialValue = {
    body: [],
    count: 0,
    filters: null,
    header: [],
    limit: 10,
    order: true,
    orderBy: '_id',
    page: 1,
    sort: ''
  }

  const [list, setList] = React.useState(initialValue)
  const [params, setParams] = React.useState({})

  const fetchData = params => {
    const _params = {
      limit: list.limit,
      page: list.page,
      sort: `${list.orderBy}|${list.order ? 'asc' : 'desc'}`,
      filters: list.filters,
      transactionNumber: transaction_number,
      ...params
    }
    setParams(_params)

    BillingDebitMemoServices.getDebitMemoDetails(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      const orderByValue = _params.orderBy ? _params.orderBy : list.orderBy
      const orderValue = typeof _params.order === 'boolean' ? _params.order : list.order
      setList(i => ({ ...i, body: results, count, limit, page, sort: _params.sort, orderBy: orderByValue, order: orderValue }))
    })
  }

  const transactionInfo = pendingDebitMemos.data
  const clientInfo = pendingDebitMemos.data.client
  const billingInfo = pendingDebitMemos.data.billing
  const contractInfo = pendingDebitMemos.data.contract
  const summaryInfo = pendingDebitMemos.data.summary
  const dateInfo = pendingDebitMemos.data.date
  const debitInfo = debitDetail.data
  function handleClose () {
    history.push('/')
  }
  console.log(clientInfo)
  console.log(contractInfo)
  console.log(pendingDebitMemos.data)
  const handleVoid = async () => {
    setBackdrop(true)
    await BillingDebitMemoServices.assignSupervisor({ transaction_number: transaction_number })
    BillingDebitMemoServices.routeDebitMemo({ transaction_number: transaction_number, action: 'void' })

    // BillingTransactionService.void({ transactionNo:transaction_number, transactionType: 'DEBIT_MEMO' }) // TODO add enum

    history.push('/')
  }

  const handleDisapprove = async () => {
    setBackdrop(true)
    await BillingDebitMemoServices.assignSupervisor({ transaction_number: transaction_number })
    BillingDebitMemoServices.routeDebitMemo({ transaction_number: transaction_number, action: 'disapprove' })

    // BillingTransactionService.disapprove({ transactionNo:transaction_number, transactionType: 'DEBIT_MEMO' }) // TODO add enum

    history.push('/')
  }

  const handleApprove = async () => {
    setBackdrop(true)
    await BillingDebitMemoServices.assignSupervisor({ transaction_number: transaction_number })
    BillingDebitMemoServices.routeDebitMemo({ transaction_number: transaction_number, action: 'approve' })

    // BillingTransactionService.approve({ transactionNo:transaction_number, transactionType: 'DEBIT_MEMO' }) // TODO add enum

    history.push('/')
  }

  function handleApproveModal () {
    setOpenDialogApprove(true)
  }

  function handleDisapproveModal () {
    setOpenDialogDisapprove(true)
  }

  function handleVoidModal () {
    setOpenDialogVoid(true)
  }

  const pagination = {
    count: list ? list.count : 0,
    rowsPerPage: list ? list.limit : 0,
    page: list ? list.page : 0
  }

  const changeView = params => {
    fetchData(params)
  }

  return (
    <div>
      <HeaderDetails>
        <Typography id='header-details-corporate-name' variant='h5'>
          Validate Debit Memo
        </Typography>
      </HeaderDetails>
      <FormDrawer
        drawerHeight={'570px'}
        drawerContent={
          <div>
            <Typography variant='h6'>{clientInfo && clientInfo.account_name}</Typography>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-account-number-field' color='textSecondary'>
                ACCOUNT NO.
              </Typography>
              <Typography className={classes['drawer-account-value-field']} id='drawer-account-number-value'>{clientInfo && clientInfo.account_number}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-account-status-field' color='textSecondary'>
                ACCOUNT STATUS
              </Typography>
              <Typography className={classes['drawer-account-value-field']} id='drawer-account-status-value'>{clientInfo && clientInfo.account_status}</Typography>
            </div>
            <div className={classes['drawer-content']}>
              <Typography id='drawer-payment-term-field' color='textSecondary'>
                PAYMENT TERM
              </Typography>
              <Typography className={classes['drawer-account-value-field']} id='drawer-payment-term-value'>{contractInfo && contractInfo.payment_term}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-tax-type-field' color='textSecondary'>
                TAX TYPE
              </Typography>
              <Typography className={classes['drawer-account-value-field']} id='drawer-tax-type-value'>{clientInfo && clientInfo.tax_type}</Typography>
            </div>
            <div className={classes['drawer-content']}>
              <Typography id='drawer-corporate-tin-field' color='textSecondary'>
                ACCOUNT TIN
              </Typography>
              <Typography className={classes['drawer-account-value-field']} id='drawer-corporate-tin-value'>{clientInfo && clientInfo.tin}</Typography>
            </div>
            <div className={classes['drawer-content']}>
              <Typography id='drawer-billing-cyle-field' color='textSecondary'>
                BILLING CYCLE
              </Typography>
              <Typography className={classes['drawer-account-value-field']} id='drawer-billing-cyle-value'>
                {billingInfo && billingInfo.cycle.code + ' - ' + billingInfo.cycle.name}
              </Typography>
            </div>
            <div className={classes['drawer-content']}>
              <Typography id='drawer-contract-plan-type-field' color='textSecondary'>
                CONTRACT TYPE
              </Typography>
              <Typography className={classes['drawer-account-value-field']} id='drawer-contract-plan-type-value'>{contractInfo && contractInfo.type}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-effectivity-date-field' color='textSecondary'>
                EFFECTIVITY DATE
              </Typography>
              <Typography className={classes['drawer-account-value-field']} id='drawer-effectivity-date-value'>
                {moment(contractInfo && contractInfo.effectivity).format('MMM DD, YYYY')}
              </Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-expiration-date-field' color='textSecondary'>
                EXPIRATION DATE
              </Typography>
              <Typography className={classes['drawer-account-value-field']} id='drawer-expiration-date-value'>
                {moment(contractInfo && contractInfo.expiration).format('MMM DD, YYYY')}
              </Typography>
            </div>
            <div className={classes['drawer-content']}>
              <Typography id='drawer-sales-channel-field' color='textSecondary'>
            SALES CHANNEL
              </Typography>
              {clientInfo && clientInfo.sales_channel ? (
                <Typography id='drawer-sales-channel-value'>
                  <Box fontWeight='fontWeightBold'>
                    {clientInfo.sales_channel.type ? clientInfo.sales_channel.type : '-'}
                  </Box>
                </Typography>
              ) : (
                <Typography id='drawer-sales-channel-value'>
                  <Box fontWeight='fontWeightBold'>-</Box>
                </Typography>
              )}
            </div>

            <div className={classes['drawer-content']}>
              {clientInfo && clientInfo.sales_channel && (
                <React.Fragment>
                  <Typography id='drawer-sales-channel-name-field' color='textSecondary'>
                    {clientInfo.sales_channel.type === SALES_CHANNEL.AGENT
                      ? 'AGENT NAME'
                      : clientInfo.sales_channel.type === SALES_CHANNEL.BROKER
                        ? 'NAME OF COMPANY'
                        : clientInfo.sales_channel.type === SALES_CHANNEL.BDO && 'BDO NAME'}
                  </Typography>
                  <Typography id='drawer-sales-channel-name-value'>
                    <Box fontWeight='fontWeightBold'>
                      {clientInfo.sales_channel.name ? clientInfo.sales_channel.name : '-'}
                    </Box>
                  </Typography>
                </React.Fragment>
              )}
            </div>

            {clientInfo && clientInfo.sales_channel && clientInfo.sales_channel.type === SALES_CHANNEL.BROKER && (
              <React.Fragment>
                <div className={classes['drawer-content']}>
                  <Typography id='drawer-broker-name-field' color='textSecondary'>
                BROKER NAME
                  </Typography>
                  <Typography id='drawer-broker-name-value'>
                    <Box fontWeight='fontWeightBold'>
                      {clientInfo.sales_channel.contact_person ? clientInfo.sales_channel.contact_person : '-'}
                    </Box>
                  </Typography>
                </div>

                <div className={classes['drawer-content']}>
                  <Typography id='drawer-position-field' color='textSecondary'>
                POSITION
                  </Typography>
                  <Typography id='drawer-position-value'>
                    <Box fontWeight='fontWeightBold'>
                      {clientInfo.sales_channel.position ? clientInfo.sales_channel.position : '-'}
                    </Box>
                  </Typography>
                </div>

                <div className={classes['drawer-content']}>
                  <Typography id='drawer-contact-no-field' color='textSecondary'>
                CONTACT NO.
                  </Typography>
                  <Typography id='drawer-contact-no-value'>
                    <Box fontWeight='fontWeightBold'>
                      {clientInfo.sales_channel.contact_no ? clientInfo.sales_channel.contact_no : '-'}
                    </Box>
                  </Typography>
                </div>

                <div className={classes['drawer-content']}>
                  <Typography id='drawer-email-field' color='textSecondary'>
                EMAIL
                  </Typography>
                  <Typography id='drawer-email-value'>
                    <Box fontWeight='fontWeightBold'>
                      {clientInfo.sales_channel.email_address ? clientInfo.sales_channel.email_address : '-'}
                    </Box>
                  </Typography>
                </div>
              </React.Fragment>
            )}
            <div className={classes['drawer-content']}>
              <Typography id='drawer-location-field' color='textSecondary'>
                LOCATION
              </Typography>
              <Typography className={classes['drawer-account-value-field']} id='drawer-location-value'>{clientInfo && clientInfo.location}</Typography>
            </div>
          </div>
        }
      >
        <div className={classes['export-view-billing-cycle-wrapper']}>
          <div className={classes['export-view-content']}>
            <Link id='export-link' to={`/`} className={classes['export-link']}>
              <ReplyIcon id='export-icon' className={classes['export-icon']} />
              Export
            </Link>

            <Link id='view-billing-link' to={`/`} className={classes['view-link']}>
              <VisibilityIcon id='export-icon' className={classes['export-icon']} />
              View Bill Run Inclusion
            </Link>
          </div>
        </div>
        <ContentHeader
          summaryFields={['Total Taxable Amount', 'Total Tax (VAT) Amount', 'Total Amount']}
          summaryValues={[
            summaryInfo && summaryInfo.sub_total,
            summaryInfo && summaryInfo.tax_amount,
            summaryInfo && summaryInfo.sub_total + summaryInfo.tax_amount
          ]}
        >
          <div className={classes['content-header-details']}>
            <div className={classes.fields}>
              <Typography id='content-details-debit-number-field' variant='h6' color='textSecondary'>
                DEBIT MEMO NO.
              </Typography>
              <Typography id='content-details-trasaction-date-field' color='textSecondary'>
                TRANSACTION DATE
              </Typography>
              <Typography id='content-details-trasaction-date-field' color='textSecondary'>
                PERIOD COVERED
              </Typography>
              <Typography id='content-details-ticket-number-field' color='textSecondary'>
                TICKET NO
              </Typography>
              <Typography id='content-details-ticket-number-field' color='textSecondary'>
                REFERENCE NO
              </Typography>
            </div>
            <div className={classes.values}>
              <Typography id='content-details-debit-number-value' variant='h6'>
                {transaction_number}
              </Typography>
              <Typography className={classes['debit-memo-values']} id='content-details-trasaction-date-value'>
                {moment(dateInfo && dateInfo.created).format('MMM DD, YYYY')}
              </Typography>
              <Typography
                className={classes['debit-memo-values']}
                id='content-details-trasaction-date-value'
                style={{ width: '100px', 'white-space': 'nowrap', overflow: 'visible' }}
              >
                {moment(transactionInfo.transaction && transactionInfo.transaction.period.start).format('MMM DD, YYYY')}{' '}
                - {moment(transactionInfo.transaction && transactionInfo.transaction.period.end).format('MMM DD, YYYY')}
              </Typography>
              <Typography className={classes['debit-memo-values']} id='content-details-ticket-number-value' style={{ width: '100px' }}>
                {transactionInfo && transactionInfo.case_number}
              </Typography>
            </div>
          </div>
        </ContentHeader>
        <CustomEnhancedTable
          count={list.count}
          changeView={changeView}
          tableHeaders={tableHeaders}
          pagination={pagination}
          search
          sort
          fetchData={fetchData}
          params={params}
        >
          <TableBody>
            {list.body &&
              list.body.map((detail, detailIndex) => (
                <TableRow hover tabIndex={-1} key={detailIndex}>
                  <TableCell
                    // id={`${content[0]}-${contentsIndex}`}
                    key={detailIndex}
                    align='center'
                  >
                    {detail.billingCodeType}
                  </TableCell>
                  <TableCell
                    // id={`${content[0]}-${contentsIndex}`}
                    key={detailIndex}
                    align='center'
                  >
                    {detail.memberAccountId}
                  </TableCell>
                  <TableCell
                    // id={`${content[0]}-${contentsIndex}`}
                    key={detailIndex}
                    align='center'
                  >
                    {detail.memberName}
                  </TableCell>
                  <TableCell
                    // id={`${content[0]}-${contentsIndex}`}
                    key={detailIndex}
                    align='center'
                  >
                    {detail.description}
                  </TableCell>
                  <TableCell
                    // id={`${content[0]}-${contentsIndex}`}
                    key={detailIndex}
                    align='right'
                  >
                    {CurrencyFormatter(detail.taxableAmount)}
                  </TableCell>
                  <TableCell
                    // id={`${content[0]}-${contentsIndex}`}
                    key={detailIndex}
                    align='right'
                  />
                </TableRow>
              ))}
          </TableBody>
        </CustomEnhancedTable>
        <div className={classes.button}>
          <Button
            id='close-button'
            variant='contained'
            color='primary'
            className={classes['button-close']}
            onClick={e => handleClose()}
          >
            Cancel
          </Button>
          <div className={classes['item-right-side']}>

            {RBAC.hasPolicy({or: ['BC-056']}) && (
              <Button
                id='void-button'
                variant='contained'
                color='primary'
                className={classes['button-void']}
                disabled={disabledSave}
                onClick={e => handleVoidModal()}
              >
                Void
              </Button>
            )}

            {RBAC.hasPolicy({or: ['BC-055']}) && (
              <Button
                id='void-button'
                variant='contained'
                color='primary'
                className={classes['button-disapprove']}
                disabled={disabledSave}
                onClick={e => handleDisapproveModal()}
              >
                Disapprove
              </Button>
            )}
            {RBAC.hasPolicy({or: ['BC-055']}) && (
              <Button
                id='appprove-link'
                variant='contained'
                className={classes['button-positive']}
                onClick={e => handleApproveModal()}
              >
                Approve
              </Button>
            )}
          </div>
        </div>
      </FormDrawer>
      {openDialogApprove === true ? (
        <Approve
          page={'debit'}
          openDialog={openDialogApprove}
          setOpenDialog={setOpenDialogApprove}
          submitFunction={() => handleApprove(transaction_number)}
          disapproveFunction={() => handleDisapprove(transaction_number)}
        />
      ) : (
        ''
      )}
      {openDialogVoid === true ? (
        <Void
          page={'debit'}
          openDialog={openDialogVoid}
          setOpenDialog={setOpenDialogVoid}
          submitFunction={() => handleVoid(transaction_number)}
        />
      ) : (
        ''
      )}
      {openDialogDisapprove === true ? (
        <Disapprove
          page={'debit'}
          openDisapproveDialog={openDialogDisapprove}
          setOpenDisapproveDialog={setOpenDialogDisapprove}
          submitFunction={() => handleDisapprove(transaction_number)}
        />
      ) : (
        ''
      )}
      {backdrop === true ? (
        <Backdrop className={classes.backdrop} open>
          <CircularProgress color='primary' />
        </Backdrop>
      ) : (
        ''
      )}
    </div>
  )
}
