import Joi from '@hapi/joi'

// Services
import ExtractValidationResult from 'Services/ExtractValidationResult'

export const Validate = _data => {
  const schema = Joi.object({
    module: Joi.string()
      .trim()
      .label('Module')
      .required()
      .messages({ 'string.empty': `"Module" is required.` }),
    report: Joi.string()
      .trim()
      .label('Report')
      .required()
      .messages({ 'string.empty': `"Report" is required.` }),
    category: Joi.string()
      .trim()
      .label('Category')
      .required()
      .messages({ 'string.empty': `"Category" is required.` }),
    date_start: Joi.date()
      .label('Date Start')
      .required()
      .messages({
        'date.base': `"Date Start" is required.`
      }),
    date_end: Joi.date()
      .label('Date End')
      .required()
      .messages({
        'date.base': `"Date End" is required.`
      }),
  }).options({ abortEarly: false })

  return ExtractValidationResult(schema.validate(_data))
}
