import React from 'react'
import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'
import Typography from '@material-ui/core/Typography'

import { makeStyles } from '@material-ui/core/styles'
import { createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';

// Styles
import classes from './CustomTabs.module.scss'

function TabPanel (props) {
  const { children, value, index, ...other } = props

  return (
    <Typography
      component='div'
      role='tabpanel'
      hidden={value !== index}
      id={`scrollable-auto-tabpanel-${index}`}
      aria-labelledby={`scrollable-auto-tab-${index}`}
      key={`tab-${index}`}
      {...other}
    >
      <div>{children}</div>
    </Typography>
  )
}
const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#3ab77d',
    },
  },
});
const useStyle = makeStyles(theme => ({
  indicator: {
    backgroundColor: '#3ab77d'
  }
}))
const CustomTabs = props => {
  const { children, defaultIndex = 0, containerSyles = {} } = props
  const [value, setValue] = React.useState(defaultIndex)
  const overrideClass = useStyle()

  let items = React.Children.toArray(children)
  items = items.filter((item, index) => {
    if (item.type.displayName === 'CustomTab') {
      return React.cloneElement(item, {
        key: index
      })
    }
  })
  function handleChange (event, newValue) {
    setValue(newValue)
  }

  return (
    <ThemeProvider theme={theme}>
      <div className={classes.root} style={containerSyles}>
        <Tabs
          value={value}
          onChange={handleChange}
          textColor='primary'
          variant='scrollable'
          scrollButtons='auto'
          aria-label='scrollable auto tabs example'
          className={classes['header']}
          classes={{ indicator: overrideClass.indicator }}
        >
          {items.map((item, index) => (
            <Tab key={`${item.props.label}-tab-label`} className={classes['item']} label={item.props.label} />
          ))}
        </Tabs>

        {items.map((item, index) => (
          <TabPanel key={`${item.props.label}-tab-content`} className={classes['panels']} value={value} index={index}>
            {item}
          </TabPanel>
        ))}
      </div>
    </ThemeProvider>
  )
}

const CustomTab = ({ children }) => {
  return children
}
CustomTab.displayName = 'CustomTab'

export { CustomTabs, CustomTab }
