import Grid from '@material-ui/core/Grid'
import React, { useRef } from 'react'
import { Typography, Paper } from '@material-ui/core'

// Components
import TextField from './../../../../Components/FormComponents/TextField/CustomTextField'
import DateTimePicker from './../../../../Components/FormComponents/DateTimePicker/DateTimePicker'
import SelectComponent from './../../../../Components/FormComponents/SelectComponent'
import Checkbox from './../../../../Components/FormComponents/Checkbox/Checkbox'
import SummaryTile from './../../../../Components/FormComponents/SummaryTile/SummaryTile'
import PaymentTable from './../../../../Components/PaymentTable'
import { PaymentDialog } from './../ActionDialog'

// Store
import { useSelector, useDispatch } from 'react-redux'
import { UpdateField } from './../../../../Store/actions'

// Styles
import classes from './CollectionDetailsComponent.module.scss'

// Config
import { banks, checkTypes, paymentMode } from './config/config-payment'

const numeral = require('numeral')
const moment = require('moment')

export default function CollectionDetailsComponent (props) {
  const { disabled = false, data = { cdf_summary: {} }, isAdvance = false } = props
  const dispatch = useDispatch()
  const _data = useSelector(state => state.CollectionForm)
  const [today, setToday] = React.useState('')
  const refContainer = useRef()

  const getDate = () => {
    const _date = new Date()
    _date.setHours(_date.getHours() + 8)
    return _date
      .toISOString()
      .split('.')[0]
      .split('.')[0]
      .split(':')
      .map((i, index) => { if (index < 2) { return i } })
      .filter( Boolean )
      .join(':')
  }

  React.useEffect(() => {
    refContainer.current.parentElement.parentElement.parentElement.parentElement.parentElement.parentElement.scrollTo(0,0)
    setToday(getDate())
  }, [])

  const computeTotal = () => {
    const amountData = _data.payment.reduce((amount, item) => {
      return amount + numeral(item.amount).value()
    }, 0)
    dispatch(UpdateField({ field: 'total', value: amountData }))
    dispatch(UpdateField({ field: 'has_error', value: false }))
    dispatch(UpdateField({
      value: {
        ..._data.total_fees,
        grand_total: amountData
      },
      field: 'total_fees'
    }))
  }

  React.useEffect(() => { computeTotal() }, [_data.total])

  return (
    <Grid container className={classes.root} spacing={2} ref={refContainer}>
      <Grid item xs={12}>
        <Typography variant='h5' className={classes.bold}>Collection Details</Typography>
      </Grid>
      <Grid item={12} sm={12} md={12} lg={12}>
        <Paper elevation={3}>
          <Grid container justify='space-between' spacing={2}>
            <Grid key={'total-taxable'} item xs={12} sm={12} md={4} lg={4}>
              <SummaryTile
                id={'total-taxable'}
                label={'total taxable amount'}
                text={numeral(isAdvance ? _data.total_fees ? _data.total_fees.sub_total : 0 : data.cdf_summary?.sub_total || 0).format('0,0.00')}
                prefix='PHP '
              />
            </Grid>
            <Grid key={'total-tax'} item xs={12} sm={12} md={4} lg={4}>
              <SummaryTile
                id={'total-tax'}
                label={'total tax (vat) amount'}
                text={isAdvance ? numeral(_data.total_fees ? _data.total_fees.sub_total_tax : 0).format('0,0.00') : numeral(data.cdf_summary?.sub_total_tax || 0).format('0,0.00')}
                prefix='PHP '
              />
            </Grid>
            <Grid key={'total-amount'} item xs={12} sm={12} md={4} lg={4}>
              <SummaryTile
                id={'total-amount'}
                label={'total amount'}
                text={isAdvance ? numeral(_data.total_fees ? _data.total_fees.grand_total : 0).format('0,0.00') : numeral(data.cdf_summary?.grand_total || data.cdf_summary?.sub_total || 0).format('0,0.00')}
                prefix='PHP '
              />
            </Grid>
          </Grid>
        </Paper>
      </Grid>
      <Grid item lg={12} style={{ marginTop: '1rem' }}>
        <Grid container direction='row' justify='space-around'>
          <Grid item xs={5}>
            <Grid container direction='column'>
              <Grid item xs={12}>
                <Typography variant='subtitle2'>COLLECTION DATE AND TIME *</Typography>
              </Grid>
              <Grid item xs={12}>&nbsp;</Grid>
              <Grid item xs={12}>
                <Grid container direction='row' justify='space-around'>
                  <Grid item xs={6}>
                    <DateTimePicker
                      label='From'
                      defaultClass='form'
                      value={_data.collection_date_start}
                      error={_data?.error?.collection_date_start || null}
                      onChange={e => {
                        dispatch(UpdateField({ field: 'collection_date_start', value: e.toISOString() }))
                      }}
                      placeholder='Collection Start'
                      min={today}
                      disabled={!!disabled}
                    />
                  </Grid>
                  <Grid item xs={6}>
                    <DateTimePicker
                      label='To'
                      defaultClass='form'
                      value={_data.collection_date_end}
                      error={_data?.error?.collection_date_end || null}
                      onChange={e => {
                        dispatch(UpdateField({ field: 'collection_date_end', value: e.toISOString() }))
                      }}
                      placeholder='Collection End'
                      min={today}
                      disabled={!!disabled}
                    />
                  </Grid>
                </Grid>
              </Grid>
              <Grid item xs={12}>
                <Typography variant='subtitle2'>COLLECTION LOCATION AND SITE *</Typography>
              </Grid>
              <Grid item xs={12}>&nbsp;</Grid>
              <Grid item xs={12}>
                <TextField
                  id='location'
                  label='Location'
                  value={_data.collection_location || ''}
                  error={_data?.error?.collection_location || null}
                  direction='row'
                  noMargin
                  defaultClass='lead'
                  disabled={!!props.disabled}
                  onChange={e => dispatch(UpdateField({ field: 'collection_location', value: e.target.value }))}
                />
              </Grid>
              <Grid item xs={12}>&nbsp;</Grid>
              <Grid item xs={12}>
                <SelectComponent
                  id='site'
                  label='Site'
                  value={_data.collection_site || ''}
                  error={_data?.error?.collection_site || null}
                  direction='row'
                  defaultClass='lead'
                  disabled={!!props.disabled}
                  select={[{ name: 'Head Office', value: 'Head Office' }]}
                  onChange={e => dispatch(UpdateField({ field: 'collection_site', value: e.value }))}
                />
              </Grid>
            </Grid>
          </Grid>
          <Grid item xs={5}>
            <Grid container direction='row'>
              <Grid item xs={12}>&nbsp;</Grid>
              <Grid item xs={7}>
                <Grid container direction='column'>
                  <Grid item xs={12}>
                    <Checkbox
                      id='bir-form-2307'
                      checked={_data.with_bir_form_2307 || false}
                      disabled={!!disabled}
                      handleOnChange={e => {
                        dispatch(UpdateField({ field: 'with_bir_form_2307', value: e.target.checked }))
                      }}
                      label={'with BIR form 2307'}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <Checkbox
                      id='net-of-commission'
                      checked={_data.net_of_commission || false}
                      disabled={!!disabled}
                      handleOnChange={e => {
                        dispatch(UpdateField({ field: 'net_of_commission', value: e.target.checked }))
                      }}
                      label={'Net of Commission'}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <Checkbox
                      id='net-of-creditable-with-tax'
                      checked={_data.net_of_creditable_with_tax || false}
                      disabled={!!disabled}
                      handleOnChange={e => {
                        dispatch(UpdateField({ field: 'net_of_creditable_with_tax', value: e.target.checked }))
                      }}
                      label={'Net of Creditable w/ Tax'}
                    />
                  </Grid>
                </Grid>
              </Grid>
              <Grid item xs={5}>
                <TextField
                  constainerStyle={{ width: 'unset', flex: 1 }}
                  disabled={!!disabled}
                  multiline
                  rows={3}
                  id='remarks'
                  label='Remarks'
                  value={_data.remarks || ''}
                  onChange={event => {
                    dispatch(UpdateField({ field: 'remarks', value: event.target.value }))
                  }}
                />
              </Grid>
              <Grid item xs={12}>&nbsp;</Grid>
              <Grid item xs={12}>
                <TextField
                  disabled
                  id='or-no'
                  label='OR No.'
                  value={_data.or_no && typeof _data.or_no === 'object' ? _data.or_no.join(', ') : ''}
                  noMargin
                  direction='row'
                />
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={12}>
        <PaymentTable
          data={_data.payment || []}
          disabled={!!disabled}
          paymentMode={paymentMode || []}
          checkType={checkTypes || []}
          onAddClicked={e => {
            dispatch(UpdateField({ field: 'payment_data', value: { data: {}, index: null }}))
          }}
          onEditLineItem={e => {
            dispatch(UpdateField({ field: 'payment_data', value: { ... e }}))
          }}
          onRemoveLineItem={e => {
            const lineItems = _data.payment || []
            lineItems.splice(e, 1)
            dispatch(UpdateField({ field: 'payment', value: lineItems }))
            computeTotal()
          }}
          onAmountChange={e => {
            computeTotal()
          }}
        />
      </Grid>
      <Grid item xs={12} className={classes['total-container']}>
        <Typography variant='h6' className={`${classes.total} ${_data.has_error ? classes.error : ''}`}>TOTAL: PHP {numeral(_data.total).format('0,0.00')}</Typography>
      </Grid>
      { _data.has_error && (
        <Grid item xs={12} className={classes['total-container']}>
          <Typography variant='h6' className={classes['total-error']}>Total Amount should match the Total Amount for Collection</Typography>
        </Grid>
      )}
      <Grid item xs={12}></Grid>
      <PaymentDialog
        open={_data.payment_data || false}
        handleOnClose={e => dispatch(UpdateField({ field: 'payment_data', value: false }))}
        paymentMode={paymentMode || []}
        banks={banks}
        checkTypes={checkTypes || []}
        data={_data.payment_data || {}}
        onAddLineItem={e => {
          const { amount } = e.data
          if (amount <= 0) return false
          const lineItems = _data.payment || []
          lineItems.push(e.data)
          dispatch(UpdateField({ field: 'payment', value: lineItems }))
          computeTotal()
        }}
        onSaveLineItem={e => {
          const { amount } = e
          if (amount <= 0) return false
          const lineItems = _data.payment || []
          lineItems[e.index] = e.data
          dispatch(UpdateField({ field: 'payment', value: lineItems }))
          computeTotal()
        }}
      />
    </Grid>
  )
}
