import Joi from '@hapi/joi'

// Services
import ExtractValidationResult from 'Services/ExtractValidationResult'

export const Validate = ({ data, type }) => {
  const schema = {
    BANK_DEPOSIT: Joi.object({
      index: Joi.any().allow('', null),
      means_of_payment: Joi.string()
        .trim()
        .label('Payment Mode')
        .required()
        .messages({ 'string.empty': `"Payment Mode" is required.` }),
      amount: Joi.number()
        .label('Amount')
        .min(1)
        .messages({
          'number.base': `"Amount" is required.`,
          'number.min': `"Amount" should be atleast PHP 1.00`
        })
        .required(),
      preferred_bank: Joi.string()
        .trim()
        .label('Bank')
        .messages({ 
          'string.base': `"Bank" is required`,
          'string.empty': `"Bank" is required.`
        })
        .required(),
      bank_account_number: Joi.string()
        .trim()
        .label('Bank')
        .messages({ 
          'string.base': `"Bank" is required`,
          'string.empty': `"Bank" is required.`
        })
        .required(),
      bank_branch: Joi.string()
        .trim()
        .label('Bank')
        .messages({ 
          'string.base': `"Bank" is required`,
          'string.empty': `"Bank" is required.`
        })
        .required()
    }).options({ abortEarly: false }),
    PICK_UP_CHECK: Joi.object({
      index: Joi.any().allow('', null),
      means_of_payment: Joi.string()
        .trim()
        .label('Payment Mode')
        .required()
        .messages({ 'string.empty': `"Payment Mode" is required.` }),
      amount: Joi.number()
        .label('Amount')
        .min(1)
        .messages({
          'number.base': `"Amount" is required.`,
          'number.min': `"Amount" should be atleast PHP 1.00`
        })
        .required(),
      bank_branch: Joi.string()
        .trim()
        .label('Bank Branch')
        .messages({ 
          'string.base': `"Bank Branch" is required`,
          'string.empty': `"Bank Branch" is required.`
        })
        .required(),
      check_number: Joi.string()
        .trim()
        .label('Check Number')
        .required()
        .messages({ 'string.empty': `"Check Number" is required.` }),
      check_date: Joi.date()
        .label('Check Date')
        .required()
        .messages({
          'date.base': `"Check Date" is required.`
        }),
      check_type: Joi.string()
        .trim()
        .label('Check Type')
        .required()
        .messages({ 'string.empty': `"Check Date" is required.` })
    }).options({ abortEarly: false }),
    DELIVER_CHECK: Joi.object({
      index: Joi.any().allow('', null),
      means_of_payment: Joi.string()
        .trim()
        .label('Payment Mode')
        .required()
        .messages({ 'string.empty': `"Payment Mode" is required.` }),
      amount: Joi.number()
        .label('Amount')
        .min(1)
        .messages({
          'number.base': `"Amount" is required.`,
          'number.min': `"Amount" should be atleast PHP 1.00`
        })
        .required(),
      bank_branch: Joi.string()
        .trim()
        .label('Bank Branch')
        .messages({ 
          'string.base': `"Bank Branch" is required`,
          'string.empty': `"Bank Branch" is required.`
        })
        .required(),
      check_number: Joi.string()
        .trim()
        .label('Check Number')
        .required()
        .messages({ 'string.empty': `"Check Number" is required.` }),
      check_date: Joi.date()
        .label('Check Date')
        .required()
        .messages({
          'date.base': `"Check Date" is required.`
        }),
      check_type: Joi.string()
        .trim()
        .label('Check Type')
        .required()
        .messages({ 'string.empty': `"Check Date" is required.` })
    }).options({ abortEarly: false }),
    PICK_UP_CASH: Joi.object({
      index: Joi.any().allow('', null),
      means_of_payment: Joi.string()
        .trim()
        .label('Payment Mode')
        .required()
        .messages({ 'string.empty': `"Payment Mode" is required.` }),
      amount: Joi.number()
        .label('Amount')
        .min(1)
        .messages({
          'number.base': `"Amount" is required.`,
          'number.min': `"Amount" should be atleast PHP 1.00`
        })
        .required()
    }).options({ abortEarly: false }),
    DELIVER_CASH: Joi.object({
      index: Joi.any().allow('', null),
      means_of_payment: Joi.string()
        .trim()
        .label('Payment Mode')
        .required()
        .messages({ 'string.empty': `"Payment Mode" is required.` }),
      amount: Joi.number()
        .label('Amount')
        .min(1)
        .messages({
          'number.base': `"Amount" is required.`,
          'number.min': `"Amount" should be atleast PHP 1.00`
        })
        .required()
    }).options({ abortEarly: false }),
    CREDIT_CARD: Joi.object({
      index: Joi.any().allow('', null),
      means_of_payment: Joi.string()
        .trim()
        .label('Payment Mode')
        .required()
        .messages({ 'string.empty': `"Payment Mode" is required.` }),
      amount: Joi.number()
        .label('Amount')
        .min(1)
        .messages({
          'number.base': `"Amount" is required.`,
          'number.min': `"Amount" should be atleast PHP 1.00`
        })
        .required()
    }).options({ abortEarly: false }),
    WIRETRANSFER: Joi.object({
      index: Joi.any().allow('', null),
      means_of_payment: Joi.string()
        .trim()
        .label('Payment Mode')
        .required()
        .messages({ 'string.empty': `"Payment Mode" is required.` }),
      amount: Joi.number()
        .label('Amount')
        .min(1)
        .messages({
          'number.base': `"Amount" is required.`,
          'number.min': `"Amount" should be atleast PHP 1.00`
        })
        .required(),
      preferred_bank: Joi.string()
        .trim()
        .label('Bank')
        .messages({ 
          'string.base': `"Bank" is required`,
          'string.empty': `"Bank" is required.`
        })
        .required(),
      bank_account_number: Joi.string()
        .trim()
        .label('Bank')
        .messages({ 
          'string.base': `"Bank" is required`,
          'string.empty': `"Bank" is required.`
        })
        .required(),
      bank_branch: Joi.string()
        .trim()
        .label('Bank')
        .messages({ 
          'string.base': `"Bank" is required`,
          'string.empty': `"Bank" is required.`
        })
        .required()
    }).options({ abortEarly: false }),
    default: Joi.object({ default: Joi.boolean().required().label('Payment Mode') })
      .options({ abortEarly: false })
  }

  const toValidate = schema[type] || schema.default

  return ExtractValidationResult(toValidate.validate(data))
}
