import React from 'react'
import clsx from 'clsx'
import Button from '@material-ui/core/Button'
import InputBase from '@material-ui/core/InputBase'
import IconButton from '@material-ui/core/IconButton'
import FormatListBulletedIcon from '@material-ui/icons/FormatListBulleted'
import Grid from '@material-ui/core/Grid'
import Paper from '@material-ui/core/Paper'
import SearchIcon from '@material-ui/icons/Search'
import Typography from '@material-ui/core/Typography'

import ReplyIcon from '@material-ui/icons/Reply'
import CloseIcon from '@material-ui/icons/Close'
import Popover from '@material-ui/core/Popover'

import noop from 'lodash/noop'
import moment from 'moment'

import DatePicker from '../DatePicker/DatePicker'
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles'

import RBAC from 'Security/RBAC'

// Style
import classes from './SearchTextComponent.module.scss'

const theme = createMuiTheme({
  overrides: {
    // Style sheet name 
    MuiPopover: {
      // Name of the rule
      paper: {
        // Some CSS
        width: '30%'
      }
    }
  }
})

export default function SearchTextComponent (props) {
  const defaultFunction = function () {}

  const {
    displaySettings = false,
    displaySettingsTitle = 'Effectivity Date Range*',
    handleOnDisplaySettingsClicked = noop,
    handleOnDisplaySettingsApplyClicked = noop,
    onChange = noop,
    placeholder = '',
    startDaterErrorMessage = 'Please enter a valid effectivity start date',
    endDaterErrorMessage = 'Please enter a valid effectivity end date',
    validEndDaterErrorMessage = 'Please enter a valid effectivity end date that is not later than the current date',
    displaySettingsButton = true,
    id,
    exportFiles,
    errorMessage = {}
  } = props

  const [values, setValues] = React.useState({
    start: null,
    end: null
  })
  const [debounce, setDebounce] = React.useState(null)
  const [focus, setFocus] = React.useState()
  const [anchorEl, setAnchorEl] = React.useState()
  const [toDateError, setToDateError] = React.useState('')
  const [fromDateError, setFromDateError] = React.useState('')

  const handleRangeChange = (key, value) => {
    setValues(prevState => ({
      ...prevState,
      [key]: value
    }))
  }

  const handleOnChange = e => {
    const _data = e.target.value
    clearTimeout(debounce)
    setDebounce(
      setTimeout(() => {
        onChange(_data)
      }, 500)
    )
  }

  const onClickPopover = event => {
    setAnchorEl(event.currentTarget)
  }

  const onClickApply = () => {
    if (values.start && values.end) {
      setFromDateError('')
      setToDateError('')
      handleOnDisplaySettingsApplyClicked(values)
      setAnchorEl(null)
    }
    if (!values.start && !values.end) {
      setFromDateError(errorMessage.date_start || startDaterErrorMessage || 'Please enter a valid effectivity start date')
      setToDateError(errorMessage.date_end || endDaterErrorMessage || 'Please enter a valid effectivity end date')
      return false
    }
    if (!values.start && values.end) {
      setFromDateError(errorMessage.date_start || startDaterErrorMessage || 'Please enter a valid effectivity start date')
      setToDateError('')
      return false
    }
    if (values.start && !values.end) {
      setToDateError(errorMessage.date_end || endDaterErrorMessage || 'Please enter a valid effectivity end date')
      setFromDateError('')
      return false
    }
    if (values.start > values.end) {
      setToDateError(validEndDaterErrorMessage || 'Please enter a valid effectivity end date that is not later than the current date')
      return false
    }
  }

  return (
    <React.Fragment>
      <Grid container className={classes.root} direction='row'>
        <Grid item xs>
          <Paper
            className={`${classes.search} ${focus && classes.focus} box-shadow-field`}
            onBlur={() => setFocus(false)}
          >
            <IconButton className={classes.icon} aria-label='search'>
              <SearchIcon />
            </IconButton>
            <InputBase
              id={id}
              className={classes.input}
              placeholder={placeholder}
              onChange={handleOnChange}
              autoComplete='off'
              onFocus={() => setFocus(true)}
            />
          </Paper>
        </Grid>
        {displaySettings && (
          <Grid item>
            <Grid container direction='column' justify='center' className={classes.actions}>
              <Grid item>
                <Grid container direction='row' className={classes['actions-wrapper']}>
                    <Grid item className={classes['actions-item']}>
                      {RBAC.hasPolicy({ or: ['BC-073', 'BC-087', 'BC-096', 'BC-110', 'BC-113', 'BC-116'] }) && (
                        <React.Fragment>
                          {exportFiles && (
                            <Button id='export' onClick={handleOnDisplaySettingsClicked}>
                              <ReplyIcon className={clsx(classes['actions-icon'], classes['reply'])} fontSize='small' />
                              <Typography variant='caption' className={`${classes['actions-label']} bold text-link`}>
                                Export
                              </Typography>
                            </Button>
                          )}
                        </React.Fragment>
                      )}
                      {RBAC.hasPolicy({ or: ['BC-002', 'BC-001', 'BC-005', 'BC-006', 'BC-021', 'BC-017', 'BC-014', 'BC-029', 'BC-030', 'BC-035', 'BC-036', 'BC-043', 'BC-044', 'BC-051', 'BC-052', 'BC-059', 'BC-062', 'BC-065', 'BC-068', 'BC-069', 'BC-070', 'BC-075', 'BC-076', 'BC-081', 'BC-082', 'BC-089', 'BC-090', 'BC-101', 'BC-104', 'BC-105', 'BC-112', 'BC-115'] }) && (
                        <React.Fragment>
                          {displaySettingsButton && (
                              <Button id='generate-report' onClick={onClickPopover}>
                                <FormatListBulletedIcon className={classes['actions-icon']} fontSize='small' />
                                <Typography variant='caption' className={`${classes['actions-label']} bold text-link`}>
                                  Display Settings
                                </Typography>
                              </Button>
                            )
                          }
                        </React.Fragment>
                      )}
                      <ThemeProvider theme={theme}>
                        <Popover
                          open={anchorEl}
                          anchorEl={anchorEl}
                          onClose={() => {
                            setToDateError('')
                            setFromDateError('')
                            setAnchorEl(null)
                          }}
                          anchorOrigin={{
                            vertical: 'bottom',
                            horizontal: 'right'
                          }}
                          transformOrigin={{
                            vertical: 'top',
                            horizontal: 'right'
                          }}
                          // Removed: Causing Console Warning Errors & doesn't affect the PopOver Style
                          // classes={{ paper: { width: '30%' } }}
                        >
                          <Grid container style={{ padding: '1rem' }}>
                            <Grid item xs style={{ margin: '1rem 0rem' }}>
                              <Typography variant='subtitle2'>{displaySettingsTitle}</Typography>
                            </Grid>
                            <Grid container spacing={2}>
                              <Grid item lg={6} style={{ width: '50%' }}>
                                <DatePicker
                                  placeholder='From'
                                  value={values.start}
                                  onClick={() => {
                                    setToDateError('')
                                    setFromDateError('')
                                  }}
                                  onChange={e =>
                                    handleRangeChange(
                                      'start',
                                      moment(e)
                                        .startOf('day')
                                        .toISOString()
                                    )
                                  }
                                  maxDateMessage={null}
                                />
                              </Grid>
                              <Grid item lg={6} style={{ width: '50%' }}>
                                <DatePicker
                                  placeholder='To'
                                  value={values.end}
                                  onClick={() => {
                                    setToDateError('')
                                    setFromDateError('')
                                  }}
                                  onChange={e =>
                                    handleRangeChange(
                                      'end',
                                      moment(e)
                                        .endOf('day')
                                        .toISOString()
                                    )
                                  }
                                  minDateMessage={null}
                                />
                              </Grid>
                            </Grid>
                            <Grid container spacing={3} style={{ 'padding-bottom': '50px' }}>
                              <Grid item xs={6} style={{ color: 'red', 'font-size': '10px' }}>
                                {fromDateError}
                              </Grid>
                              <Grid item xs={6} style={{ color: 'red', 'font-size': '10px' }}>
                                {toDateError}
                              </Grid>
                            </Grid>
                            <Grid container direction='row' justify='flex-end' style={{ marginTop: '2rem' }}>
                              <Button
                                id='display-settings-cancel'
                                onClick={() => {
                                  setToDateError('')
                                  setFromDateError('')
                                  setValues(i => ({ ...i, start: null, end: null }))
                                  setAnchorEl(null)
                                }}
                                variant='contained'
                                disableElevation
                                style={
                                  {
                                    marginRight: '10px',
                                    backgroundColor: 'transparent',
                                    border: 'solid 1px #3ab77d',
                                    color: '#3ab77d',
                                    textTransform: 'capitalize',
                                    padding: '5px 30px'
                                  }
                                }
                              >
                                Cancel
                              </Button>
                              <Button
                                id='display-settings-apply'
                                onClick={onClickApply}
                                variant='contained'
                                className={classes['button-apply']}
                                disableElevation
                                style={
                                  {
                                    backgroundColor: '#3ab77d',
                                    border: 'solid 1px #3ab77d',
                                    color: '#fff',
                                    textTransform: 'capitalize',
                                    padding: '5px 30px'
                                  }
                                }
                              >
                                Apply
                              </Button>
                            </Grid>
                          </Grid>
                        </Popover>
                      </ThemeProvider>
                    </Grid>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        )}
      </Grid>
    </React.Fragment>
  )
}
