import React, { useState } from 'react'

// MATERIAL COMPONENTS
import { Grid, Typography, Select, MenuItem, FormControl, InputLabel } from '@material-ui/core'

// MATERIAL ICONS
import { ArrowDownwardRounded } from '@material-ui/icons';

// SHARED COMPONENTS
import SearchTextComponent from '../../../Components/FormComponents/SearchTextComponent/SearchTextComponent'

/*
 * PAGE COMPONENTS 
 * 
 * Components customized for current page
*/
import DropDown from './DropDown'
import DownloadPopOver from './DownloadPopOver'

// STYLES
import style from '../AuditLog.module.scss'

export default function ListViewOptions(props) {
  const { onListViewTopFilterChange, params, reportType, config, list } = props

  const tableHeaders = config[reportType].header
  
  const accessors = tableHeaders.map(header=> header.accessor)

  const [popOverStatus, setPopOverStatus] = useState({
    open: false, anchorEl: null
  })

  const handleDownloadButtonClick = (event) => {
    setPopOverStatus({ open: true, anchorEl: event.currentTarget })
  }

  const handleDownloadButtonClose = (event) => {
    setPopOverStatus({ open: false, anchorEl: null })
  }

  const onDropDownChange = (value) => {
    onListViewTopFilterChange({ filters: { ...params.filters, module: value } })
  }

  const onMainSearchChange = (value) => {
    onListViewTopFilterChange({ 
      search: value,
      accessors,
      page: 1,
      filters: { ...params.filters }
    })
  }

  return (

    <div className={style.listViewOptions}>
      <div className={style.dropdown}>
        <DropDown onChange={onDropDownChange} />
      </div>
      <div className={style.searchTextWrapper}>
        <SearchTextComponent
          placeholder="Search Audit Logs"
          displaySettings={false}
          onChange={onMainSearchChange}
        />
      </div>
      <div className={style.downloadOptionButtonWrapper}>
        <div className={style.downloadOptionButton} onClick={handleDownloadButtonClick}>
          <ArrowDownwardRounded className={style.downloadOptionIcon} />
          <span className={style.downloadOptionText}>Download Logs</span>
        </div>
        <DownloadPopOver reportType={reportType} list={list} open={popOverStatus.open} anchorEl={popOverStatus.anchorEl} handleClose={handleDownloadButtonClose} />
      </div>
    </div>
  )
}
