import React from 'react'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'

// Components
import TextField from 'StandardComponents/TextField/CustomTextField'

// Styles
import classes from './CustomizedDialogs.module.scss'

const SuccessDialog = props => {
  const { page, openSuccessDialog, setOpenSuccessDialog, submitFunction } = props

  const handleOnCloseSuccessDialog = () => {
    setOpenSuccessDialog(false)
  }

  const handleOnSuccessDialog = () => {
    submitFunction(false)
  }

  return (
    <Dialog
      fullWidth
      maxWidth='xs'
      onClose={handleOnCloseSuccessDialog}
      aria-labelledby='customized-dialog-title'
      open={openSuccessDialog}
      className={classes.root}
      PaperProps={{ style: { height: '35%', display: 'flex' } }}
    >
      <DialogTitle disableTypography className={classes['header-container']}>
        <div className={classes.header}>
          <IconButton
            aria-label='close'
            id='success-close-button'
            className={classes.button}
            onClick={handleOnCloseSuccessDialog}
          >
            <CloseIcon />
          </IconButton>
          <Typography variant='h6' id={`${page}-title`} className={classes.title}>
            {page === 'update'
              ? 'Tax Charging Update Saved Successfully'
              : ''}
          </Typography>
        </div>
      </DialogTitle>
      <DialogContent>
        {page === 'update'
          ? 'The change/updates made to the selected Tax Charging has been successfully updated.'
          : ''}
        <div className={classes.button}>
          <Button
            variant='contained'
            id={`${page}-ok-button`}
            className={classes['button-positive']}
            onClick={handleOnSuccessDialog}
          >
            OK
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  )
}

export default function CustomizedDialogs (props) {
  const { page, openDialog, setOpenDialog, submitFunction } = props
  const [openSuccessDialog, setOpenSuccessDialog] = React.useState(false)

  const handleOnClose = () => {
    setOpenDialog(false)
  }

  const onShowSuccessDialog = e => {
    if (page === 'update') {
      if (openDialog === true) {
        setOpenSuccessDialog(true)
        if (openSuccessDialog === true) setOpenDialog(false)
      }
    } else {
      if (e) setOpenDialog(false)
    }
  }

  return (
    <React.Fragment>
      <Dialog
        fullWidth
        maxWidth='xs'
        onClose={handleOnClose}
        aria-labelledby='customized-dialog-title'
        open={openDialog}
        className={classes.root}
        PaperProps={{ style: { height: '350px', display: 'flex' } }}
      >
        <React.Fragment>
          <DialogTitle disableTypography className={classes['header-container']}>
            <div className={classes.header}>
              <IconButton
                aria-label='close'
                id='confirm-close-button'
                className={classes.button}
                onClick={handleOnClose}
              >
                <CloseIcon />
              </IconButton>
              <Typography variant='h6' id='confirm-title' className={classes.title}>
                {page === 'update' ? 'Save Tax Charging Updates' : 'Deactivate Tax Charging'}
              </Typography>
            </div>
          </DialogTitle>
          <DialogContent id='soa-content'>
            {page === 'update' ? 'Are you sure you want to save the updates made to the Tax Charging entry?' : 'Are you sure you want to deactivate the selected Tax Charging?'}
            <br />
            <br />
            {page === 'update' ? 'Changes mande to the Tax Charging will only be applied to the future transactions after the update.' : 'Please ensure that you have inform the Administrator that you will be deactivating this Tax Charging. Once deactivated, it will no longer be available for selection in the future transactions.'}
            {page === 'update' ? (<br />, 'Do you still want to continue?') : 'Once'}
            <br />
            <br />
            <div className={classes.button}>
              <Button
                variant='outlined'
                id='soa-cancel-button'
                className={classes['button-negative']}
                onClick={handleOnClose}
              >
                No
              </Button>
              <Button
                variant='contained'
                id='soa-submit-button'
                className={classes['button-positive']}
                onClick={e => onShowSuccessDialog(e)}
              >
                Yes
              </Button>
            </div>
          </DialogContent>
        </React.Fragment>
      </Dialog>
      <SuccessDialog
        page={page}
        openSuccessDialog={openSuccessDialog}
        setOpenSuccessDialog={setOpenSuccessDialog}
        submitFunction={setOpenDialog}
      />
    </React.Fragment>
  )
}
