import React from 'react'
import { Link } from 'react-router-dom'
import clsx from 'clsx'
import Button from '@material-ui/core/Button'
import InputBase from '@material-ui/core/InputBase'
import IconButton from '@material-ui/core/IconButton'
import FormatListBulletedIcon from '@material-ui/icons/FormatListBulleted'
import Grid from '@material-ui/core/Grid'
import Paper from '@material-ui/core/Paper'
import SearchIcon from '@material-ui/icons/Search'
import Typography from '@material-ui/core/Typography'
import ViewListRoundedIcon from '@material-ui/icons/ViewListRounded'

import ReplyIcon from '@material-ui/icons/Reply'
import CloseIcon from '@material-ui/icons/Close'
import Popover from '@material-ui/core/Popover'

import noop from 'lodash/noop'
import moment from 'moment'

import DatePicker from '../DatePicker/DatePicker'
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles'

import RBAC from 'Security/RBAC'

// Style
import classes from './DisplaySettings.module.scss'

const theme = createMuiTheme({
  overrides: {
    // Style sheet name 
    MuiPopover: {
      // Name of the rule
      paper: {
        // Some CSS
        width: '30%'
      }
    }
  }
})

export default function DisplaySettings (props) {

  const {
    displaySettings = false,
    displaySettingTitle = 'Effectivity Date Range*',
    handleOnDisplaySettingsClicked = noop,
    handleOnDisplaySettingsApplyClicked = noop,
    onChange = noop,
    placeholder,
    startDateError,
    endDateError,
    laterStartDateError,
    laterEndDateError,
    displaySettingsButton = true,
    id,
    errorMessage = {}
  } = props

  const [values, setValues] = React.useState({
    start: null,
    end: null
  })
  const [debounce, setDebounce] = React.useState(null)
  const [focus, setFocus] = React.useState()
  const [anchorEl, setAnchorEl] = React.useState()
  const [toDateError, setToDateError] = React.useState('')
  const [fromDateError, setFromDateError] = React.useState('')

  const handleRangeChange = (key, value) => {
    setValues(prevState => ({
      ...prevState,
      [key]: value
    }))
  }

  const handleOnChange = e => {
    const _data = e.target.value
    clearTimeout(debounce)
    setDebounce(
      setTimeout(() => {
        onChange(_data)
      }, 300)
    )
  }

  const onClickPopover = event => {
    setAnchorEl(event.currentTarget)
  }

  const onClickApply = () => {
    let fdate = new Date(values.start)
    let tdate = new Date(values.end)
    let endOfToday = moment().endOf('day').toISOString()
    let today = new Date(endOfToday)
    console.log('tdate', tdate.valueOf())
    console.log('Today', today.valueOf())
    setFromDateError('')
    setToDateError('')
    if (!fdate.valueOf() && !tdate.valueOf()) {
      setFromDateError(`${startDateError}`)
      setToDateError(`${endDateError}`)
    } else if (!fdate.valueOf()) {
      setFromDateError(`${startDateError}`)
    } else if (!tdate.valueOf()) {
      setToDateError(`${endDateError}`)
    } else if (fdate.valueOf() > today.valueOf() && tdate.valueOf() > today.valueOf()) {
      setFromDateError(`${laterStartDateError}`)
      setToDateError(`${laterEndDateError}`)
    } else if (fdate.valueOf() > today.valueOf()) {
      setFromDateError(`${laterStartDateError}`)
    } else if (tdate.valueOf() > today.valueOf()) {
      setToDateError(`${laterEndDateError}`)
    } else {
      handleOnDisplaySettingsApplyClicked(values)
    }
  }

  const handleOnCancel = (event) => {
    setFromDateError('')
    setToDateError('')
    setValues(i => ({ ...i, start: null, end: null }))
  }

  const handleClick = event => {
    if (event.target.value === 'x') {
      setAnchorEl(null)
    } else {
      setAnchorEl(anchorEl ? null : event.currentTarget)
    }
  }

  return (
    <React.Fragment>
      <Grid container className={classes.root} direction='row'>
        <Grid item xs>
          <Paper
            className={`${classes.search} ${focus && classes.focus} box-shadow-field`}
            onBlur={() => setFocus(false)}
          >
            <IconButton className={classes.icon} aria-label='search'>
              <SearchIcon />
            </IconButton>
            <InputBase
              id={id}
              className={classes.input}
              placeholder={placeholder}
              onChange={handleOnChange}
              autoComplete='off'
              onFocus={() => setFocus(true)}
            />
          </Paper>
        </Grid>
        {displaySettings && (
          <Grid item>
            <Grid container direction='column' justify='center' className={classes.actions}>
              <Grid item>
                <Grid container direction='row' className={classes['actions-wrapper']}>
                    <Grid item className={classes['actions-item']}>
                        <React.Fragment>
                          {displaySettingsButton && (
                              <Button id='generate-report' onClick={onClickPopover}>
                                <FormatListBulletedIcon className={classes['actions-icon']} fontSize='small' />
                                <Typography variant='caption' className={`${classes['actions-label']} bold text-link`}>
                                  Display Settings
                                </Typography>
                              </Button>
                            )
                          }
                        </React.Fragment>
                        {/* <React.Fragment>
                            <Button id='export' onClick={handleOnDisplaySettingsClicked}>
                              <ReplyIcon className={clsx(classes['actions-icon'], classes['reply'])} fontSize='small' />
                              <Typography variant='caption' className={`${classes['actions-label']} bold text-link`}>
                                Export
                              </Typography>
                            </Button>
                        </React.Fragment> */}
                      <ThemeProvider theme={theme}>
                        <Popover
                          open={anchorEl}
                          anchorEl={anchorEl}
                          onClose={() => {
                            setToDateError('')
                            setFromDateError('')
                            setAnchorEl(null)
                          }}
                          anchorOrigin={{
                            vertical: 'bottom',
                            horizontal: 'right'
                          }}
                          transformOrigin={{
                            vertical: 'top',
                            horizontal: 'right'
                          }}
                        >
                          <Grid container style={{ padding: '1rem' }}>
                            <Grid item xs xs={10} style={{ margin: '1rem 0rem' }}>
                              <Typography variant='subtitle2'>{displaySettingTitle}</Typography>
                            </Grid>
                              <Grid
                                item
                                xs={2}
                                style={{ 'font-size': '22px', 'padding-left': '20px', 'padding-right': '3px', cursor: 'pointer' }}
                                onClick={e => handleClick(e)}
                              >
                                X
                              </Grid>
                            <Grid container spacing={2}>
                              <Grid item lg={6} style={{ width: '50%' }}>
                                <DatePicker
                                  placeholder='From'
                                  value={values.start}
                                  onClick={() => {
                                    setToDateError('')
                                    setFromDateError('')
                                  }}
                                  onChange={e =>
                                    handleRangeChange(
                                      'start',
                                      moment(e)
                                        .startOf('day')
                                        .toISOString()
                                    )
                                  }
                                  maxDateMessage={null}
                                />
                              </Grid>
                              <Grid item lg={6} style={{ width: '50%' }}>
                                <DatePicker
                                  placeholder='To'
                                  value={values.end}
                                  onClick={() => {
                                    setToDateError('')
                                    setFromDateError('')
                                  }}
                                  onChange={e =>
                                    handleRangeChange(
                                      'end',
                                      moment(e)
                                        .endOf('day')
                                        .toISOString()
                                    )
                                  }
                                  minDateMessage={null}
                                />
                              </Grid>
                            </Grid>
                            <Grid container spacing={3} style={{ 'padding-bottom': '50px' }}>
                              <Grid item xs={6} style={{ color: 'red', 'font-size': '10px' }}>
                                {fromDateError}
                              </Grid>
                              <Grid item xs={6} style={{ color: 'red', 'font-size': '10px' }}>
                                {toDateError}
                              </Grid>
                            </Grid>
                            <Grid container direction='row' justify='flex-end' style={{ marginTop: '2rem' }}>
                              <Button
                                id='display-settings-cancel'
                                onClick={handleOnCancel}
                                variant='contained'
                                disableElevation
                                style={
                                  {
                                    marginRight: '10px',
                                    backgroundColor: 'transparent',
                                    border: 'solid 1px #3ab77d',
                                    color: '#3ab77d',
                                    textTransform: 'capitalize',
                                    padding: '5px 30px'
                                  }
                                }
                              >
                                Cancel
                              </Button>
                              <Button
                                id='display-settings-apply'
                                onClick={onClickApply}
                                variant='contained'
                                className={classes['button-apply']}
                                disableElevation
                                style={
                                  {
                                    backgroundColor: '#3ab77d',
                                    border: 'solid 1px #3ab77d',
                                    color: '#fff',
                                    textTransform: 'capitalize',
                                    padding: '5px 30px'
                                  }
                                }
                              >
                                Apply
                              </Button>
                            </Grid>
                          </Grid>
                        </Popover>
                      </ThemeProvider>
                    </Grid>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        )}
      </Grid>
    </React.Fragment>
  )
}
