import React from 'react'
import { Button, Grid, IconButton, Typography } from '@material-ui/core'
import CreateIcon from '@material-ui/icons/Create'
import RemoveCircleIcon from '@material-ui/icons/RemoveCircle'

import classes from './style.module.scss'

const moment = require('moment')
const numeral = require('numeral')

export default function PaymentTable ({
  onAddClicked = () => {},
  onRemoveLineItem = () => {},
  onEditLineItem = () => {},
  onAmountChange = () => {},
  data = [],
  paymentMode = [],
  checkType = [],
  disabled = false,
  ...other
}) {

  const [nameLists, setNameLists] = React.useState({})

  React.useEffect(() => {
    const _nameLists = {}

    computeTotal()
    paymentMode.map(i => {
      _nameLists[i.value] = i.name
    })
    checkType.map(i => {
      _nameLists[i.value] = i.name
    })
    setNameLists(i => ({ ..._nameLists }))
  }, [])

  const computeTotal = () => {
    const amountData = data.reduce((amount, item) => {
      return amount + numeral(item.amount).value()
    }, 0)
    onAmountChange(amountData)
  }

  return (
    <Grid container direction='column' className={classes.root}>
      <Grid item xs={12} className={classes.header}>
        <Grid container column='row' justify='space-between'>
          <Grid item>
            <Typography variant='h5' className={classes.title}>Payment Details</Typography>
          </Grid>
          <Grid item>
            { !disabled && (
              <Button
                variant='contained'
                size='medium'
                color='success'
                className={classes['button-fill-success']}
                onClick={() => onAddClicked(true)}
              >
                Add Payment
              </Button>
            )}
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={12}>
        <Grid container direction='column'>
          {!data || data.length === 0 && (
            <Grid item xs={12} className={classes.empty}>
              <Typography>No Payment Details yet</Typography>
            </Grid>
          )}
          {data && data.map((i, index) => (
            <Grid item xs={12} className={classes.list}>
              <Grid container direction='row' className={classes['list-container']}>
                { !disabled && (
                  <Grid item className={classes.action}>
                    <React.Fragment>
                      <IconButton className={`${classes['icon-button']} ${classes.positive}`} onClick={() => onEditLineItem({ data: i, index: index })}>
                        <CreateIcon fontSize='small' />
                      </IconButton>
                      <IconButton className={`${classes['icon-button']} ${classes.danger}`} onClick={() => {
                        onRemoveLineItem(index)
                        computeTotal()
                      }}>
                        <RemoveCircleIcon fontSize='small' />
                      </IconButton>
                    </React.Fragment>
                  </Grid>
                )}
                <Grid item xs={!['PICK_UP_CHECK', 'DELIVER_CHECK'].find(e => e === i.means_of_payment)}>
                  <Grid container direction='column'>
                    <Grid item xs={12}>
                      <Typography className={classes['payment-mode']}>{nameLists[i.means_of_payment] || ''}</Typography>
                    </Grid>
                    {i.bank_branch && (
                      <Grid item xs={12}>
                        <Typography className={classes['sub-details']}>{i.bank_branch}</Typography>
                      </Grid>
                    )}
                    {i.bank_account_number && (
                      <Grid item xs={12}>
                        <Typography className={classes['sub-details']}>{i.bank_account_number}</Typography>
                      </Grid>
                    )}
                  </Grid>
                </Grid>
                {['PICK_UP_CHECK', 'DELIVER_CHECK'].find(e => e === i.means_of_payment) && (
                  <Grid item className={classes['check-details']}>
                    <Grid container direction='column'>
                      <Grid item xs={12}>
                        <Grid container direction='row' justify='space-between'>
                          <Grid item xs={5}>
                            <Typography className={classes.label}>CHECK NO.</Typography>
                          </Grid>
                          <Grid item xs={5}>
                            <Typography className={classes.value}>{i.check_number}</Typography>
                          </Grid>
                        </Grid>
                      </Grid>
                      <Grid item xs={12}>
                        <Grid container direction='row' justify='space-between'>
                          <Grid item xs={5}>
                            <Typography className={classes.label}>CHECK DATE</Typography>
                          </Grid>
                          <Grid item xs={5}>
                            <Typography className={classes.value}>{moment(i.check_date).format('ll')}</Typography>
                          </Grid>
                        </Grid>
                      </Grid>
                      <Grid item xs={12}>
                        <Grid container direction='row' justify='space-between'>
                          <Grid item xs={5}>
                            <Typography className={classes.label}>CHECK TYPE</Typography>
                          </Grid>
                          <Grid item xs={5}>
                            <Typography className={classes.value}>{nameLists[i.check_type] || ''}</Typography>
                          </Grid>
                        </Grid>
                      </Grid>
                    </Grid>
                  </Grid>
                )}
                {!!['PICK_UP_CHECK', 'DELIVER_CHECK'].find(e => e === i.means_of_payment) && (
                  <Grid item xs={!!['PICK_UP_CHECK', 'DELIVER_CHECK'].find(e => e === i.means_of_payment)}>&nbsp;</Grid>
                )}
                <Grid item>
                  <Typography variant='h6'>PHP {numeral(i.amount).format('0,0.00') || 0}</Typography>
                </Grid>
              </Grid>
            </Grid>
          ))}
        </Grid>
      </Grid>
    </Grid>
  )
}
