import React from 'react'
import { Grid, Typography, Button } from '@material-ui/core'

// Components
import SelectComponent from './../FormComponents/SelectComponent'
import DateTimePicker from './../FormComponents/DateTimePicker/DateTimePicker'
import PdfViewer from 'StandardComponents/PdfViewer/PdfViewer2'
import LocalPrintshopIcon from '@material-ui/icons/LocalPrintshop'
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward'
import GetAppRoundedIcon from '@material-ui/icons/GetAppRounded'
import Checkbox from './../FormComponents/Checkbox/Checkbox'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import BillingSOAReportService from 'Services/Api/Billing/SOA/BillingSOAReportService'
import CollectionService from 'Services/Api/CollectionCDFServices'
import CollectionLookupService from 'Services/Api/Collection/LookupServices'

// style
import classes from './GenerateReports.module.scss'

// config
import { categories, reports, modules } from './config/generate-config'
import { Validate } from './config/Validator'

// Security
import RBAC from 'Security/RBAC'

const moment = require('moment')

export default function GenerateReports () {
  const [data, setData] = React.useState({})
  const [file, setFile] = React.useState()
  const [accounts, setAccounts] = React.useState([])


  React.useEffect(() => {
    BreadcrumbsService.setSelectedItemsWithoutHome(['collectionDistributionDashboard', 'generateReports'])
    CollectionLookupService.getAccounts()
      .then(res => {
        const { results = [] } = res.data.data
        setAccounts(results)
      })
      .catch(err => console.error(`Collection Lookup Service: ${err}`))
  }, [])

  const handleOnExport = () => {
    if (!file) return false
    const accounts = data?.category === 'Client' ? data?.client || [] : data?.member || []
    const dateFrom = data?.dateStart || null
    const dateTo = data?.dateEnd || null

    if (data?.report === 'Collection Forecast') {
      CollectionService.getCollectionForecast({ accounts, dateFrom, dateTo, isExports: true })
        .then(response => {
          const url = URL.createObjectURL(new Blob([response.data]))
          const link = document.createElement('a')
          link.href = url
          link.setAttribute('download', `collection-forecast_${moment().format('L')}.xlsx`)
          document.body.appendChild(link)
          link.click()
        })
    } else if (data?.report === 'Collection Report') {
      CollectionService.getCollectionReport({ accounts, dateFrom, dateTo, isExports: true })
        .then(response => {
          const url = URL.createObjectURL(new Blob([response.data]))
          const link = document.createElement('a')
          link.href = url
          link.setAttribute('download', `collection-report_${moment().format('L')}.xlsx`)
          document.body.appendChild(link)
          link.click()
        })
    }
  }

  const handleOnPrint = () => {
    if (!file) return false
    const url = URL.createObjectURL(file)
    const link = document.createElement('a')
    link.href = url
    link.setAttribute('download', `report_${moment().format('L')}.pdf`)
    document.body.appendChild(link)
    link.click()
  }

  const handleOnGenerate = () => {
    const accounts = data?.category === 'Client' ? data?.client || [] : data?.member || []
    const dateFrom = data?.dateStart || null
    const dateTo = data?.dateEnd || null

    const toValidate = {
      module: data?.module || '',
      report: data?.report || '',
      category: data?.category || '',
      date_start: dateFrom,
      date_end: dateTo
    }

    const { error } = Validate(toValidate)
    if (typeof error === 'object' && Object.keys(error).length > 0) {
      setData(i => ({ ...i, error: { ...error } }))
      return false
    }

    if (moment(toValidate.date_start) > moment(toValidate.date_end)) {
      setData(i => ({ ...i, error: {
        date_start: `"Date Start" should not be greater that Date End`,
        date_end: `"Date End" should not be before the Start End`
      }}))
      return false
    }

    setFile()

    if (data?.report === 'Collection Forecast') {
      CollectionService.getCollectionForecast({ accounts, dateFrom, dateTo })
        .then(response => {
          const blob = new Blob([response.data])
          setFile(blob)
        })
    } else if (data?.report === 'Collection Report') {
      CollectionService.getCollectionReport({ accounts, dateFrom, dateTo })
        .then(response => {
          const blob = new Blob([response.data])
          setFile(blob)
        })
    }
  }

  const handleOnDownload = () => {
    if (!data?.option) return false
    if (data?.option === 'xls') {
      handleOnExport()
    } else if (data?.option === 'pdf') {
      handleOnPrint()
    }
  }

  return (
    <Grid container direction='row' className={classes.root}>
      <Grid item xs={12}>
        <Typography id='generate-reports-header' variant='h5'>
          Generate Reports
        </Typography>
      </Grid>
      <Grid item xs={3} className={classes.form}>
        <Grid container direction='column' spacing={3}>
          <Grid item xs={12}>
            <SelectComponent
              id='module'
              label='Module'
              value={data?.module || ''}
              error={data?.error?.module || null}
              direction='column'
              defaultClass='lead'
              showPlaceholder={false}
              select={modules || []}
              onChange={e => setData(i => ({ ...i, module: e.value }))}
            />
          </Grid>
          <Grid item xs={12}>
            <SelectComponent
              id='report'
              label='Report'
              value={data?.report || ''}
              error={data?.error?.report || null}
              direction='column'
              defaultClass='lead'
              showPlaceholder={false}
              select={reports || []}
              onChange={e => setData(i => ({ ...i, report: e.value }))}
            />
          </Grid>
          <Grid item xs={12}>
            <SelectComponent
              id='category'
              label='Category'
              value={data?.category || ''}
              error={data?.error?.category || null}
              direction='column'
              defaultClass='lead'
              showPlaceholder={false}
              select={categories || []}
              onChange={e => setData(i => ({ ...i, category: e.value }))}
            />
          </Grid>
          { data?.category === 'Client' && (
            <Grid item xs={12}>
              <SelectComponent
                id='client'
                label='Client'
                value={data?.client || []}
                error={data?.error?.client || null}
                direction='column'
                defaultClass='lead'
                showPlaceholder={false}
                select={accounts || []}
                multiple
                onChange={e => setData(i => ({ ...i, client: e.value }))}
                renderValue={selected => {
                  if (selected.length === 0) {
                    return <span>All Clients</span>;
                  }
                  const accountNames = []
                  selected.map(i => {
                    const index = accounts.map(account => { return account.value }).indexOf(i)
                    if (index >= 0) accountNames.push(accounts[index].name)
                  })
                  return accountNames.join(', ');
                }}
              />
          </Grid>
          )}
          <Grid item xs={12}>
            <DateTimePicker
              label='Date Start'
              value={data?.dateStart || null}
              error={data?.error?.date_start || null}
              defaultClass='lead'
              onChange={e => setData(i => ({ ...i, dateStart: e.toISOString() }))}
              placeholder='Date Start'
              type='date'
              orientation='column'
              momentFormat='ll'
              customStyle={{ padding: '8px' }}
            />
          </Grid>
          <Grid item xs={12}>
            <DateTimePicker
              label='Date End'
              value={data?.dateEnd || null}
              error={data?.error?.date_end || null}
              defaultClass='lead'
              onChange={e => setData(i => ({ ...i, dateEnd: e.toISOString() }))}
              placeholder='Date End'
              type='date'
              orientation='column'
              momentFormat='ll'
              customStyle={{ padding: '8px' }}
            />
          </Grid>
          <Grid item xs={12}>
            <Button
              id='generate-button'
              className={classes.generate}
              variant='contained'
              onClick={handleOnGenerate}
            >
              Generate
            </Button>
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={9}>
        <Grid container direction='column'>
          {file && (
            <Grid item xs={12}>
              <Grid container direction='row'>
                <Grid item className={classes.action}>
                  <Grid container direction='row' className={classes.control}>
                    <Grid item>
                      <ArrowDownwardIcon
                        fontSize={'small'}
                        className={`${classes.icon} ${classes['download-icon']}`}
                      />
                    </Grid>
                    <Grid item><Typography className={classes.label}>Download Report</Typography></Grid>
                  </Grid>
                  <Grid container className={classes.dialog} direction='column'>
                    <Grid item xs={12}>
                      <Typography>Choose Format</Typography>
                    </Grid>
                    <Grid item xs={12}>
                      <Checkbox
                        id='pdf-option'
                        checked={!!(data?.option === 'pdf') || false}
                        handleOnChange={e => setData(i => ({ ...i, option: e.target.checked ? 'pdf' : '' }))}
                        label={'PDF'}
                      />
                    </Grid>
                    <Grid item xs={12}>
                      <Checkbox
                        id='xls-option'
                        checked={!!(data?.option === 'xls') || false}
                        handleOnChange={e => setData(i => ({ ...i, option: e.target.checked ? 'xls' : '' }))}
                        label={'XLS'}
                      />
                    </Grid>
                    <Grid item xs={12}>
                      <Button
                        id='download-button'
                        className={classes.download}
                        variant='contained'
                        onClick={handleOnDownload}
                      >
                        <Grid container direction='row'>
                          <Grid item className={classes['icon-wrapper']}><GetAppRoundedIcon fontSize='small' /></Grid>
                          <Grid item>
                            <Grid container direction='column' justify='space-around'>
                              <Grid item>
                                <Typography className={classes['download-text']}>Download Report</Typography>
                              </Grid>
                            </Grid>
                          </Grid>
                        </Grid>
                      </Button>
                    </Grid>
                  </Grid>
                </Grid>

                {RBAC.hasPolicy({or: ['BC-140']}) && (
                  <Grid
                    item
                    className={classes.action}
                    onClick={handleOnPrint}
                  >
                    <Grid container direction='row' className={classes.control}>
                      <Grid item><LocalPrintshopIcon className={classes.icon} /></Grid>
                      <Grid item><Typography className={classes.label}>Print Report</Typography></Grid>
                    </Grid>
                  </Grid>
                )}
              </Grid>
            </Grid>
          )}
          <Grid item xs={12} className={classes.viewer}>
            <PdfViewer
              initialScale={1}
              file={file}
              download={() => handleOnPrint()}
              onPrint={() => handleOnPrint()}
            />
          </Grid>
        </Grid>
      </Grid>
    </Grid>
  )
}
