import instance from '../../instance'

const axios = instance('/b/t')
// const axiosCreditMemo = instance('/b/ccm')
// const axiosDebitMemo = instance('/b/cdm')
// const axiosInvoice = instance('/b/ci')

const axiosCreditMemo = instance('/b/cm')
const axiosDebitMemo = instance('/b/dm')
const axiosInvoice = instance('/b/ci')
/**
 * Abstracts the underlying technology that is being used to process the actions in the frontend
 * e.g. Processmaker route terminology, as processmaker can be replaced or removed
 *
 * Front end should only request to process actions like save, submit, approve, disapprove, and void
 *
 * @export
 * @class BillingTransactionService
 */
export default class BillingTransactionService {
  static save ({ transactionNo, transactionType }) {
    return axios({
      url: `/billing/transactions/save?transactionType=${transactionType}&transactionNumber=${transactionNo}`,
      method: 'PUT'
    })
  }

  static submit ({ transactionNo, transactionType }) {
    return axios({
      url: `/billing/transactions/submit?transactionType=${transactionType}&transactionNumber=${transactionNo}`,
      method: 'PUT'
    })
  }

  static approve ({ transactionNo, transactionType }) {
    return axios({
      url: `/billing/transactions/approve?transactionType=${transactionType}&transactionNumber=${transactionNo}`,
      method: 'PUT'
    })
  }

  static disapprove ({ transactionNo, transactionType }) {
    return axios({
      url: `/billing/transactions/disapprove?transactionType=${transactionType}&transactionNumber=${transactionNo}`,
      method: 'PUT'
    })
  }

  static void ({ transactionNo, transactionType }) {
    return axios({
      url: `/billing/transactions/void?transactionType=${transactionType}&transactionNumber=${transactionNo}`,
      method: 'PUT'
    })
  }

  static get (params, transactionType) {
    let axiosCall = axios
    let url = `/billing/${params.entityType}/${transactionType}/${params.status}`
    switch (transactionType) {
      case 'credit':
        axiosCall = axiosCreditMemo
        break
      case 'debit':
        axiosCall = axiosDebitMemo
        break
      case 'invoice':
        axiosCall = axiosInvoice
        break
      default:
        url = `/billing/transactions/${params.status}`
    }

    return axiosCall({
      url: url,
      method: 'GET',
      params,
      headers: { skip_generate_form_data: 1 }
    })
  }
}
