import React from 'react'

// MATERIAL COMPONENTS
import { Popover, Button } from '@material-ui/core'

// MATERIAL ICONS
import { GetApp } from '@material-ui/icons';

/*
 * PAGE COMPONENTS 
 * 
 * Components customized for current page
*/
import DownloadRadioGroup from './DownloadRadioGroup'
import DownloadCheckBoxGroup from './DownloadCheckBoxGroup'

// Services
import AuditLogServices from 'Services/Api/Audit/AuditLogServices'

// STYLES
import style from '../AuditLog.module.scss'


export default function DownloadPopOver ({ anchorEl, open, handleClose, list, reportType }) {
  
  const toDownload = []
  const [downloadAll, setDownloadAll] = React.useState(true)

  const exports = () => {
    for (const i in toDownload) {
      console.log(`downloading ${toDownload[i]}...`)
      const type = {toDl: toDownload[i], report: reportType, dlAll: downloadAll}
      if (toDownload[i] !== 'xml') {
        AuditLogServices.getAuditExports(list.body, type).then(res => {
          console.log(res)
          const url = URL.createObjectURL(new Blob([res.data]))
          const link = document.createElement('a')
          link.href = url
          link.setAttribute('download', `audit.${toDownload[i]}`)
          document.body.appendChild(link)
          link.click()
        })
      } else {
        AuditLogServices.getAuditXMLExports(list.body, type).then(res => {
          console.log(res)
          const url = URL.createObjectURL(new Blob([res.data.data], {type: 'text/plain'}))
          const link = document.createElement('a')
          link.href = url
          link.setAttribute('download', `audit.${toDownload[i]}`)
          document.body.appendChild(link)
          link.click()
        })
      }
    }
  }

  const chooseData = (params) => {
    if (params.data === 'filtered') {
      setDownloadAll(true)
    } else {
      setDownloadAll(false)
    }
  }

  const downloadables = (params) => {
    if (!toDownload.includes(params.downloads)) {
      toDownload.push(params.downloads)
    } else if (toDownload.includes(params.downloads)) {
      if (toDownload.length === 1) {
        toDownload.pop()
      } else if ((toDownload.indexOf(params.downloads) > -1)) {
        toDownload.splice(toDownload.indexOf(params.downloads), 1)
      }
    }
  }

  return <Popover
    className={style.downloadPopOver}
    open={open}
    anchorEl={anchorEl}
    onClose={handleClose}
    anchorOrigin={{
      vertical: 'bottom',
      horizontal: 'left'
    }}
    transformOrigin={{
      vertical: 'top',
      horizontal: 'center'
    }}
  >
    <div className={style.downloadPopOverContent}>
      <DownloadRadioGroup chooseData={chooseData} />
      <br />
      <DownloadCheckBoxGroup downloadables={downloadables}/>
      <br />
      <div className={style.downloadPopOverButton}>
        <Button
          variant='contained'
          color='primary'
          className={`button-positive`}
          startIcon={<GetApp />}
          onClick={exports}
          // disabled={dlList}
        >
          Download Report
        </Button>
      </div>
    </div>

  </Popover >
}
