import instance from '../../../instance'
// const axios = instance('/b/cdm')
const axios = instance('/b/dm')

export default class BillingDebitMemosServices {
  /**
   * Used By:
   *  - ClientDebitMemosToValidatePage
   * TODO use the DebitMemoViewService instead
   * @static
   * @param {*} { transaction_number }
   * @returns
   * @memberof BillingDebitMemosServices
   */
  static getTobeRevisedDebitMemo ({ transaction_number }) {
    return axios({
      url: `/billing/debit-get/${transaction_number}`,
      method: 'GET'
    })
  }

  /**
   * Used By:
   *  - ClientDebitMemosToValidatePage
   *
   * @static
   * @param {*} params
   * @returns
   * @memberof BillingDebitMemosServices
   */
  static getDebitMemoDetails (params) {
    return axios({
      url: `/billing/debit-memo-details/${params.transactionNumber}`,
      method: 'GET',
      params,
      headers: { skip_generate_form_data: 1 }
    })
  }

  /**
   * Used By:
   *  - ClientDebitMemosToValidatePage
   *
   * @static
   * @param {*} { transaction_number }
   * @returns
   * @memberof BillingDebitMemosServices
   */
  static assignSupervisor ({ transaction_number }) {
    return axios({
      url: `/billing/debit/debit-assign-supervisor/${transaction_number}`,
      method: 'PUT'
    })
  }

  /**
   * Used By:
   *  - ClientDebitMemosToValidatePage
   *
   * @static
   * @param {*} { transaction_number }
   * @returns
   * @memberof BillingDebitMemosServices
   */
  static routeDebitMemo ({ transaction_number, action }) {
    return axios({
      url: `/billing/debit/route-ticket?action=${action}&transactionNumber=${transaction_number}`,
      method: 'PUT'
    })
  }

  // TODO ==============FOR CLEAN UP =============================================

  static getDebitMemosNew (params) {
    return axios({
      url: `/billing/debit-new`,
      method: 'GET',
      params,
      headers: { skip_generate_form_data: 1 }
    })
  }

  static getDebitMemosNewTile (params) {
    return axios({
      url: `/billing/debit-new-tile`,
      method: 'GET',
      params,
      headers: { skip_generate_form_data: 1 }
    })
  }

  static getDebitMemosSaved () {
    return axios({
      url: `/billing/debit-save`,
      method: 'GET'
    })
  }
  static getDebitMemosForApproval (params) {
    return axios({
      url: `/billing/debit-for-approval`,
      method: 'GET',
      params,
      headers: { skip_generate_form_data: 1 }
    })
  }
  static getDebitMemosForApprovalTile (params) {
    return axios({
      url: `/billing/debit-for-approval-tile`,
      method: 'GET',
      params,
      headers: { skip_generate_form_data: 1 }
    })
  }
  static getDebitMemosForApprovalAnalyst (params) {
    return axios({
      url: `/billing/debit-for-approval-analyst`,
      method: 'GET',
      params,
      headers: { skip_generate_form_data: 1 }
    })
  }
  static getDebitMemosForApprovalAnalystTile (params) {
    return axios({
      url: `/billing/debit-for-approval-analyst-tile`,
      method: 'GET',
      params,
      headers: { skip_generate_form_data: 1 }
    })
  }
  static getDebitMemosValidated () {
    return axios({
      url: `/billing/debit-validated`,
      method: 'GET'
    })
  }
  static getDebitMemosApproved (params) {
    return axios({
      url: `/billing/debit-approved`,
      method: 'GET',
      params,
      headers: { skip_generate_form_data: 1 }
    })
  }
  static getDebitMemosApprovedTile (params) {
    return axios({
      url: `/billing/debit-approved-tile`,
      method: 'GET',
      params,
      headers: { skip_generate_form_data: 1 }
    })
  }
  static createDebitMemo ({ data }) {
    return axios({
      url: `/billing/debit-create`,
      method: 'POST',
      data
    })
  }
  static assignAnalyst ({ transaction_number }) {
    return axios({
      url: `/billing/debit/debit-assign/${transaction_number}`,
      method: 'PUT'
    })
  }

  static updateDebitMemoHeader ({ data }, transaction_number) {
    return axios({
      url: `/billing/debit-header/update/${transaction_number}`,
      method: 'PUT',
      data
    })
  }
  static deleteDebitMemoDetails (transaction_number) {
    return axios({
      url: `/billing/debit-memo-details/${transaction_number}`,
      method: 'DELETE'
    })
  }
  static createDebitMemoDetails ({ data }, transaction_number) {
    return axios({
      url: `/billing/debit-memo-details/${transaction_number}`,
      method: 'POST',
      data
    })
  }
}
