import React, { useState } from 'react'

// MATERIAL COMPONENTS
import { Radio, RadioGroup, FormControl, FormLabel, FormControlLabel, Typography } from '@material-ui/core';

// STYLES
import style from '../AuditLog.module.scss'

export default function DownloadRadioGroup(props) {
  const { chooseData } = props

  const [value, setValue] = useState('all');

  const handleChange = (event) => {
    setValue(event.target.value);
    chooseData({data: value})
  };

  return (
    <div>
      <FormControl component="fieldset">
        <FormLabel >
          <div className={style.downloadGroupLabel}>
            Choose Data
        </div>
        </FormLabel>
        <RadioGroup aria-label="gender" name="gender1" value={value} onChange={handleChange}>
          <FormControlLabel style={{ margin: 0, padding: 0 }} value="all"
            control={<Radio style={{ color: '#555', margin: 0, paddingTop: 3, paddingBottom: 3, paddingLeft: 0, fontWeight: 400 }} />}
            label="Include all rows" />
          <FormControlLabel style={{ margin: 0, padding: 0 }} value="filtered"
            control={<Radio style={{ color: '#555', margin: 0, paddingTop: 3, paddingBottom: 3, paddingLeft: 0, fontWeight: 400 }} />}
            label="Include Only Filtered Rows" />
        </RadioGroup>
      </FormControl>
    </div>
  )
}
