import React from 'react'
import Paper from '@material-ui/core/Paper'
import InputBase from '@material-ui/core/InputBase'
import Typography from '@material-ui/core/Typography'
import NumberFormat from 'react-number-format'
import { CurrencyFormatter } from 'Utilities/TextFormatter'
import Grid from '@material-ui/core/Grid'

import classes from './TextFieldCurrency.module.scss'
export default function TextFieldCurrency (props) {
  const {
    alignItems = '',
    disabled,
    wrapperSize = '',
    label,
    required,
    currency = true,
    currencyTheme = 'grey',
    readOnly,
    error = null,
    onChange,
    placeholder = '',
    direction='column',
    defaultClass = null,
    labelWidth = null,
    ...other
  } = props

  const [focus, setFocus] = React.useState()
  const [_error, setError] = React.useState()

  React.useEffect(() => {
    setError(error)
  }, [error])

  const handleOnChange = e => {
    if (typeof onChange === 'function') onChange({ target: { value: e.floatValue, name: other.name } })
    if (_error) setError()
  }

  return (
    <Grid className={classes.root} container direction={direction}>
      <Grid
        item
        xs={!labelWidth && direction === 'row' ? 2 : labelWidth ? labelWidth : false}
        className={`${direction === 'row' ? classes['text-padding'] : ''}`}
      >
        <Grid
          container
          direction={direction === 'row' ? 'column' : direction}
          justify={`${direction === 'row' ? 'space-around' : ''}`}
          className={`${direction === 'row' ? classes['vertical-container'] : ''}`}
        >
          <Grid item>
            <Typography variant='subtitle2' className={`${classes.label} ${disabled ? classes.disabled : ''} ${classes[defaultClass] || ''}`}>
              {label}
              {!readOnly && required ? ' *' : ''}
            </Typography>
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs className={classes.wrapper} style={{ width: `${wrapperSize}` }}>
        {readOnly ? (
          //  Read Only Value
          <Typography id={props.id} variant='subtitle2' className={`${classes.label} ${classes['read-only-value']}`}>
            {CurrencyFormatter(other.value)}
          </Typography>
        ) : (
          <React.Fragment>
            <Paper
              className={`${classes['text-field']} ${
                _error ? classes['has-error'] : focus && classes.focus
              } box-shadow-field`}
            >
              <NumberFormat
                id={props.id}
                className={`${classes.input} ${disabled ? classes.disabled : ''}`}
                disabled={!!disabled}
                required
                customInput={InputBase}
                isNumericString
                displayType={'input'}
                thousandSeparator
                decimalScale={2}
                fixedDecimalScale
                allowLeadingZeros={false}
                value={other.value}
                onValueChange={handleOnChange}
                isAllowed={values => {
                  const { floatValue } = values
                  return floatValue <= 10000000000000
                }}
                autoComplete='off'
                onFocus={() => setFocus(true)}
                onBlur={() => setFocus(false)}
                placeholder={placeholder}
                // {...other}
              />
              {currency && (
                <Grid className={`${classes.currency} ${classes[currencyTheme] || ''} ${disabled ? classes.disabled : ''}`}>
                  <Typography variant='subtitle2' className={classes.label}>
                    PHP
                  </Typography>
                </Grid>
              )}
            </Paper>
            {_error && <span className={classes.error}>{_error}</span>}
          </React.Fragment>
        )}
      </Grid>
    </Grid>
  )
}
