import Grid from '@material-ui/core/Grid'
import CloseIcon from '@material-ui/icons/Close'
import React from 'react'
import { Button, Dialog, Typography } from '@material-ui/core'
import TextField from 'SharedComponents/TextField/CustomTextField'
import CustomTextField from './../../../../Components/FormComponents/TextField/CustomTextField'
import SelectComponent from './../../../../Components/FormComponents/SelectComponent'
import TextFieldCurrency from './../../../../Components/FormComponents/TextFieldCurrency/TextFieldCurrency'
