// Styles
import classes from './ActionDialog.module.scss'

// Validators
import { Validate } from './../../config/LineItemValidators'

const moment = require('moment')

export const ApproveDialog = props => {
  const defaultFunction = function () {}
  const { open = false, handleOnApprove = defaultFunction, handleOnClose = defaultFunction, isAdvance, category } = props
  const [remarks, setRemarks] = React.useState('')

  return (
    <Dialog onClose={() => handleOnClose(false)} open={open} disableBackdropClick>
      <Grid className={classes['root-approve-dialog']} container direction='column'>
        <Grid item className={classes.title} xs={12}>
          <Grid className={classes['title-wrapper']} container direction='row'>
            <Grid item xs={12}>
              <Typography variant='h6'>Approve Collection Distribution</Typography>
            </Grid>
            <Grid className={classes['title-action']} item>
              <CloseIcon onClick={() => handleOnClose(false)} />
            </Grid>
          </Grid>
        </Grid>
        <Grid item className={classes.details} xs={12}>
          <Typography>
            Are you sure you want to approve the {isAdvance ? 'Advance ' : null }{category ? `${category} ` : ''}Collection Distribution?
          </Typography>
        </Grid>
        <Grid item className={classes.action} xs={12}>
          <Grid container direction='row' justify='space-between'>
            <Grid item xs={5}>
              <Button className={classes.cancel} variant='outlined' onClick={() => handleOnClose(false)}>
                No
              </Button>
            </Grid>
            <Grid item xs={5}>
              <Button className={classes.assign} variant='contained' onClick={() => handleOnApprove(remarks)}>
                Yes
              </Button>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    </Dialog>
  )
}

export const DisApproveDialog = props => {
  const defaultFunction = function () {}
  const { open = false, handleOnDisapprove = defaultFunction, handleOnClose = defaultFunction, isAdvance } = props
  const [remarks, setRemarks] = React.useState('')
  const [hasError, setError] = React.useState(false)

  return (
    <Dialog onClose={() => handleOnClose(false)} open={open} disableBackdropClick>
      <Grid className={classes['root-disapprove-dialog']} container direction='column'>
        <Grid item className={classes.title} xs={12}>
          <Grid className={classes['title-wrapper']} container direction='row'>
            <Grid item xs={12}>
              <Typography variant='h6'>Disapprove {isAdvance ? 'Advance ' : null }Collection Distribution</Typography>
            </Grid>
            <Grid className={classes['title-action']} item>
              <CloseIcon onClick={() => handleOnClose(false)} />
            </Grid>
          </Grid>
        </Grid>
        <Grid item className={classes.details} xs={12}>
          <Typography>
            Are you sure you want to disapprove the Collection Distribution? This will return the Collection
            Distribution form to the Collection Analyst for correction.
          </Typography>
        </Grid>
        <Grid item className={classes.info} xs={12}>
          <Typography>Select "No" to return to the current screen.</Typography>
        </Grid>
        <Grid item className={classes.info} xs={12}>
          <Typography>Reason for Disapproval *</Typography>
        </Grid>
        <Grid item className={`${classes.input} ${hasError ? classes.error : ''}`}>
          <Grid container direction='column'>
            <TextField
              id='disapproval-dialog-remarks'
              value={remarks}
              placeholder='Enter your disapproval reasons here'
              onChange={event => {
                setRemarks(event.target.value)
              }}
              onClick={e => setError(false)}
            />
          </Grid>
        </Grid>
        {hasError && (
          <Grid item className={classes.error} xs={12}>
            <Typography>Please enter your reason for disapproving the Client CD</Typography>
          </Grid>
        )}
        <Grid item className={classes.action} xs={12}>
          <Grid container direction='row' justify='space-between'>
            <Grid item xs={5}>
              <Button className={classes.cancel} variant='outlined' onClick={() => handleOnClose(false)}>
                NO
              </Button>
            </Grid>
            <Grid item xs={5}>
              <Button
                className={classes.assign}
                variant='contained'
                onClick={() => {
                  if (remarks.length === 0) {
                    setError(true)
                    return false
                  }
                  handleOnDisapprove(remarks)
                }}
              >
                YES
              </Button>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    </Dialog>
  )
}

