export const PaymentDialog = props => {
  const defaultFunction = function () {}
  const {
    open = false,
    onAddLineItem = defaultFunction,
    onSaveLineItem = defaultFunction,
    handleOnClose = defaultFunction,
    data = { data: null, index: null },
    paymentMode = [],
    banks = [],
    checkTypes = []
  } = props
  const [_data, setData] = React.useState({})
  const [_errors, setErrors] = React.useState()

  React.useEffect(() => {
    if (data.data) setData(i => ({ ...i, ...data.data, index: data.index }))
  }, [data])

  const handleChangeBank = _params => {
    const _bankDetails = banks.find(e => e.value === _params)
    if (!_bankDetails) return false
    setData(i => ({
      ...i,
      bank_account_number: _bankDetails.bank_account_number || '',
      bank_branch: _bankDetails.bank_branch || ''
    }))
  }

  const handleOnSubmit = () => {
    const { error } = Validate({ data: _data, type: _data.means_of_payment || '' })
    if (typeof error === 'object' && Object.keys(error).length > 0) {
      setErrors(i => ({ ...error }))
      return false
    }
    if (!_data.amount) return false
    if (typeof data.index !== 'number') onAddLineItem({ data: _data })
    if (typeof data.index === 'number') onSaveLineItem({ data: _data, index: data.index })
    setData({})
    setErrors()
    handleOnClose(false)
  }

  return (
    <Dialog onClose={() => { setData({}); handleOnClose(false); }} open={open} disableBackdropClick maxWidth={false}>
      <Grid className={classes['root-payment-dialog']} container direction='column'>
        <Grid item className={classes.title} xs={12}>
          <Grid className={classes['title-wrapper']} container direction='row'>
            <Grid item xs={12}>
              <Typography variant='h6'>Add Payment Detailsasdasd</Typography>
            </Grid>
            <Grid className={classes['title-action']} item>
              <CloseIcon onClick={() => { handleOnClose(false) }} />
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={12}>
          <SelectComponent
            id='payment-mode'
            label='Payment Mode'
            labelWidth={5}
            required
            value={_data.means_of_payment || ''}
            direction='row'
            defaultClass='lead'
            disabled={!!props.disabled}
            error={_errors?.default || _errors?.means_of_payment || null}
            select={paymentMode || []}
            showPlaceholder={false}
            onChange={e => { setData(i => ({ means_of_payment: e.value, index: i.index })); setErrors(); }}
          />
        </Grid>
        {/* Space */}
        <Grid item xs={12}>&nbsp;</Grid>

        { ['BANK_DEPOSIT', 'WIRETRANSFER'].find( e => e === _data.means_of_payment) && (
          <Grid item xs={12}>
            <Grid container direction='column'>
              <Grid item xs={12}>
                <SelectComponent
                  id='banks'
                  label='Bank'
                  labelWidth={5}
                  required
                  value={_data.preferred_bank || ''}
                  error={_errors?.bank_account_number || _errors?.bank_branch || null}
                  direction='row'
                  defaultClass='lead'
                  disabled={!!props.disabled}
                  select={banks || []}
                  showPlaceholder={false}
                  onChange={e => {
                    setData(i => ({
                      ...i,
                      preferred_bank: e.value
                    }))
                    handleChangeBank(e.value)
                  }}
                />
              </Grid>
              <Grid item xs={12}>&nbsp;</Grid>
              { (_data.bank_account_number || _data.bank_branch) && (
                <Grid item xs={12}>
                  <Grid container direction='row'>
                    <Grid item xs={5}>&nbsp;</Grid>
                    <Grid item xs>
                      <Grid container direction='row'>
                        { _data.bank_account_number && (
                          <React.Fragment>
                            <Grid item xs={6}>
                              <Typography className={classes['text-label']}>Bank Account No.</Typography>
                            </Grid>
                            <Grid item xs={6}>
                              <Typography className={classes['text-value']}>{_data.bank_account_number}</Typography>
                            </Grid>
                          </React.Fragment>
                        )}
                        { _data.bank_branch && (
                          <React.Fragment>
                            <Grid item xs={6}>
                              <Typography className={classes['text-label']}>Branch</Typography>
                            </Grid>
                            <Grid item xs={6}>
                              <Typography className={classes['text-value']}>{_data.bank_branch}</Typography>
                            </Grid>
                          </React.Fragment>
                        )}
                      </Grid>
                    </Grid>
                  </Grid>
                </Grid>
              )}
            </Grid>
          </Grid>
        ) }

        { ['PICK_UP_CHECK', 'DELIVER_CHECK'].find( e => e === _data.means_of_payment) && (
          <React.Fragment>
            <Grid item xs={12}>
              <CustomTextField
                id='bank-branch'
                label='Bank - Branch'
                labelWidth={5}
                required
                placeholder='Enter Bank - Branch'
                value={_data.bank_branch || ''}
                error={_errors?.bank_branch || null}
                direction='row'
                defaultClass='lead'
                noMargin
                disabled={!!props.disabled}
                onChange={e => {
                  const data = e.target.value
                  setData(i => ({ ...i, bank_branch: data }))
                }}
              />
            </Grid>
            <Grid item xs={12}>&nbsp;</Grid>
            <Grid item xs={12}>
              <CustomTextField
                id='check-number'
                label='Check No.'
                labelWidth={5}
                required
                placeholder='Enter Check No'
                value={_data.check_number || ''}
                error={_errors?.check_number || null}
                direction='row'
                defaultClass='lead'
                noMargin
                disabled={!!props.disabled}
                onChange={e => {
                  const data = e.target.value
                  setData(i => ({ ...i, check_number: data }))
                }}
              />
            </Grid>
            <Grid item xs={12}>&nbsp;</Grid>
            <Grid item xs={12}>
              <DateTimePicker
                label='Check Date.'
                labelWidth={5}
                defaultClass='lead'
                type='date'
                customStyle={{ padding: '8px 5px' }}
                momentFormat='ll'
                required
                value={_data.check_date}
                error={_errors?.check_date || null}
                noMargin
                onChange={e => setData(i => ({ ...i, check_date: e.toISOString() }))}
                placeholder='Select Check Date'
                min={moment().format('YYYY-MM-DD')}
              />
            </Grid>
            <Grid item xs={12}>&nbsp;</Grid>
            <Grid item xs={12}>
              <SelectComponent
                id='check-type'
                label='Check Type'
                labelWidth={5}
                required
                value={_data.check_type || ''}
                error={_errors?.check_type || null}
                direction='row'
                defaultClass='lead'
                disabled={!!props.disabled}
                select={checkTypes || []}
                showPlaceholder={false}
                onChange={e => {
                  setData(i => ({ ...i, check_type: e.value }))
                }}
              />
            </Grid>
            <Grid item xs={12}>&nbsp;</Grid>
          </React.Fragment>
        ) }

        {/* Amount */}
        <Grid item xs={12}>
          <TextFieldCurrency
            id='amount'
            label='Amount'
            labelWidth={5}
            currencyTheme='outline'
            value={_data.amount || ''}
            error={_errors?.amount || null}
            required
            placeholder='Enter Amount'
            direction='row'
            defaultClass='lead'
            onChange={e => setData(i => ({ ...i, amount: e.target.value }))}
          />
        </Grid>
        
        <Grid item className={classes.action} xs={12}>
          <Grid container direction='row' justify='space-between'>
            <Grid item xs={5}>
              <Button id='add-payment-cancel-button' className={classes.cancel} variant='outlined' onClick={() => { setData({}); handleOnClose(false); }}>
                Cancel
              </Button>
            </Grid>
            <Grid item xs={5}>
              <Button
                id='add-payment-yes-button'
                className={classes.assign}
                variant='contained'
                onClick={() => handleOnSubmit()}
              >
                { typeof data.index !== 'number' ? 'Add' : 'Update' }
              </Button>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    </Dialog>
  )
}
