import React from 'react'
import PropTypes from 'prop-types'
import Typography from '@material-ui/core/Typography'
import noop from 'lodash/noop'

import CustomDialog from 'SharedComponents/CustomDialog/CustomDialog'

ConfirmDialog.propTypes = {
  open: PropTypes.bool,
  cancelBtn: PropTypes.bool,
  onCancel: PropTypes.func,
  onSubmit: PropTypes.func,
  title: PropTypes.string,
  paragraph1: PropTypes.string,
  paragraph2: PropTypes.string,
  submitLabel: PropTypes.string,
}

export default function ConfirmDialog({ 
  open = false, 
  cancelBtn = true, 
  onCancel = noop, 
  onSubmit = noop, 
  title, 
  paragraph1, 
  paragraph2, 
  submitLabel = "Yes",
  buttonStyle,
   ...props }) {

  return (
    <CustomDialog
      open={open}
      title={title}
      content={
        <div>
          <Typography style={{ marginBottom: 20 }}>{paragraph1}</Typography>
          { paragraph2 ? 
          <Typography style={{ marginBottom: 20 }}>{paragraph2}</Typography> : null
          }
        </div>
      }
      {...props}
      onClose={onCancel}
      onOk={onSubmit}
      submitLabel={submitLabel}
      cancelBtn={cancelBtn}
      cancelLabel={"No"}
      buttonStyle={{textTransform: 'none'}}
    />
  )
}
