import React from 'react'
import { useHistory } from 'react-router-dom'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
// Styles
import classes from './ConfirmDialog.module.scss'
import { Typography, DialogContent, Button, Grid } from '@material-ui/core'

export default function CustomizedDialogs (props) {
  const { assign, setAssign, onCancel, onOK, negative_label, positive_label, preGenConflict } = props
  const history = useHistory()

  const handleOnCloseSuccessDialog = () => {
    setAssign(false)
    // history.push('/')
  }

  return (
    <Dialog
      PaperProps={{
        style: {
          borderRadius: '0px',
          width: '450px',
          boxShadow: 'none'
        }
      }}
      onClose={handleOnCloseSuccessDialog}
      open={assign.show}
      aria-labelledby='confirm-dialog-parent'

      maxWidth='xs'
      onClose={handleOnCloseSuccessDialog}
      open={assign.show}
      className={classes.root}
      // PaperProps={{ style: { display: 'flex' } }}
    >
      <DialogTitle style={{marginLeft: '15px'}}>
        {assign.title}
        <IconButton
            aria-label='close'
            id='success-close-button'
            className={classes['success-close-button']}
            onClick={handleOnCloseSuccessDialog}
          >
            <CloseIcon />
          </IconButton>
      </DialogTitle>
      <DialogContent>
        <Typography>{assign.text}</Typography>
        <br />
        {preGenConflict ? <Typography>{assign.text2}</Typography> : null}

        <div className={classes.button}>
          {/* {negative_label && preGenConflict ? (
            <Button variant='outlined' className={classes['button-negative']} onClick={onCancel}>
              {negative_label}
            </Button>
          ) : (
            ''
          )} */}
          {positive_label ? (
            <Button variant='contained' className={classes['button-positive']} onClick={onOK}>
              {positive_label}
            </Button>
          ) : (
            ''
          )}
        </div>
      </DialogContent>
    </Dialog>
  )
}
