export default function AccountListPage (props) {
  // const [accountList, setAccountList] = React.useState({ account_list: {} })
  const [collapseAll, setCollapseAll] = React.useState(false)
  const [header_collapsed, setHeaderState] = React.useState()
  const [headers, setHeaders] = React.useState(header_items)
  const [tableData, setTableData] = React.useState([])
  const [clientTransactionLedgerData, setTransactionData] = React.useState([])
  const [table_columns, setColumns] = React.useState(mainColumn)
  const [sub_columns, setSubColumns] = React.useState(subColumn)
  const [userList, setUsersList] = React.useState([])
  const [userInfo, setUserInfo] = React.useState({
    userId: '',
    userFirstName: '',
    userMiddleName: '',
    userLastName: '',
    userRole: ''
  })

  const initialValue = {
    show: false,
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    filters: null,
    data: [],
    orderBy: '_id',
    order: true,
    sort: '',
    range: null,
    accessors: [
      'client.account_number',
      'client.account_name',
      'client.account_status',
      'contract.type',
      'contract.effectivity|isDate',
      'contract.expiry|isDate'
    ]
  }

  const [isFetching, setIsFetchingState] = React.useState(false)
  const [list, setList] = React.useState(initialValue)
  const [params, setParams] = React.useState({})

  const [pagination, setPagination] = React.useState({
    count: list ? list.count : 0,
    rowsPerPage: list ? list.limit : 0,
    page: list ? list.page : 0
  })

  // For Display Settings
  var today = moment().format('YYYY-MM-DD')
  const [fromDate, setFromDate] = React.useState(null)
  const [toDate, setToDate] = React.useState(null)
  const [toDateError, setToDateError] = React.useState('')
  const [fromDateError, setFromDateError] = React.useState('')
  const [anchorEl, setAnchorEl] = React.useState(null)
  const [selectedValue, setSelectedValue] = React.useState('active');

  const handleChange = (event) => {
    setSelectedValue(event.target.value);
  };

  const handleClick = (event) => {
    if (event.target.value === 'x' || event.target.value === 'Cancel') {
      setAnchorEl(null)
    } else {
      setAnchorEl(anchorEl ? null : event.currentTarget)
    }
  }
  const open = Boolean(anchorEl)
  const id = open ? 'date-popper' : undefined
  const handleOnApply = (event) => {
    let fdate = new Date(fromDate)
    let tdate = new Date(toDate)
    const params = {
      'range': {
        'start': fdate,
        'end': tdate
      }
    }
    if (!fdate.valueOf() && !tdate.valueOf()) {
      setFromDateError('Please select an effectivity start date that is not later than the current date')
      setToDateError('Please select an effectivity end date that is not later than the current date')
    } else if (fdate.valueOf() > tdate.valueOf()) {
      setToDateError('Please select an effectivity end date that is not later than the current date')
    } else {
      setFromDateError('')
      setToDateError('')
      if (fetchData) {
        fetchData(params)
      }
    }
  }
  // END For Display Settings

  React.useEffect(() => {
    setPagination({
      count: list ? list.count : 0,
      rowsPerPage: list ? list.limit : 0,
      page: list ? list.page : 0
    })
  }, [list])

  const StyledTableCell = withStyles(theme => ({
    head: {
      backgroundColor: theme.palette.common.grey,
      color: theme.palette.common.white
    },
    body: {
      fontSize: 14
    }
  }))(TableCell)

  const StyledTableRow = withStyles(theme => ({
    root: {
      '&:nth-of-type(odd)': {
        backgroundColor: theme.palette.action.hover
      }
    }
  }))(TableRow)

  const handleTableCollapseAll = () => {
    console.log('ALL: ', collapseAll)
    setCollapseAll(!collapseAll)
    setHeaderState(!collapseAll)
    header_collapsed == true ? setHeaders(header_items) : setHeaders(header_items_collapsed)
    header_collapsed == true ? setColumns(mainColumn) : setColumns(mainColumn_collapsed)
  }

  const handleHeaders = header_collapsed => {
    console.log('header_collapsed')
    header_collapsed ? setHeaders(header_items) : setHeaders(header_items_collapsed)
    header_collapsed ? setColumns(mainColumn) : setColumns(mainColumn_collapsed)
  }

  // const handleTableCollapse = () => {
  //   collapseAll ? setHeaders(header_items_collapsed) : setHeaders(header_items)
  //   collapseAll ? setColumns(mainColumn) : setColumns(mainColumn_collapsed)
  //   console.log('header_collapsed')
  // }

  React.useEffect(() => {
    try {
      const { getUserInfoFunction } = ConfigController2.getUserInfo().then(values => {
        const { data } = values
        setUserInfo(data)
      })
    } catch (error) {
      console.log(error)
    }
  }, [])

  React.useEffect(() => {
    fetchData(params)
  }, [])

  const fetchData = params => {
    setIsFetchingState(true)
    const _params = {
      userList: userList,
      limit: list.limit,
      page: list.page,
      sort: `${list.orderBy}|${list.order ? 'asc' : 'desc'}`,
      filters: list.filters,
      search: list.search,
      accessors: list.accessors,
      range: list.range,
      ...params
    }
    if (localStorage.getItem('isAnalyst') === 'true') {
      _params['analystUserId'] = localStorage.getItem('user_id')
    }

    setParams(_params)
    console.log(params)
    Promise.all([BillAnalystServices.getClientTransactionLedgerEntries(_params)]).then(values => {
      const [transactions] = values
      // console.log(transactions)
      const { results, count, limit, page } = transactions.data.data
      setList(i => ({
        ...i,
        body: results,
        count,
        limit,
        page,
        sort: _params.sort,
        range: _params.range ? _params.range : list.range,
        orderBy: _params.orderBy ? _params.orderBy : list.orderBy,
        search: typeof _params.search === 'string' ? _params.search : list.search
      }))
      setIsFetchingState(false)
    })
  }

  const mainSearchChange = _.debounce(e => {
    console.log(e.target.value)
    const _params = {
      ...params,
      search: '.*' + e.target.value + '.*',
      page: 1
    }
    fetchData(_params)
  }, 300)

  React.useEffect(() => {
    setCollapseAll(false)
    PageTitleService(`List of Accounts`)
    BreadcrumbsService.setSelectedItemsWithoutHome(['journal', 'accountList'])
  }, [])

  return (
    <React.Fragment>
      <div className={classes['search-container']} style={{marginTop: '10px'}}>
        <div id='modal-search-field' className={classes['search-field']}>
          <SearchTextComponent
            placeholder={'Search client or member'}
            onChange={e => {
              e.persist()
              mainSearchChange(e)
            }}
          />
        </div>
        
        { RBAC.hasPolicy({or: ['BC-003']}) &&
          <Link id='display-settings-link' className={classes['display-settings']} to='/' color='primary' size='medium'>
            <IconButton id='export-icon'>
              <ReplyIcon className={classes['export-icon']} />
              <Typography className='text-link underline'>Export</Typography>
            </IconButton>
          </Link>
        }

        <Link id='display-settings-link' className={classes['display-settings']} onClick={handleClick} color='primary' size='medium'>
          <IconButton id='display-settings-icon'>
            <ViewListRoundedIcon className={classes['display-settings-icon']} />
            <Typography className='text-link underline'>Display Settings</Typography>
          </IconButton>
        </Link>
        <Popper id={id} open={open} anchorEl={anchorEl} transition
            style={{'z-index': '1301'}}>
            {({ TransitionProps }) => (
              <Fade {...TransitionProps} timeout={350}>
                <div
                  style={{
                    'padding': '15px',
                    'border-radius': '5px',
                    'background-color': 'white',
                    'box-shadow': '5px 5px 5px 5px rgb(0,0,0,5%)',
                    'width': '500px'
                  }}
                >
                  <Grid container spacing={3}>
                    <Grid item xs={11}>
                      <Typography variant="body1" style={{fontWeight: "bold"}}>
                        Effectivity Date Range
                      </Typography>
                    </Grid>
                    <Grid item xs={1} style={{cursor: 'pointer', }} onClick={e => handleClick(e)}><CloseIcon /></Grid>
                  </Grid>
                  <Grid container spacing={2}>
                    <Grid item xs={6}>
                      <TextField
                        id='date'
                        type='date'
                        label='From'
                        variant="outlined" 
                        className={classes.textField}
                        InputLabelProps={{
                          shrink: true
                        }}
                        value={fromDate}
                        onChange={e => {
                          setFromDate(e.target.value)
                        }}
                        FormHelperTextProps={{
                          className: classes.helperText
                        }}
                        helperText={fromDateError}
                      />
                    </Grid>
                    <Grid item xs={6}>
                      <TextField
                        id='date'
                        type='date'
                        label='To'
                        variant="outlined" 
                        className={classes.textField}
                        InputLabelProps={{
                          shrink: true
                        }}
                        value={toDate}
                        onChange={e => {
                          setToDate(e.target.value)
                        }}
                        FormHelperTextProps={{
                          className: classes.helperText
                        }}
                        helperText={toDateError}
                      />
                    </Grid>
                  </Grid>
                  <Grid container spacing={3} style={{marginTop: "15px"}}>
                    <Grid item xs={12}>
                      <Typography variant="body1" style={{fontWeight: "bold"}}>
                      Display Accounts With Contracts That Are:
                      </Typography>
                    </Grid>
                    <Grid item xs={12} style={{textAlign: "center", padding: "0", marginBottom: "30px"}}>
                      <FormControl component="fieldset">
                        <RadioGroup row name="display-settings-status" value={selectedValue} onChange={handleChange}>
                          <FormControlLabel value="active" control={<Radio color="primary" />} label="Active" />
                          <FormControlLabel value="inactive" control={<Radio color="primary"/>} label="Inactive" style={{padding: "0 40px"}}/>
                          <FormControlLabel value="all" control={<Radio color="primary"/>} label="All Status" />
                        </RadioGroup>
                      </FormControl>
                    </Grid>
                  </Grid>
                  <div>
                    <Button
                      id='submitted-link'
                      variant='contained'
                      onClick={e => handleClick(e)}
                      style={{color: '#3ab77d',
                        'background-color': 'white',
                        width: '100px',
                        'margin-left': '140px'
                      }}
                    >
                        Cancel
                    </Button>
                    <Button
                      id='submitted-link'
                      variant='contained'
                      style={{color: 'white',
                        'background-color': '#3ab77d',
                        width: '100px',
                        'margin-left': '20px'
                      }}
                      onClick={handleOnApply}
                    >
                        Apply
                    </Button>
                  </div>
                </div>
              </Fade>
            )}
          </Popper>
          
      </div>
      <TreeViewTable
        headCells={headers}
        handleCollapseEvent={handleTableCollapseAll}
        collapseAllState={collapseAll}
        isViewTree
        sorter
        fetchData={fetchData}
        params={params}
        count={list.count}
        pagination={pagination}
      >
        <TableBody>
          {isFetching && (
            <StyledTableRow>
              <StyledTableCell component='th' colspan={10}>
                <CircularLoading />
              </StyledTableCell>
            </StyledTableRow>
          )}
          {!isFetching &&
            list &&
            list.body &&
            list.body.map((item, index) =>
              Array.isArray(item.members) & (item.members[0] !== null) & (item.members.length > 0) ? (
                <CollapsibleRow
                  index={index}
                  id={`account-id-${index}`}
                  data={item}
                  objectKey={'members'}
                  tableColumnOrder={table_columns}
                  subColumnOrder={sub_columns}
                  offset={3}
                  collapseAllState={collapseAll}
                  collapseFunction={handleHeaders}
                  actionTo={`/billing/journal/transactions`}
                  param='corporate_id'
                  actionText={'View'}
                  subActionTo={`/billing/journal/member`}
                  subParam='person_id'
                  subActionText={'View'}
                  monetary_columns={monetary_columns}
                  emphasis_columns={emphasis_columns}
                />
              ) : (
                <NormalRow
                  index={index}
                  table_columns={table_columns}
                  data={item}
                  id={`link-table-${index}`}
                  actionTo={`/billing/journal/transactions`}
                  param='corporate_id'
                  actionText={'View'}
                  style={{ textDecoration: 'none' }}
                  show={localStorage.getItem('isAnalyst') !== 'true'}
                  monetary_columns={monetary_columns}
                  emphasis_columns={emphasis_columns}
                />
              )
            )}
        </TableBody>
      </TreeViewTable>
    </React.Fragment>
  )
}
