import React from 'react'
import * as moment from 'moment'

import { Typography, Box } from '@material-ui/core'
import RemoveCircleIcon from '@material-ui/icons/RemoveCircle'

import classes from '../../CreateTransactionPage.module.scss'

import SALES_CHANNEL from '../../../../Constant/SalesChannelTypes'

export default function Sidebar (props) {
  const { clientInfo, contractInfo, billingInfo, location, handlBackToSelectClientContract } = props

  return (
    <>
      <div>
        <Typography variant='h6'>
          {clientInfo.account_name}
          <RemoveCircleIcon
            onClick={() => handlBackToSelectClientContract()}
            color='secondary'
            className={classes['remove-icon']}
          />
        </Typography>

        <div className={classes['drawer-content']}>
          <Typography id='drawer-account-number-field' color='textSecondary'>
            ACCOUNT NO.
          </Typography>
          <Typography id='drawer-account-number-value'>
            <Box fontWeight='fontWeightBold'>{clientInfo.account_number}</Box>
          </Typography>
        </div>

        <div className={classes['drawer-content']}>
          <Typography id='drawer-account-status-field' color='textSecondary'>
            ACCOUNT STATUS
          </Typography>
          <Typography id='drawer-account-status-value'>
            <Box fontWeight='fontWeightBold'>{clientInfo.account_status}</Box>
          </Typography>
        </div>
        <div className={classes['drawer-content']}>
          <Typography id='drawer-payment-term-field' color='textSecondary'>
            PAYMENT TERM
          </Typography>
          <Typography id='drawer-payment-term-value'>
            <Box fontWeight='fontWeightBold'>{contractInfo.payment_term}</Box>
          </Typography>
        </div>

        <div className={classes['drawer-content']}>
          <Typography id='drawer-tax-type-field' color='textSecondary'>
            TAX TYPE
          </Typography>
          <Typography id='drawer-tax-type-value'>
            <Box fontWeight='fontWeightBold'>{clientInfo.tax_type}</Box>
          </Typography>
        </div>

        <div className={classes['drawer-content']}>
          <Typography id='drawer-corporate-tin-field' color='textSecondary'>
            ACCOUNT TIN
          </Typography>
          <Typography id='drawer-corporate-tin-value'>
            <Box fontWeight='fontWeightBold'>{clientInfo.tin}</Box>
          </Typography>
        </div>

        <div className={classes['drawer-content']}>
          <Typography id='drawer-billing-cyle-field' color='textSecondary'>
            BILLING CYCLE
          </Typography>
          <Typography id='drawer-billing-cyle-value'>
            <Box fontWeight='fontWeightBold'>{billingInfo.cycle.code + ' - ' + billingInfo.cycle.name}</Box>
          </Typography>
        </div>
        <div className={classes['drawer-content']}>
          <Typography id='drawer-contract-plan-type-field' color='textSecondary'>
            CONTRACT TYPE
          </Typography>
          <Typography id='drawer-contract-plan-type-value'>
            <Box fontWeight='fontWeightBold'>{contractInfo.type}</Box>
          </Typography>
        </div>

        <div className={classes['drawer-content']}>
          <Typography id='drawer-effectivity-date-field' color='textSecondary'>
            EFFECTIVITY DATE
          </Typography>
          <Typography id='drawer-effectivity-date-value'>
            <Box fontWeight='fontWeightBold'>{moment(contractInfo.effectivity).format('MMM DD, YYYY')}</Box>
          </Typography>
        </div>

        <div className={classes['drawer-content']}>
          <Typography id='drawer-expiration-date-field' color='textSecondary'>
            EXPIRATION DATE
          </Typography>
          <Typography id='drawer-expiration-date-value'>
            <Box fontWeight='fontWeightBold'>{moment(contractInfo.expiration).format('MMM DD, YYYY')}</Box>
          </Typography>
        </div>

        <div className={classes['drawer-content']}>
          <Typography id='drawer-sales-channel-field' color='textSecondary'>
            SALES CHANNEL
          </Typography>
          {clientInfo.sales_channel ? (
            <Typography id='drawer-sales-channel-value'>
              <Box fontWeight='fontWeightBold'>
                {clientInfo.sales_channel.type ? clientInfo.sales_channel.type : '-'}
              </Box>
            </Typography>
          ) : (
            <Typography id='drawer-sales-channel-value'>
              <Box fontWeight='fontWeightBold'>-</Box>
            </Typography>
          )}
        </div>

        <div className={classes['drawer-content']}>
          {clientInfo.sales_channel && (
            <React.Fragment>
              <Typography id='drawer-sales-channel-name-field' color='textSecondary'>
                {clientInfo.sales_channel.type === SALES_CHANNEL.AGENT
                  ? 'AGENT NAME'
                  : clientInfo.sales_channel.type === SALES_CHANNEL.BROKER
                  ? 'NAME OF COMPANY'
                  : clientInfo.sales_channel.type === SALES_CHANNEL.BDO && 'BDO NAME'}
              </Typography>
              <Typography id='drawer-sales-channel-name-value'>
                <Box fontWeight='fontWeightBold'>
                  {clientInfo.sales_channel.name ? clientInfo.sales_channel.name : '-'}
                </Box>
              </Typography>
            </React.Fragment>
          )}
        </div>

        {clientInfo.sales_channel && clientInfo.sales_channel.type === SALES_CHANNEL.BROKER && (
          <React.Fragment>
            <div className={classes['drawer-content']}>
              <Typography id='drawer-broker-name-field' color='textSecondary'>
                BROKER NAME
              </Typography>
              <Typography id='drawer-broker-name-value'>
                <Box fontWeight='fontWeightBold'>
                  {clientInfo.sales_channel.contact_person ? clientInfo.sales_channel.contact_person : '-'}
                </Box>
              </Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-position-field' color='textSecondary'>
                POSITION
              </Typography>
              <Typography id='drawer-position-value'>
                <Box fontWeight='fontWeightBold'>
                  {clientInfo.sales_channel.position ? clientInfo.sales_channel.position : '-'}
                </Box>
              </Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-contact-no-field' color='textSecondary'>
                CONTACT NO.
              </Typography>
              <Typography id='drawer-contact-no-value'>
                <Box fontWeight='fontWeightBold'>
                  {clientInfo.sales_channel.contact_no ? clientInfo.sales_channel.contact_no : '-'}
                </Box>
              </Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-email-field' color='textSecondary'>
                EMAIL
              </Typography>
              <Typography id='drawer-email-value'>
                <Box fontWeight='fontWeightBold'>
                  {clientInfo.sales_channel.email_address ? clientInfo.sales_channel.email_address : '-'}
                </Box>
              </Typography>
            </div>
          </React.Fragment>
        )}

        <div className={classes['drawer-content']}>
          <Typography id='drawer-location-field' color='textSecondary'>
            ACCOUNT LOCATION
          </Typography>
          <Typography id='drawer-location-value'>
            <Box fontWeight='fontWeightBold'>{location}</Box>
          </Typography>
        </div>
      </div>
    </>
  )
}
