import React from 'react'
import _ from 'lodash'

import PropTypes from 'prop-types'
import clsx from 'clsx'
import { lighten, makeStyles } from '@material-ui/core/styles'
import Table from '@material-ui/core/Table'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TablePagination from '@material-ui/core/TablePagination'
import TableRow from '@material-ui/core/TableRow'
import TableSortLabel from '@material-ui/core/TableSortLabel'
import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'
import Paper from '@material-ui/core/Paper'
import Checkbox from '@material-ui/core/Checkbox'
import IconButton from '@material-ui/core/IconButton'
import Tooltip from '@material-ui/core/Tooltip'
import DeleteIcon from '@material-ui/icons/Delete'
import FilterListIcon from '@material-ui/icons/FilterList'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSort } from '@fortawesome/free-solid-svg-icons'

import FirstPageIcon from '@material-ui/icons/FirstPage'
import KeyboardArrowLeft from '@material-ui/icons/KeyboardArrowLeft'
import KeyboardArrowRight from '@material-ui/icons/KeyboardArrowRight'
import LastPageIcon from '@material-ui/icons/LastPage'
import Box from '@material-ui/core/Box'

import CustomSearchTextComponent from '../CustomSearchTextComponent/CustomSearchTextComponent'

const useToolbarStyles = makeStyles(theme => ({
  root: {
    paddingLeft: theme.spacing(2),
    paddingRight: theme.spacing(1)
  },
  highlight:
    theme.palette.type === 'light'
      ? {
          color: theme.palette.secondary.main,
          backgroundColor: lighten(theme.palette.secondary.light, 0.85)
        }
      : {
          color: theme.palette.text.primary,
          backgroundColor: theme.palette.secondary.dark
        },
  spacer: {
    flex: '1 1 100%'
  },
  actions: {
    color: theme.palette.text.secondary
  },
  title: {
    flex: '0 0 auto'
  }
}))

const EnhancedTableToolbar = props => {
  const classes = useToolbarStyles()
  const { numSelected } = props

  return (
    <Toolbar
      className={clsx(classes.root, {
        [classes.highlight]: numSelected > 0
      })}
    >
      <div className={classes.title}>
        {numSelected > 0 ? (
          <Typography color='inherit' variant='subtitle1'>
            {numSelected} selected
          </Typography>
        ) : (
          <Typography variant='h6' id='tableTitle'>
            Nutrition
          </Typography>
        )}
      </div>
      <div className={classes.spacer} />
      <div className={classes.actions}>
        {numSelected > 0 ? (
          <Tooltip title='Delete'>
            <IconButton aria-label='delete'>
              <DeleteIcon />
            </IconButton>
          </Tooltip>
        ) : (
          <Tooltip title='Filter list'>
            <IconButton aria-label='filter list'>
              <FilterListIcon />
            </IconButton>
          </Tooltip>
        )}
      </div>
    </Toolbar>
  )
}

EnhancedTableToolbar.propTypes = {
  numSelected: PropTypes.number.isRequired
}

const useStyles = makeStyles(theme => ({
  root: {
    width: '100%'
    // marginTop: theme.spacing(3)
  },
  paper: {
    width: '100%',
    marginBottom: theme.spacing(2)
  },
  table: {
    minWidth: 750
  },
  analystTable: {
    minWidth: 650
  },
  tableWrapper: {
    overflowX: 'auto'
  },
  visuallyHidden: {
    border: 0,
    clip: 'rect(0 0 0 0)',
    height: 1,
    margin: -1,
    overflow: 'hidden',
    padding: 0,
    position: 'absolute',
    top: 20,
    width: 1
  }
}))

export default function EnhancedTable (props) {
  const {
    headCells,
    children,
    handleRequestSort,
    handleSelectAllClick,
    order,
    orderBy,
    pagination,
    checkAll,
    sorter,
    size,
    indeterminate,
    checked,
    checkboxColor,
    analyst,
    allowCheck = true,
    allowCheckDisabled = false,
    selectStatus = false,
    noSearchIcon,
    fetchData,
    params,
    count,
    changeView
  } = props
  const classes = useStyles()

  const [newPage, setPage] = React.useState(pagination.page - 1)
  const [rowsPerPage, setRowsPerPage] = React.useState(pagination.rowsPerPage)

  const onChangeSearch = _.debounce((e) => {
    const filters = params ? params.filters : {}
    const value = {
      [e.target.name]: e.target.value
    }
    if (fetchData) {
      changeView({
        filters: { ...filters, ...value },
        page: 1,
      })
    }
  }, 800)

  const onSort = (name) => {
    const order = !params.order
    let nameTmp = name
    if (name.includes('|')) {
      const [field, type] = name.split('|')
      nameTmp = field
    }
    changeView({
      ...params,
      sort: `${nameTmp}|${order ? 'asc' : 'desc'}`,
      order: order,
      orderBy: nameTmp,
      page: 1
    })
  }


  const handleChangePage = (event, newPage) => {
    setPage(newPage - 1)
    changeView({ page: newPage, limit: rowsPerPage, filters: { ...params.filters }, 'search': params.search, 'accessors': params.accessors })
  }

  const handleChangeRowsPerPage = event => {
    setRowsPerPage(parseInt(event.target.value))
    changeView({ page: 1, limit: parseInt(event.target.value), filters: { ...params.filters }, 'search': params.search, 'accessors': params.accessors })
  }

  function TablePaginationActions (props) {
    const { onChangePage } = props

    const handleFirstPageButtonClick = event => {
      onChangePage(event, 1)
    }

    const handleBackButtonClick = event => {
      onChangePage(event, pagination.page - 1)
    }

    const handleNextButtonClick = event => {
      onChangePage(event, pagination.page + 1)
    }

    const handleLastPageButtonClick = event => {
      onChangePage(event, Math.max(0, Math.ceil(pagination.count / rowsPerPage)))
    }

    React.useEffect(() => {
      setPage(pagination.page - 1)
      setRowsPerPage(pagination.rowsPerPage)
    }, [pagination])

    return (
      <Box component='div' m={1} display='flex'>
        <IconButton onClick={handleFirstPageButtonClick} disabled={newPage === 0} aria-label='first page'>
          <FirstPageIcon />
        </IconButton>
        <IconButton onClick={handleBackButtonClick} disabled={newPage === 0} aria-label='previous page'>
          <KeyboardArrowLeft />
        </IconButton>
        <IconButton
          onClick={handleNextButtonClick}
          disabled={newPage >= Math.ceil(count / rowsPerPage) - 1}
          aria-label='next page'
        >
          <KeyboardArrowRight />
        </IconButton>
        <IconButton
          onClick={handleLastPageButtonClick}
          disabled={newPage >= Math.ceil(count / rowsPerPage) - 1}
          aria-label='last page'
        >
          <LastPageIcon />
        </IconButton>
      </Box>
    )
  }

  return (
    <div className={classes.root}>
      <Paper className={classes.paper}>
        {/* <EnhancedTableToolbar numSelected={selected.length} /> */}
        <div className={classes.tableWrapper}>
          <Table
            className={`${analyst ? classes.analystTable : classes.table}`}
            aria-labelledby='tableTitle'
            size={size}
          >
            <TableHead>
              <TableRow>
                {selectStatus
                  ? checkAll && (
                      <TableCell padding='checkbox'>
                        {allowCheck ? (
                          <Checkbox
                            disabled={allowCheckDisabled}
                            indeterminate={indeterminate}
                            checked={checked}
                            onChange={handleSelectAllClick}
                            color={checkboxColor}
                          />
                        ) : null}
                      </TableCell>
                    )
                  : null}

                {headCells.map(headCell => (
                  <TableCell
                    key={headCell.id}
                    align={headCell.center ? 'center' : headCell.numeric ? 'right' : 'left'}
                    padding={headCell.disablePadding ? 'none' : 'default'}
                    sortDirection={orderBy === headCell.id ? order : false}
                    width={headCell.width}
                  >
                    {sorter ? (
                      <TableSortLabel
                        active={orderBy === headCell.id}
                        direction={order}
                        onClick={event => onSort(headCell.sortAccessor)}
                      >
                        {headCell.label}
                        {orderBy === headCell.id ? (
                          <span className={classes.visuallyHidden}>
                            {order === 'desc' ? 'sorted descending' : 'sorted ascending'}
                          </span>
                        ) : null}
                      </TableSortLabel>
                    ) : (
                      headCell.label
                    )}
                    {headCell.label !== '' && headCell.sort ? (
                      <FontAwesomeIcon className={classes['sort-icon']} onClick={e => onSort(headCell.accessor)} icon={faSort} />
                    ) : (
                      ''
                    )}
                    {headCell.label !== '' && headCell.search ? <CustomSearchTextComponent name={headCell.accessor} width={headCell.width}  onChange={onChangeSearch} noIcon={noSearchIcon} /> : ''}
                  </TableCell>
                ))}
              </TableRow>
            </TableHead>
            {children}
            {pagination && (
              <TablePagination
                className={classes.pagination}
                rowsPerPageOptions={[5, 10, 20]}
                count={pagination.count}
                rowsPerPage={rowsPerPage}
                page={newPage}
                ActionsComponent={TablePaginationActions}
                onChangePage={handleChangePage}
                onChangeRowsPerPage={handleChangeRowsPerPage}
              />
            )}
          </Table>
        </div>
      </Paper>
      
    </div>
  )
}
