import React from 'react'
import { Grid, Typography, Avatar, GridList, Divider, GridListTile } from '@material-ui/core'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import images from 'Constant/images/images'
import { useHistory, Link } from 'react-router-dom'
// Components
import SummaryPage from './SummaryPage'
import TransactionDetailsPage from './TransactionDetailsPage'

// Services
import UserManagementServices from 'Services/Api/UserManagementServices 3'

import classes from './KPIPage.module.scss'

export default function KPIPage () {
  const history = useHistory()
  const [team, setTeam] = React.useState([])
  const [view, setView] = React.useState(false)
  const [name, setName] = React.useState('')
  const [filter, setFilter] = React.useState('this-week')
  React.useEffect(() => {
    BreadcrumbsService.setSelectedItems(['kpi'])
    UserManagementServices.getUsers(window.localStorage.getItem('role')).then(response => {
      console.log('USERS', response.data)
      setTeam(response.data)
    })
  }, [])
  const getDesignation = () => {
    const designation = window.localStorage
      .getItem('group')
      .toLocaleLowerCase().replace('_', ' ')
    return designation
      .toLowerCase()
      .split(' ')
      .map(word => word.charAt(0).toUpperCase() + word.slice(1))
      .join(' ')
  }

  const handleFilterChange = event => {
    setFilter(event.target.value)
  }

  const handleOnView = args => {
    const { firstName, lastName } = args
    setName(`${firstName} ${lastName}`)
    setView(true)
  }

  return (
    <div className={classes.root}>
      <Grid container spacing={6} direction='column'>
        <Grid item xs={12}>
          <Grid container className={classes.header}>
            <Grid item>
              <Typography variant='h5'>
                KPI
              </Typography>
              <Typography variant='h4'>
                {window.localStorage.getItem('username')}
              </Typography>
            </Grid>
          </Grid>
        </Grid>
        <Grid container item spacing={6} direction='row' style={{ marginLeft: '10px' }}>
          <div style={{ float: 'left' }}>
            <Grid container direction='row' item xs={2} style={{ padding: '20px' }}>
              <Grid item>
                <Grid item>
                  <Avatar style={{ marginLeft: '2%', height: '200px', width: '200px' }} alt='avatar' src={images.avatar} />
                </Grid>
                <Grid item>
                  <Typography variant='h6' style={{ marginTop: '12%', marginLeft: '2%' }}>
                    {window.localStorage.getItem('full_name')}
                  </Typography>
                  <Typography style={{ marginLeft: '2%', color: '#1C34FF' }}>
                    {getDesignation()}
                  </Typography>
                  <Typography style={{ marginTop: '15%', marginLeft: '2%', color: '#1C34FF' }}>
                    My Team
                  </Typography>
                  <Grid container direction='row'>
                    <GridList cellHeight={100} cols={3}>
                      {team && team.map((item, index) => (
                        <GridListTile style={{marginTop: '3%', paddingLeft: '2%'}} cols={1}>
                          <Link onClick={() => handleOnView({ firstName: item.first_name, lastName: item.last_name })}>
                            <Avatar alt='avatar' src={images.avatar} />
                            <Typography style={{ color: '#1C34FF' }}>
                              {item.first_name}
                            </Typography>
                          </Link>
                          
                        </GridListTile>
                      ))}
                    </GridList>
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
          </div>
          <Divider />
          <div style={{ position: 'absolute', borderRadius: '3pt', marginLeft: '18%' }}>
            {view ? (
              <TransactionDetailsPage
                name={name}
                setView={setView}
                images={images}
              />
            ) : (
              <SummaryPage
                handleFilterChange={handleFilterChange}
                filter={filter}
                team={team}
                images={images}
              />
            )}

          </div>
        </Grid>
      </Grid>
    </div>
  )
}
