import React from 'react'
import { NavLink } from 'react-router-dom'

// MATERIAL COMPONENTS
import { Grid, Avatar } from '@material-ui/core'

// STYLES
import style from '../AuditLog.module.scss'

export default function SideNav(props) {
  const { reportType, profilePic } = props

  // TODO: Use profile pic url of user
  return (
    <Grid container spacing={3}>
      <Grid item xs={12} >
        <Avatar className={style.avatar}>
          {profilePic ? <img alt='avatar' id="audit-log-profile-pic" src ={`data:image/png;base64,${profilePic}`} width="100%"/> : null}
        </Avatar>
      </Grid>
      <Grid item xs={12}>
        <Grid container className={style.sidenavLinks}>
          <Grid item xs={12}>
            <NavLink to="/audit-log/activity" isActive={() => reportType === 'activity'} activeClassName={style.sidenavLinkActive} className={`${style.sidenavLink}`}>
              Activity Logs
            </NavLink>
          </Grid>
          <Grid item xs={12}>
            <NavLink to="/audit-log/audit" isActive={() => reportType === 'audit'} activeClassName={style.sidenavLinkActive} className={`${style.sidenavLink}`}>
              Audit Logs
          </NavLink>
          </Grid>
        </Grid>
      </Grid>
    </Grid>
  )
}
