import React from 'react'
import { Link, useHistory } from 'react-router-dom'
import _ from 'lodash'
import moment from 'moment'

import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'

import IconButton from '@material-ui/core/IconButton'
import Typography from '@material-ui/core/Typography'
import CloseIcon from '@material-ui/icons/Close'
import ViewListRoundedIcon from '@material-ui/icons/ViewListRounded'

import { withStyles, makeStyles } from '@material-ui/core/styles'
import TableCell from '@material-ui/core/TableCell'
import TableBody from '@material-ui/core/TableBody'
import TableRow from '@material-ui/core/TableRow'
import { Popper, TextField, Fade, Button, Box } from '@material-ui/core'
import Grid from '@material-ui/core/Grid'

import { CurrencyFormatter, DateFormatterByAbbreviation } from 'Utilities/TextFormatter'
import SearchTextComponent from 'SharedComponents/SearchTextComponent/SearchTextComponent'
import CustomEnhancedTable from '../CustomEnhancedTable/CustomEnhancedTable'
import DisplaySettings from '../../Components/FormComponents/DisplaySettings/DisplaySettings'
import CircularLoading from '../../../../../SharedComponents/CircularLoading/CircularLoading'
import ViewMore from '../ViewMore/UpdatedAccountDetailsModal'
import { GrabByString } from 'Utilities/GrabObject'
import classes from './ViewAll.module.scss'

// Services
import ExportsServices from 'Services/Api/ExportsServices'

// Security
import RBAC from 'Security/RBAC'

export default function CustomizedDialogs (props) {
  const {
    config,
    tile,
    type,
    title,
    contents,
    count,
    show,
    list,
    fetchData,
    params,
    accessors,
    setShow,
    isFetching,
    placeholder,
    statuses,
    displaySettingTitle='Effectivity Date Range *',
    startDateError='Please enter a valid effectivity start date',
    endDateError='Please enter a valid effectivity end date',
    laterStartDateError='Please select an effectivity start date that is not later than the current date',
    laterEndDateError='Please select an effectivity end date that is not later than the current date'
  } = props

  const history = useHistory()

  const StyledTableCell = withStyles(theme => ({
    head: {
      backgroundColor: theme.palette.common.grey,
      color: theme.palette.common.white
    },
    body: {
      fontSize: 14
    }
  }))(TableCell)

  const StyledTableRow = withStyles(theme => ({
    root: {
      '&:nth-of-type(odd)': {
        backgroundColor: theme.palette.action.hover
      }
    }
  }))(TableRow)

  const tableHeaders = config[tile].headers
  const tableColumnSequence = config[tile].sequence
  const [pagination, setPagination] = React.useState({
    count: list ? list.count : 0,
    rowsPerPage: list ? list.limit : 0,
    page: list ? list.page : 0
  })
  const [viewMore, setViewMore] = React.useState({
    show: false
  })

  React.useEffect(() => {
    setPagination({
      count: list ? list.count : 0,
      rowsPerPage: list ? list.limit : 0,
      page: list ? list.page : 0
    })
  }, [list])
  // const handleOnClose = () => {
  //   setViewAll(false)
  //   history.push('/')
  // }

  const handleOnClose = () => {
    setShow(false)
  }

  const changeView = params => {
    if (fetchData) {
      fetchData(params)
    }
  }

  const handleDisplaySettingsApplyClicked = range => {
    const params = {
      range: {
        start: range.start,
        end: range.end
      }
    }
    fetchData(params)
  }

  const handleDisplaySettingsClicked = params => {
    console.log(params)
  }

  const handleSearchChanged = search => {
    const params = {
      search: search,
      accessors: accessors,
      page: 1
    }
    console.log(params)
    fetchData(params)
  }

  const mainSearchChange = _.debounce(e => {
    const params = {
      search: e.target.value,
      accessors: accessors,
      page: 1
    }
    changeView(params)
  }, 300)

  const typeTile = type => {
    let tile
    if (type === 'Client Pre-SOAs To Analyze' || type === 'Pending Pre-SOAs' || type === 'Client Pre-SOAs To Validate' || type === 'Approved Pre-SOAs' || type === 'Pre-SOAs Submitted for Approval' || type === 'Validated Pre-SOAs') {
      tile = 'CLIENT_SOA'
    }
    if (type === 'Member Pre-SOAs To Analyze' || type === 'Member Invoices To Validate') {
      tile = 'MEMBER_SOA'
    }
    return tile
  }

  const downloadServices = async args => {
    const { type, contents } = args
    console.log(contents)
    const list = []

    if (type === 'Member Pre-SOAs To Analyze' || type === 'Member Pre-SOAs to Validate') {
      for (const i of contents) {
        await list.push({
          field_1: i.account_name,
          field_2: i.first_name + ' ' + i.last_name,
          field_3: i.assigned_analyst,
          field_4: DateFormatterByAbbreviation(i.date.created)
        })
      }
    }
    if (type === 'Validated Pre-SOAs') {
      for (const i of contents) {
        await list.push({
          field_1: i.account_number,
          field_2: i.account_name,
          field_3: i.soa_number,
          field_4: i.billing_cycle_code + ' ' + i.billing_cycle,
          field_5: DateFormatterByAbbreviation(i.date.created)
        })
      }
    } if (type === 'Client Pre-SOAs To Analyze' || type === 'Pending Pre-SOAs' || type === 'Client Pre-SOAs To Validate' || type === 'Approved Pre-SOAs' || type === 'Pre-SOAs Submitted for Approval') {
      for (const i of contents) {
        await list.push({
          field_1: i.account_number,
          field_2: i.account_name,
          field_3: i.billing_cycle_code + ' ' + i.billing_cycle,
          field_4: DateFormatterByAbbreviation(i.due_date)
        })
      }
    }
    console.log(list)
    ExportsServices.getClientSOAExports(list, {tile: type, type: typeTile(type)}).then(response => {
      const url = URL.createObjectURL(new Blob([response.data]))
      const link = document.createElement('a')
      link.href = url
      link.setAttribute('download', `${type}.xlsx`)
      document.body.appendChild(link)
      link.click()
    })
  }

  const showUpdatedInformation = (data) => {
    setViewMore({
      show: true,
      title: data.account_name,
      subtitle: 'ACCOUNT NUMBER. ' + data.account_number,
      data: data
    })
  }

  return (
    <Dialog
      fullWidth
      maxWidth='lg'
      onClose={handleOnClose}
      aria-labelledby='customized-dialog-title'
      open={show}
      className={classes.root}
      PaperProps={{ style: { height: '100%', display: 'flex', backgroundColor: '#f4f6f8' } }}
    >
      <DialogTitle disableTypography className={classes['header-container']}>
        <div className={classes.header}>
          <Typography id='modal-title' variant='h6' className={classes.title}>
            {title}
          </Typography>
          <IconButton
            id='modal-close-button'
            aria-label='close'
            className={classes['close-button']}
            onClick={handleOnClose}
          >
            <CloseIcon />
          </IconButton>
          <div className={classes.clear} />
        </div>
      </DialogTitle>
      <DialogContent className={classes['content-container']}>
        <div className={classes['search-container']}>
          <DisplaySettings
            placeholder={placeholder}
            displaySettings
            displaySettingTitle={displaySettingTitle}
            startDateError={startDateError}
            endDateError={endDateError}
            laterStartDateError={laterStartDateError}
            laterEndDateError={laterEndDateError}
            handleOnDisplaySettingsClicked={handleDisplaySettingsClicked}
            handleOnDisplaySettingsApplyClicked={handleDisplaySettingsApplyClicked}
            onChange={handleSearchChanged}
          />
          {RBAC.hasPolicy({or: ['BC-066', 'BC-011']}) && (
            <Link
              id='exports-link'
              className={classes['exports']}
              onClick={() => downloadServices({ type: type, contents: contents })}
              color='primary'
              size='medium'
              style={{marginLeft: '0'}}
            >
              <Grid container id='exports-icon'>
                <Box style={{display: 'flex'}}>
                  <ViewListRoundedIcon style={{color: 'rgb(58, 183, 125)'}} className={classes['exports-icon']} />
                  <Typography style={{color: 'rgb(58, 183, 125)'}} className='text-link underline'>Export</Typography>
                </Box>
              </Grid>
            </Link>
          )}

        </div>
        {/* For Pre Soa to Analyze Modal */}
        { statuses
          ? <div className={classes.count}>
            <Grid container spacing={2} direction='row'>
              <Grid item xs={12}>
                <Grid container justify='center' spacing={10}>
                  <Grid item className={classes.grids}>
                    {type === 'Client Pre-SOAs To Analyze' ? (
                      <React.Fragment>
                        <Typography variant='h4' align='center' className={classes.name} style={{fontWeight: 'bold'}}>
                        {statuses.toAnalyze}
                      </Typography>
                        <Typography variant='h6'>TO ANALYZE</Typography>
                      </React.Fragment>
                    ) : (
                      <React.Fragment>
                        <Typography variant='h4' align='center' className={classes.name} style={{fontWeight: 'bold'}}>
                          {statuses.forApproval}
                        </Typography>
                        <Typography variant='h6'>TO VALIDATE</Typography>
                      </React.Fragment>
                    )}
                  </Grid>
                  <Grid item className={classes.grids}>
                    <Typography variant='h4' align='center' className={classes.name} style={{fontWeight: 'bold'}}>
                      {statuses.approved}
                    </Typography>
                    <Typography variant='h6' align='center'>
                    APPROVED
                    </Typography>
                  </Grid>
                  <Grid item className={classes.grids}>
                    <Typography variant='h4' align='center' className={classes.name} style={{fontWeight: 'bold'}}>
                      {statuses.disapproved}
                    </Typography>
                    <Typography variant='h6' align='center'>
                    DISAPPROVED
                    </Typography>
                    <Typography className={classes.smtext} align='center'>
                    (FOR REVISION)
                    </Typography>
                  </Grid>
                  <Grid item className={classes.grids}>
                    <Typography variant='h4' align='center' className={classes.name} style={{fontWeight: 'bold'}}>
                      {statuses.void}
                    </Typography>
                    <Typography variant='h6' align='center'>
                    VOID
                    </Typography>
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
          </div> : null}

        <CustomEnhancedTable
          count={count}
          changeView={changeView}
          tableHeaders={tableHeaders}
          pagination={pagination}
          search
          sort
          noSearchIcon
          fetchData={fetchData}
          params={params}
        >
          <TableBody>
            {isFetching && (
              <StyledTableRow>
                <StyledTableCell component='th' colspan={10}>
                  <CircularLoading />
                </StyledTableCell>
              </StyledTableRow>
            )}
            {!isFetching &&
              contents &&
              contents.map((row, contentsIndex) => (
                <StyledTableRow key={contentsIndex}>
                  <StyledTableCell component='th'>
                    <Typography variant='caption' align='left'>
                      {type === 'Client Pre-SOAs To Analyze' ||
                      type === 'Pre-SOAs Submitted for Approval' ||
                      type === 'Client Pre-SOAs To Validate' ||
                      type === 'Validated Pre-SOAs' ||
                      type === 'Approved Pre-SOAs' || type === 'Pending Pre-SOAs'
                        ? row.account_number
                        : ''}
                      {type === 'Member Pre-SOAs To Analyze' || type === 'Clients With Updated Account Information' 
                        ? row.account_name : ''}
                    </Typography>
                  </StyledTableCell>
                  <StyledTableCell component='th'>
                    <Typography variant='caption' align='left'>
                      {type === 'Client Pre-SOAs To Analyze' ||
                      type === 'Pre-SOAs Submitted for Approval' ||
                      type === 'Client Pre-SOAs To Validate' ||
                      type === 'Validated Pre-SOAs' ||
                      type === 'Approved Pre-SOAs' || type === 'Pending Pre-SOAs'
                        ? row.account_name
                        : null}
                      {/* TODO: member name for Pending Pre-SOAs is undefined */}
                      {type === 'Member Pre-SOAs To Analyze' || (type === 'Pending Pre-SOAs' && row.first_name)
                        ? `${row.first_name + ' ' + row.last_name}`
                        : ''}
                      {type === 'Clients With Updated Account Information' ? row.account_number : ''}
                    </Typography>
                  </StyledTableCell>
                  <StyledTableCell component='th' align='left'>
                    <Typography variant='caption' align='left'>
                      {type === 'Client Pre-SOAs To Analyze' ||
                      type === 'Pre-SOAs Submitted for Approval' ||
                      type === 'Client Pre-SOAs To Validate' ||
                      type === 'Approved Pre-SOAs' || type === 'Pending Pre-SOAs'
                        ? row.billing_cycle_code + ' ' + row.billing_cycle
                        : ''}
                      {type === 'Member Pre-SOAs To Analyze' ? row.assigned_analyst : ''}
                      {type === 'Validated Pre-SOAs' ? row.soa_number : ''}
                      {type === 'Clients With Updated Account Information' ? row.sending_module : ''}
                    </Typography>
                  </StyledTableCell>
                  <StyledTableCell component='th'>
                    <Typography variant='caption' align='left'>
                      {type === 'Client Pre-SOAs To Analyze' ||
                      type === 'Pre-SOAs Submitted for Approval' ||
                      type === 'Client Pre-SOAs To Validate'
                        ? DateFormatterByAbbreviation(row.due_date)
                        : null}
                      {type === 'Validated Pre-SOAs' ? row.billing_cycle_code_and_description : ''}
                      {type === 'Member Pre-SOAs To Analyze' ? DateFormatterByAbbreviation(row.date.created) : null}
                      {/* TODO: this should be date.submitted, API is not setting the date submitted */}
                      {type === 'Pending Pre-SOAs' ? DateFormatterByAbbreviation(row.date.created) : null}
                      {/* TODO: this should be date.approved, API is not setting the date approved */}
                      {type === 'Approved Pre-SOAs' ? DateFormatterByAbbreviation(row.date.created) : null}
                      {type === 'Clients With Updated Account Information' ? row.category : ''}
                    </Typography>
                  </StyledTableCell>

                  {type === 'Member Pre-SOAs To Analyze' ? (
                    <StyledTableCell component='th' align='left'>
                      <Typography variant='caption' align='left'>
                        {/* {row.case_number} */}
                      </Typography>
                    </StyledTableCell>
                  ) : null}

                  {type === 'Pending Pre-SOAs' ? (
                    <StyledTableCell component='th' align='left'>
                      <Typography variant='caption' align='left'>
                        {CurrencyFormatter(row.summary.sub_total + row.summary.tax_amount)}
                      </Typography>
                    </StyledTableCell>
                  ) : null}

                  {type === 'Validated Pre-SOAs' ? (
                    <StyledTableCell component='th' align='left'>
                      <Typography variant='caption' align='left'>
                        {/* TODO: this should be date.approved, API is not setting the date approved */}
                        {DateFormatterByAbbreviation(row.date.created)}
                      </Typography>
                    </StyledTableCell>
                  ) : null}

                  {type === 'Clients With Updated Account Information' ? (
                    <StyledTableCell component='th' align='left'>
                      <Typography variant='caption' align='left'>
                        {/* TODO: this should be date.approved, API is not setting the date approved */}
                        {DateFormatterByAbbreviation(row.date_received)}
                      </Typography>
                    </StyledTableCell>
                  ) : null}

                  <StyledTableCell component='th' align='left'>
                    {type === 'Approved Pre-SOAs' && (
                      <Link to={`/billing/ledgers/${row.soa_number}/${'analyst-approved'}`}>
                        <Typography variant='caption' className='text-link underline'>
                          View
                        </Typography>
                      </Link>
                    )}
                    {type === 'Client Pre-SOAs To Analyze' && (
                      <Link to={`/billing/ledgers/${row.soa_number}/${'analyst'}`}>
                        <Typography variant='caption' className='text-link underline'>
                          Analyze
                        </Typography>
                      </Link>
                    )}
                    {(type === 'Pre-SOAs Submitted for Approval' || type === 'Pending Pre-SOAs') && (
                      <Link to={`/billing/ledgers/${row.soa_number}/${'analyst'}`}>
                        <Typography variant='caption' className='text-link underline'>
                          View
                        </Typography>
                      </Link>
                    )}
                    {type === 'Client Pre-SOAs To Validate' && (
                      <Link to={`/billing/ledgers/${row.soa_number}/${'supervisor'}`}>
                        <Typography variant='caption' className='text-link underline'>
                          Validate
                        </Typography>
                      </Link>
                    )}

                    {type === 'Member Pre-SOAs To Analyze' && (
                      <Link to={`/billing/soa/member/${row.soa_number}/${'analyst'}`}>
                        <Typography variant='caption' className='text-link underline'>
                          Analyze
                        </Typography>
                      </Link>
                    )}
                    {type === 'Member Pre-SOAs To Validate' && (
                      <Link to={`/billing/soa/member/${row.soa_number}/${'analyst'}`}>
                        <Typography variant='caption' className='text-link underline'>
                          Analyze
                        </Typography>
                      </Link>
                    )}
                    {type === 'Validated Pre-SOAs' && (
                      <Link to={`/billing/ledgers-validated/${row.soa_number}/${'validated'}`}>
                        <Typography variant='caption' className='text-link underline'>
                          View
                        </Typography>
                      </Link>
                    )}
                    {type === 'Clients With Updated Account Information' && (
                      <Link onClick={() => showUpdatedInformation(row)}>
                        <Typography variant='caption' className='text-link underline'>
                          View
                        </Typography>
                      </Link>
                    )}
                  </StyledTableCell>
                </StyledTableRow>
              ))}
              <div>
                {/*<ViewMore viewMore={viewMore} setViewMore={setViewMore} />*/}
                <ViewMore viewMore={viewMore} setViewMore={setViewMore} fetchData={fetchData} params={params} changeViewForViewMore={fetchData} />
              </div>
          </TableBody>
        </CustomEnhancedTable>
      </DialogContent>
    </Dialog>
  )
}
