import React from 'react'
import { Document, Page, pdfjs, PDFFindController, PDFViewer } from 'react-pdf'
import { IconButton, Tooltip, Typography, Button, InputBase, Paper } from '@material-ui/core'
import SearchIcon from '@material-ui/icons/Search'
import MaximizeIcon from '@material-ui/icons/Maximize'
import HighlightOffIcon from '@material-ui/icons/HighlightOff'
import ReplyRoundedIcon from '@material-ui/icons/ReplyRounded'
import ZoomInIcon from '@material-ui/icons/ZoomIn'
import ZoomOutIcon from '@material-ui/icons/ZoomOut'
import ZoomOutMapIcon from '@material-ui/icons/ZoomOutMap'
import UnfoldMoreIcon from '@material-ui/icons/UnfoldMore'
import PrintIcon from '@material-ui/icons/Print'
import SaveIcon from '@material-ui/icons/Save'
import TurnedInNotIcon from '@material-ui/icons/TurnedInNot'
import Fullscreen from './Fullscreen'
import showReport from './showReport'
import { useReactToPrint } from 'react-to-print'
import BillingSOAReportService from 'Services/Api/Billing/SOA/BillingSOAReportService'
import { Link } from 'react-router-dom'

// import file from './INTELLICARE SPECIMEN CONTRACT_2017.pdf'
// Styles
import classes from './PdfViewer.module.scss'

// Security
import RBAC from 'Security/RBAC'

pdfjs.GlobalWorkerOptions.workerSrc = `https://cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`

export default function PdfViewer (props) {
  const {
    rotate,
    file,
    fileName,
    initialPageNumber = 1,
    initialScale = 1.3,
    download = () => {},
    // print = () => {},
    soaNumber,
    options,
    type
  } = props
  const [scale, setScale] = React.useState(initialScale)
  const [pageNumber, setPageNumber] = React.useState(initialPageNumber)
  const [numPages, setNumPages] = React.useState(0)
  const componentRef = React.useRef()
  const [isShowReport, setIsShowReport] = React.useState(false)
  const [option, setOption] = React.useState(false)
  const [selectedClaimReport, setSelectedSoaReport] = React.useState(null)
  const [search, setSearch] = React.useState(false)
  const [searchText, setSearchText] = React.useState('')
  const onClickEvent = scale => {
    if (scale === 1.3) {
      setScale(4)
    }
    if (scale < 1.3) {
      setScale(4)
    }
    if (scale > 1.3) {
      setScale(1.3)
    }
  }

  function handleClickSubmitted () {
    showReport({
      soaNumber: soaNumber,
      options: options,
      setSelectedSoaReport,
      setIsShowReport,
      setOption
    })
  }

  function closePDFView () {
    setIsShowReport(false)
  }

  const handlePrint = useReactToPrint({
    content: () => componentRef.current
  })

  const handlePrintBIR = () => {
    BillingSOAReportService.getReport({ type: type, soaNumber: soaNumber }).then(response => {
      const url = URL.createObjectURL(new Blob([response.data]))
      const link = document.createElement('a')
      link.href = url
      link.setAttribute('download', `soa-bir-${soaNumber}.pdf`)
      document.body.appendChild(link)
      link.click()
    })
  }

  const printEvent = () => {
    if (type === 'preview') {
      handlePrintBIR()
    } else {
      handlePrint()
    }
  }

  const handOnSearch = () => {
    setSearchText('')
    setSearch(true)
  }
  const handleOnSearchOnChange = e => {
    const searchBarText = e.target.value
    // const query = new RegExp('(\\b' + searchBarText + '\\b)', 'gim')
    setSearchText(searchBarText)
  }

  const highlightPattern = (text, pattern) => {
    const splitText = text.split(pattern)

    if (splitText.length <= 1) {
      return text
    }
    const matches = text.match(pattern)
    return splitText.reduce((arr, element, index) => (matches[index] ? [
      ...arr,
      element,
      <mark style={{ marginLeft: '2px', fontSize: '14px' }}>
        {matches[index]}
      </mark>
    ] : [...arr, element]), [])
  }

  const makeTextRenderer = searchText => textItem => highlightPattern(textItem.str, searchText)

  return (
    <div className={classes.root}>
      <div className={classes.pdf} id={'document'}>
        <Document
          ref={componentRef}
          file={file}
          onLoadSuccess={document => {
            setNumPages(document.numPages)
          }}
        >
          <Page
            pageNumber={pageNumber}
            customTextRenderer={makeTextRenderer(searchText)}
            scale={scale}
            rotate={rotate} />
        </Document>
      </div>
      <div className={classes.controls}>
        <div className={classes['left-pane']}>
          <Typography className={classes['file-name']} align='left'>
            {fileName}
          </Typography>
          <Tooltip title='Search' className={classes['control-left']}>
            <IconButton onClick={() => handOnSearch()}>
              <SearchIcon style={{ transform: 'scaleX(-1)', color: 'white' }} />
            </IconButton>
          </Tooltip>
          {search ? (
            <Tooltip title='Search' className={classes['search-span']}>
              <Paper style={{ display: 'flex' }}>
                <div style={{ float: 'left' }}>
                  <InputBase placeholder='Search Keyword...' onChange={e => handleOnSearchOnChange(e)} style={{ borderBottom: 'none', padding: '10px' }} />
                </div>
                <div style={{ marginLeft: 'auto' }}>
                  <Link>
                    <Typography>
                      <IconButton onClick={() => setSearch(false)}>
                        <HighlightOffIcon style={{ color: 'black' }} />
                      </IconButton>
                    </Typography>
                  </Link>
                </div>
              </Paper>
            </Tooltip>
          ) : ('')}
          <Tooltip title='Previous Page' className={classes.control}>
            <IconButton onClick={() => setPageNumber(pageNumber > 1 ? pageNumber - 1 : pageNumber)}>
              <ReplyRoundedIcon style={{ color: 'white', transform: 'rotate(270deg) scaleX(-1)' }} />
            </IconButton>
          </Tooltip>
          <Tooltip title='' className={classes.bar}>
            <MaximizeIcon style={{ fontSize: '24px', paddingTop: '-10px', color: 'gray', transform: 'rotate(-90deg)' }} />
          </Tooltip>
          <Tooltip title='Next Page' className={classes.back}>
            <IconButton onClick={() => setPageNumber(pageNumber < numPages ? pageNumber + 1 : pageNumber)}>
              <ReplyRoundedIcon style={{ color: 'white', transform: 'rotate(-270deg) scaleX(-1)' }} />
            </IconButton>
          </Tooltip>
        </div>
        <div className={classes['page-number']}>
          <Typography>
            {pageNumber} of {numPages}
          </Typography>
        </div>
        <div className={classes['middle']}>
          <Tooltip title='Zoom in' className={classes.zoom}>
            <IconButton onClick={() => setScale(scale + 0.1)}>
              <ZoomInIcon style={{ transform: 'scale(1.2)', color: 'white' }} />
            </IconButton>
          </Tooltip>
          <Tooltip title='' className={classes.bar}>
            <MaximizeIcon style={{ fontSize: '24px', paddingTop: '-10px', color: 'gray', transform: 'rotate(-90deg)' }} />
          </Tooltip>
          <Tooltip title='Zoom out' className={classes.zoomout}>
            <IconButton onClick={() => (scale === 0.6 ? '' : setScale(scale - 0.1))}>
              <ZoomOutIcon style={{ transform: 'scale(1.2)', color: 'white' }} />
            </IconButton>
          </Tooltip>
          <Tooltip title='Automatic Zoom' className={classes.autozoom}>
            <Button style={{ backgroundColor: '#4f4f4f' }} onClick={() => onClickEvent(scale)}>
              Automatic Zoom
              <UnfoldMoreIcon style={{ color: 'white' }} />
            </Button>
          </Tooltip>
        </div>
        <div className={classes['far-right']}>

          <Tooltip title='Fullscreen' className={classes.fullscreen}>
            <IconButton onClick={handleClickSubmitted}>
              <ZoomOutMapIcon style={{ color: 'white' }} />
            </IconButton>
          </Tooltip>

          {RBAC.hasPolicy({or: ['BC-012', 'BC-015', 'BC-067']}) && (
            <Tooltip title='Print' className={classes.control}>
              <IconButton onClick={() => printEvent()}>
                <PrintIcon style={{ color: 'white' }} />
              </IconButton>
            </Tooltip>
          )}

          <Tooltip title='Download' className={classes.control}>
            <IconButton onClick={download}>
              <SaveIcon style={{ color: 'white' }} />
            </IconButton>
          </Tooltip>

          <Tooltip title='In Not' className={classes.control}>
            <IconButton onClick={download}>
              <TurnedInNotIcon style={{ color: 'white' }} />
            </IconButton>
          </Tooltip>

        </div>
      </div>

      {isShowReport ? (
        <Fullscreen
          soaNumber={soaNumber}
          downloadFullscreen={download}
          open={isShowReport}
          close={() => closePDFView()}
          claimNo={selectedClaimReport}
          options={options}
          type={type}
        />
      ) : (
        ''
      )}
    </div>
  )
}
