import React from 'react'
import moment from 'moment'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import IconButton from '@material-ui/core/IconButton'

import classes from './TreeViewTable.module.scss'
import { Link } from 'react-router-dom'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import { CurrencyFormatter, DateFormatterByAbbreviation } from 'Utilities/TextFormatter'

export default function CollapsibleRow (props) {
  const {
    index,
    id,
    data,
    objectKey,
    tableColumnOrder,
    subColumnOrder,
    offset,
    collapseAllState,
    collapseFunction,
    actionText,
    actionTo,
    param,
    subActionTo,
    subParam,
    subActionText,
    monetary_columns = [],
    emphasis_columns = [],
    multipleActions,
    multipleActionsInactive,
    multipleActionsActive,
    actionAccessor,
    editAccessor,
    coloredRow,
    showLine
  } = props

  const [collapse, setCollapse] = React.useState(true)
  const [iconImage, setIconImage] = React.useState(null)
  const add_icon = 'plus-square'
  const remove_icon = 'minus-square'

  React.useEffect(() => {
    handleTableCollapseAll()
  }, [collapseAllState])

  const handleTableCollapse = () => {
    setCollapse(!collapse)
    if (collapse) {
      setIconImage(add_icon)
    } else {
      setIconImage(remove_icon)
    }
  }

  const handleTableCollapseAll = () => {
    setCollapse(collapseAllState)

    if (collapseAllState) {
      setIconImage(remove_icon)
    } else {
      setIconImage(add_icon)
    }

    collapseFunction(collapse)
  }

  const offsetCells = []
  for (var i = 0; i < offset; i++) {
    offsetCells.push(<TableCell colspan='1' />)
  }

  return (
    <React.Fragment>
      <TableRow key={index}>
        <TableCell id={`button-${id}`} className={[classes.rowCollapseButton, editAccessor && data[editAccessor] && data[editAccessor] === coloredRow ? classes.editRow : '']}>
          <IconButton onClick={handleTableCollapse} className={classes.rowCollapseIcon}>
            <FontAwesomeIcon icon={iconImage} />
          </IconButton>
        </TableCell>
        {tableColumnOrder.map((rowItem, rowIndex) => (
          <TableCell
            id={rowIndex}
            className={[monetary_columns.includes(rowItem) ? classes.contentCellMonetary : classes.contentCell, editAccessor && data[editAccessor] && data[editAccessor] === coloredRow ? classes.editRow : '']}
            // align={monetary_columns.includes(rowItem) ? 'right' : 'inherit'}
            style={
              emphasis_columns.includes(rowItem) === true ? { 'font-weight': 'bold' } : { 'font-weight': 'normal' }
            }
          >
            {monetary_columns.includes(rowItem) === true
              ? 'Php ' + CurrencyFormatter(data[rowItem])
              : moment(data[rowItem], moment.ISO_8601).isValid()
              ? DateFormatterByAbbreviation(data[rowItem])
              : data[rowItem]}
          </TableCell>
        ))}
        {actionTo != null && (
          <TableCell className={[classes.contentCell, editAccessor && data[editAccessor] && data[editAccessor] === coloredRow ? classes.editRow : '']}>
            <Link
              id={`link-table-${index}`}
              to={param == null ? actionTo : actionTo.concat('/', data[param])}
              className={classes.link}
            >
              {actionText}
            </Link>
          </TableCell>
        )}
        {multipleActions ? (
          <TableCell className={[classes.contentCell, editAccessor && data[editAccessor] && data[editAccessor] === coloredRow ? classes.editRow : '']}>
            {multipleActions.map((action, iteration) => (
              <Link
                id
                onClick={() => action.handleClick(data, data[editAccessor])}
                className={classes.link}
                style={{ textDecoration: 'underline', marginRight: '10px' }}
              >
                {action.label}
              </Link>
            ))}
          </TableCell>
        ) : null}
      </TableRow>
      {/* <Collapse in={collapse.show} className={classes.collapse}> */}
      {collapse &&
        data[objectKey].map((subItem, subIndex) => (
          <TableRow key={index + '-' + subIndex}>
            {subColumnOrder.map((colItem, colindex) =>
              colItem === '' && colindex === 0 && showLine ? (
                <TableCell className={[classes.contentCell, editAccessor && subItem[editAccessor] && subItem[editAccessor] === coloredRow ? classes.editRow : '']}>
                  <div className={subIndex === data[objectKey].length-1 ? classes.corner : classes.vertical}>&nbsp;</div> 
                </TableCell>
              ) : (
                <TableCell
                  className={[monetary_columns.includes(colItem) ? classes.contentCellMonetary : classes.contentCell, editAccessor && subItem[editAccessor] && subItem[editAccessor] === coloredRow ? classes.editRow : '']}
                  // align={monetary_columns.includes(colItem) ? 'right' : 'inherit'}
                >
                  {monetary_columns.includes(colItem) === true
                    ? 'Php ' + CurrencyFormatter(subItem[colItem])
                    : moment(subItem[colItem], moment.ISO_8601).isValid()
                    ? DateFormatterByAbbreviation(subItem[colItem])
                    : subItem[colItem]}
                </TableCell>
              )
            )}
            {actionTo != null && (
              <TableCell className={[classes.contentCell, editAccessor && subItem[editAccessor] && subItem[editAccessor] === coloredRow ? classes.editRow : '']}>
                <Link
                  color='primary'
                  id={`link-table-sub-${index}`}
                  to={subParam == null ? subActionTo : subActionTo.concat('/', subItem[subParam])}
                  className={classes.link}
                >
                  {subActionText}
                </Link>
              </TableCell>
            )}
            {multipleActions ? (
              <TableCell className={[classes.contentCell, editAccessor && subItem[editAccessor] && subItem[editAccessor] === coloredRow ? classes.editRow : '']}>
                {subItem.inactive && multipleActionsInactive.map((action, iteration) => (
                  <Link
                    id
                    onClick={() => action.handleClick(subItem, subItem[editAccessor])}
                    className={classes.link}
                    style={{ textDecoration: 'underline', marginRight: '10px' }}
                  >
                    {action.label}
                  </Link>
                ))}
                {!subItem.inactive && multipleActionsActive.map((action, iteration) => (
                  <Link
                    id
                    onClick={() => action.handleClick(subItem, subItem[editAccessor])}
                    className={classes.link}
                    style={{ textDecoration: 'underline', marginRight: '10px' }}
                  >
                    {action.label}
                  </Link>
                ))}
              </TableCell>
            ) : null}
          </TableRow>
        ))}
      {/* </Collapse> */}
    </React.Fragment>
  )
}
