import React from 'react'
import { useHistory } from 'react-router-dom'
import Button from '@material-ui/core/Button'
import Typography from '@material-ui/core/Typography'

// Components
import Void from '../Void/Void'
import Disapprove from 'StandardComponents/Disapprove/Disapprove'

// styles
import classes from './ContentPageComponent.module.scss'

export default function ContentPage (props) {
  const { shift, soaPage, getStepContent, activeStep, nextStep, stepBack, content, voided, disapproved, setActiveStep } = props
  const history = useHistory()
  //void
  const [openDialog, setOpenDialog] = React.useState(false)
  //disapprove
  const [openDisapproveDialog, setOpenDisapproveDialog] = React.useState(false)

  const handleVoid = () => {
    setOpenDialog(true)
  }
  const handleDisapprove = () => {
    setOpenDisapproveDialog(true)
  }

  const handleSave = () => {
    history.push('/')
  }
  return (
    <div className={`${classes.root} ${shift ? classes.contentShift : ''}`}>
      {soaPage ? (
        <div>
          <div>
            {activeStep === 4 ? (
              <Typography className={classes.instructions}>{getStepContent({ stepIndex: 3, activeStep })}</Typography>
            ) : (
              <Typography className={classes.instructions}>{getStepContent({ stepIndex: activeStep })}</Typography>
            )}
            <div className={classes.button}>
              <Button
                variant='contained'
                id={`${activeStep}-prev-button`}
                onClick={stepBack}
                className={classes['button-negative']}
              >
                {activeStep === 0 ? 'Cancel' : 'Back'}
              </Button>
              <div className={classes['item-right-side']}>
                
                {activeStep === 3 || activeStep === 4 ? (
                <>
                  <Button
                      variant='contained'
                      id={'void-button'}
                      onClick={handleVoid}
                      className={classes['button-void']}
                    >
                    Void
                  </Button>
                  &emsp;&emsp;
                  <Button
                      variant='contained'
                      id={'disapprove-button'}
                      onClick={handleDisapprove}
                      className={classes['button-disapprove']}
                  >
                    Disapprove
                  </Button>
                  &emsp;&emsp;
                  <Button
                    variant='contained'
                    id={`${activeStep}-next-button`}
                    className={classes['button-positive']}
                    onClick={nextStep}
                  >
                    Approve
                  </Button>
                </>
                ) : (
                <>
                  <Button
                      variant='contained'
                      id={'save-button'}
                      onClick={handleSave}
                      className={classes['button-positive']}
                  >
                    Save
                  </Button>
                  &emsp;&emsp;
                  <Button
                    variant='contained'
                    id={`${activeStep}-next-button`}
                    className={classes['button-positive']}
                    onClick={nextStep}
                  >
                    Next
                  </Button>
                </>
                )}
              </div>
            </div>
          </div>
        </div>
      ) : content != null ? (
        content
      ) : (
        'Test'
      )}
      {openDialog === true ? (
        <Void
          page={'soa'}
          openDialog={openDialog}
          setOpenDialog={setOpenDialog}
          setActiveStep={setActiveStep}
          submitFunction={voided}
        />
      ) : (
        ''
      )}
      {openDisapproveDialog === true ? (
        <Disapprove
          page={'soa'}
          openDisapproveDialog={openDisapproveDialog}
          setOpenDisapproveDialog={setOpenDisapproveDialog}
          setActiveStep={setActiveStep}
          submitFunction={disapproved}
        />
      ) : (
        ''
      )}
    </div>
  )
}
