import React, { useEffect, useState } from 'react'

// MATERIAL COMPONENTS
import {
  Grid, Typography, TableCell,
  TableRow,
  Hidden
} from '@material-ui/core'

// SHARED COMPONENTS
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'

/*
 * PAGE COMPONENTS 
 * 
 * Components customized for current page
*/
import ListViewOptions from './components/ListViewOptions'
import SideNav from './components/SideNav'
import DetailsModal from './components/DetailsModal'
import ListView from './components/ListView'

import ConfigController2 from 'Controllers/ConfigController2'

// API SERVICES
import AuditLogServices from '../../../../../Services/Api/Audit/AuditLogServices'

// STYLES
import style from './AuditLog.module.scss'

import { config } from './config/ViewAllList.js'

export default function ActivityLog(props) {
  // LOCAL CONSTANTS
  const reportType = props.match.params.type
  const initialValue = {
    body: [],
    count: 0,
    search: '',
    filters: {},
    header: [],
    limit: 10,
    order: false,
    orderBy: 'date_of_entry',
    page: 1,
    sort: 'date_of_entry|desc',
    reportType: 'activity'
  }

  // LOCAL STATES
  const [pageInfo, setPageInfo] = useState({ reportType: 'activity', breadCrumb: 'activityLogs', user: '', profilePic: "" })
  const [list, setList] = useState(initialValue)
  const [showModal, setShowModal] = useState()
  const [params, setParams] = useState(initialValue)
  const [itemDetails, setItemDetails] = useState()

  // LOCAL METHODS 
  const onListViewFilterChange = (params) => {
    const _params = {
      limit: list.limit,
      page: list.page,
      sort: `${list.orderBy}|${list.order ? 'asc' : 'desc'}`,
      filters: list.filters,
      reportType: pageInfo.reportType,
      search: list.search,
      accessors: list.accessors,
      ...params
    }
    setParams(_params)
  }

  const fetchAuditTrailLists = () => {
    if (params.reportType) {
      AuditLogServices.getAuditList(params).then(({ data: res }) => {
        console.log(res)
        const { results, count, limit, page } = res.data
        setList(i => ({ ...i, body: results, count, limit, page }))
      })
    }
  }

  const fetchItemDetails = ({ id, activityId }) => {
    switch (pageInfo.reportType) {
      case 'activity':
        AuditLogServices.getActivityDetails(activityId).then(({ data: res }) => {
          const { results } = res.data
          console.log(res)
          setItemDetails({
            ...results[0],
            fieldsData: results.map(item => ({ field_name: item.field_name, old_value: item.old_value, new_value: item.new_value }))
          })
        })
        break;
      case 'audit':
        AuditLogServices.getAuditDetails(id).then(({ data: res }) => {
          const { results } = res.data
          setItemDetails({
            ...results[0],
            fieldsData: results.map(item => ({ field_name: item.field_name, old_value: item.old_value, new_value: item.new_value }))
          })
        })
        break;
      default:
        break;
    }
  }

  useEffect(() => {
    ConfigController2.getUserManagementData()
    .then(({ full_name, profile_pic }) => {
      PageTitleService(full_name)

      if(profile_pic === 'DEFAULT_PIC') profile_pic = ''

      setPageInfo(item => ({ ...item, user: full_name, profilePic: profile_pic }))
    })
  }, [])

  /*
  * CONDITIONAL PAGE DATA 
  * 
  * Choose data to render based on activity log route
  */
  useEffect(() => {
    console.log(reportType)
    switch (reportType) {
      case 'activity':
        setPageInfo(item => ({ ...item, reportType: 'activity', breadCrumb: 'activityLogs' }))
        setParams(item => ({ ...item, reportType: 'activity' }))
        break;
      case 'audit':
        setPageInfo(item => ({ ...item, reportType: 'audit', breadCrumb: 'auditLogs' }))
        setParams(item => ({ ...item, reportType: 'audit' }))
        break;
      default:
        break
    }
  }, [reportType])

  // BREADCRUMB SERVICE
  useEffect(() => {
    pageInfo && BreadcrumbsService.setSelectedItemsWithoutHome([
      pageInfo.breadCrumb,
    ])
  }, [pageInfo])

  // LIST VIEW FETCH ON FILTER CHANGE
  useEffect(() => {
    fetchAuditTrailLists()
  }, [params])

  // RENDER
  return pageInfo.user ?
    <div style={{ marginTop: '2em' }}>
      <Grid container spacing={5}>
        <Grid item xs={12}>
          <div className={style.container}>
            <div className={style.sidenav}>
              <SideNav reportType={pageInfo.reportType} profilePic={pageInfo.profilePic}/>
            </div>
            <div className={style.listView}>
              <ListViewOptions list={list} onListViewTopFilterChange={onListViewFilterChange} params={params} config={config} reportType={pageInfo.reportType}/>
              <div className={style.listWrapper}>
                <ListView
                    id={'audit-log-view-all-list'}
                    config={config}
                    contents={list.body}
                    count={list.count}
                    list={list}
                    fetchItemDetails={fetchItemDetails}
                    onListViewFilterChange={onListViewFilterChange}
                    params={params}
                    setShowModal={setShowModal}
                    reportType={pageInfo.reportType}
                />
              </div>
            </div>
          </div>
        </Grid>
      </Grid>
      <DetailsModal showModal={showModal} setShowModal={setShowModal} itemDetails={itemDetails} />
    </div> :
    null
}
