import React from 'react'
import Grid from '@material-ui/core/Grid'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCalendarWeek } from '@fortawesome/free-solid-svg-icons'

import classes from './DateTimePicker.module.scss'
import { FormHelperText, Typography } from '@material-ui/core'

const moment = require('moment')

export default function DateTimePicker (props) {
  const {
    id,
    label = '',
    required,
    value = null,
    format = null,
    disabled = false,
    onChange = () => {},
    orientation = 'row',
    error = null,
    placeholder = 'Input Date/Time',
    type = 'datetime-local',
    customStyle = {},
    momentFormat = 'lll',
    defaultClass = null,
    labelWidth = null,
    labelJustify = null,
    ...other
  } = props

  const [_error, setError] = React.useState()
  
  React.useEffect(() => {
    setError(error)
  }, [error])

  const handleDateChange = e => {
    setError()
    onChange(moment(e.target.value))
  }

  return (
    <Grid container direction={orientation} className={classes.root}>
      <Grid
        item
        className={`${classes.title} ${orientation === 'row' ? classes.center : ''} ${labelWidth ? classes['text-padding'] : ''}`}
        xs={!labelWidth && orientation === 'row' ? 3 : labelWidth ? labelWidth : false}
      >
        <Grid
          container
          direction='column'
          justify={`${!labelJustify && orientation === 'row' ? 'space-around' : labelJustify ? labelJustify : ''}`}
          className={`${orientation === 'row' ? classes['text-container'] : ''}`}
        >
          <Grid item>
            <Typography className={`${classes.label} ${disabled ? classes.disabled : ''} ${classes[defaultClass] || ''}`}>
              {label}
              {required ? ' *' : ''}
            </Typography>
          </Grid>
        </Grid>
      </Grid>
      <Grid item className={`${classes.wrapper} ${_error ? classes.error : ''}`} xs style={customStyle}>
        <Grid container direction='row'>
          <Grid item className={classes.text} xs>
            <Typography className={`${!value ? classes.background : ''} ${disabled ? classes.disabled : ''}`}>{value ? moment(value).format(momentFormat) : placeholder}</Typography>
          </Grid>
          <Grid item><FontAwesomeIcon icon={faCalendarWeek} className={classes.icon} /></Grid>
        </Grid>
        { !disabled && (
          <input
            type={type}
            className={classes['date-picker']}
            onChange={handleDateChange}
            { ...other }
          />
        )}
      </Grid>
      { _error && (
        <Grid item xs={12} className={classes['error-wrapper']}>
          <Grid container direction={orientation}>
            {orientation && orientation === 'row' && (
              <Grid
                item
                className={`${classes.title} ${orientation === 'row' ? classes.center : ''} ${labelWidth ? classes['text-padding'] : ''}`}
                xs={!labelWidth && orientation === 'row' ? 3 : labelWidth ? labelWidth : false}
              >
                &nbsp;
              </Grid>
            )}
            <Grid item xs>
              <FormHelperText className={classes.error}>{_error}</FormHelperText>
            </Grid>
          </Grid>
        </Grid>
      )}
    </Grid>
  )
}
