import React from 'react'
import { Link } from 'react-router-dom'
import _ from 'lodash'
import moment from 'moment'

import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'

import IconButton from '@material-ui/core/IconButton'
import Typography from '@material-ui/core/Typography'
import CloseIcon from '@material-ui/icons/Close'
import ViewListRoundedIcon from '@material-ui/icons/ViewListRounded'
import { withStyles } from '@material-ui/core/styles'
import TableCell from '@material-ui/core/TableCell'
import TableBody from '@material-ui/core/TableBody'
import TableRow from '@material-ui/core/TableRow'
import { Popper, TextField, Fade, Button } from '@material-ui/core'
import Grid from '@material-ui/core/Grid'

import { CurrencyFormatter, DateFormatterByAbbreviation } from 'Utilities/TextFormatter'
// import SearchTextComponent from 'SharedComponents/SearchTextComponent/SearchTextComponent'
import CustomEnhancedTable from '../CustomEnhancedTable/CustomEnhancedTable'
import CircularLoading from '../../../../../SharedComponents/CircularLoading/CircularLoading'
import classes from './CustomViewAll.module.scss'
import DisplaySettings from '../../Components/FormComponents/DisplaySettings/DisplaySettings'

// Services
import InvoiceExportsServices from 'Services/Api/InvoiceExportsServices'
import ClientJournalToSetupExports from 'Services/Api/ClientJournalToSetupExports'

export default function CustomViewAll (props) {
  const {
    config,
    tile,
    type,
    title,
    displaySettingTitle,
    contents,
    count,
    show,
    setShow,
    list,
    fetchData,
    params,
    accessors,
    isFetching,
    placeholder
  } = props

  const StyledTableCell = withStyles(theme => ({
    head: {
      backgroundColor: theme.palette.common.grey,
      color: theme.palette.common.white
    },
    body: {
      fontSize: 14
    }
  }))(TableCell)

  const StyledTableRow = withStyles(theme => ({
    root: {
      '&:nth-of-type(odd)': {
        backgroundColor: theme.palette.action.hover
      }
    }
  }))(TableRow)

  const [pagination, setPagination] = React.useState({
    count: list ? list.count : 0,
    rowsPerPage: list ? list.limit : 0,
    page: list ? list.page : 0
  })

  React.useEffect(() => {
    setPagination({
      count: list ? list.count : 0,
      rowsPerPage: list ? list.limit : 0,
      page: list ? list.page : 0
    })
  }, [list])

  const tableHeaders = config[tile].headers

  const handleOnClose = () => {
    setShow(false)
    setAnchorEl(null)
  }

  const changeView = params => {
    if (fetchData) {
      fetchData(params)
    }
  }

  // const mainSearchChange = _.debounce(e => {
  //   const params = {
  //     search: e.target.value,
  //     accessors: accessors,
  //     page: 1
  //   }
  //   changeView(params)
  // }, 300)

  const handleDisplaySettingsApplyClicked = range => {
    const params = {
      range: {
        start: range.start,
        end: range.end
      }
    }
    console.log(range.start)
    console.log(range.end)
    fetchData(params)
  }

  const handleDisplaySettingsClicked = params => {
    console.log(params)
    console.log('initial function')
  }

  const handleSearchChanged = search => {
    console.log(search)
    const params = {
      search: search,
      accessors: accessors,
      page: 1
    }
    console.log(params)
    fetchData(params)
  }

  // For Display Settings
  const [anchorEl, setAnchorEl] = React.useState(null)

  const open = Boolean(anchorEl)
  const id = open ? 'date-popper' : undefined

  const tileType = type => {
    let tile
    if (type === 'Assign Analyst' || type === 'Setup Billing Cycle') {
      tile = 'CLIENT_TRANSACTION_JOURNAL'
    }
    if (type === 'Pending Invoices' || type === 'Approved Invoices' || type === 'Invoice Submitted' || type === 'Client Invoices To Validate') {
      tile = 'INVOICE'
    }
    return tile
  }

  const downloadServices = args => {
    const { type, contents } = args
    const list = []
    for (const i of contents) {
      console.log(i)
      if (type === 'Assign Analyst' || type === 'Setup Billing Cycle') {
        list.push({
          field_1: i.client.account_number,
          field_2: i.client.account_name,
          field_3: i.contract.type,
          field_4: DateFormatterByAbbreviation(i.contract.effectivity)
        })
      }
      if (type === 'Approved Invoices') {
        list.push({
          field_1: DateFormatterByAbbreviation(i.transaction.date),
          field_2: i.transaction.number,
          field_3: i.client.account_name,
          field_4: i.approved_by ? i.approved_by : 'undefined',
          field_5: DateFormatterByAbbreviation(i.date.approved),
          field_6: i.summary.sub_total
        })
      }
      if (type === 'Invoice Submitted' || type === 'Client Invoices To Validate') {
        list.push({
          field_1: DateFormatterByAbbreviation(i.transaction.date),
          field_2: i.transaction.number,
          field_3: i.client.account_name,
          field_4: i.submitted_by ? i.submitted_by : 'undefined',
          field_5: DateFormatterByAbbreviation(i.date.modified),
          field_6: i.summary.sub_total
        })
      } 
      if (type === 'Pending Invoices') {
        list.push({
          field_1: DateFormatterByAbbreviation(i.transaction.date),
          field_2: i.transaction.number,
          field_3: i.client.account_name,
          field_4: DateFormatterByAbbreviation(i.date.modified),
          field_5: i.summary.sub_total
        })
      }
    }
    switch (tileType(type)) {
      case 'CLIENT_TRANSACTION_JOURNAL':
        ClientJournalToSetupExports.getClientJournalToSetupExports(list, {tile: type, type: tileType(type)}).then(response => {
          const url = URL.createObjectURL(new Blob([response.data]))
          const link = document.createElement('a')
          link.href = url
          link.setAttribute('download', `${type}.xlsx`)
          document.body.appendChild(link)
          link.click()
        })
        break
      case 'INVOICE':
        InvoiceExportsServices.getClientInvoiceExports(list, {tile: type, type: tileType(type)}).then(response => {
          const url = URL.createObjectURL(new Blob([response.data]))
          const link = document.createElement('a')
          link.href = url
          link.setAttribute('download', `${type}.xlsx`)
          document.body.appendChild(link)
          link.click()
        })
    }

  }

  return (
    <Dialog
      fullWidth
      maxWidth='lg'
      onClose={handleOnClose}
      aria-labelledby='customized-dialog-title'
      open={show}
      className={classes.root}
      PaperProps={{ style: { height: '100%', display: 'flex', backgroundColor: '#f4f6f8' } }}
    >
      <DialogTitle disableTypography className={classes['header-container']}>
        <div className={classes.header}>
          <Typography id='modal-title' variant='h6' className={classes.title} style={{fontWeight: 'bold'}}>
            {title}
          </Typography>
          <IconButton
            id='modal-close-button'
            aria-label='close'
            className={classes['close-button']}
            onClick={handleOnClose}
          >
            <CloseIcon />
          </IconButton>
          <div className={classes.clear} />
        </div>
      </DialogTitle>
      <DialogContent className={classes['content-container']}>
        <div className={classes['search-container']}>
          {/* <div id='modal-search-field' className={classes['search-field']}>
            <SearchTextComponent
              placeholder={placeholder}
              onChange={e => {
                e.persist()
                mainSearchChange(e)
              }}
            />
          </div> */}
          <DisplaySettings
            placeholder={placeholder}
            displaySettings
            displaySettingTitle='Effectivity Date Range*'
            startDateError='Please enter a valid Effectivity start date'
            endDateError='Please enter a valid Effectivity end date'
            laterStartDateError='Please select an effectivity start date that is not later than the current date'
            laterEndDateError='Please select an effectivity end date that is not later than the current date'
            handleOnDisplaySettingsClicked={handleDisplaySettingsClicked}
            handleOnDisplaySettingsApplyClicked={handleDisplaySettingsApplyClicked}
            onChange={handleSearchChanged}
          />
          {/* <Link
            id='exports-link'
            className={classes['exports']}
            onClick={() => downloadServices({ type: type, contents: contents })}
            color='primary'
            size='medium'
          >
            <Grid container id='exports-icon'>
              <ViewListRoundedIcon className={classes['exports-icon']} />
              <Typography className='text-link underline'>Export</Typography>
            </Grid>
          </Link> */}
        </div>

        <CustomEnhancedTable
          count={count}
          changeView={changeView}
          tableHeaders={tableHeaders}
          pagination={pagination}
          search
          sort
          noSearchIcon={true}
          fetchData={fetchData}
          params={params}
        >
          <TableBody>
            {isFetching && (
              <StyledTableRow>
                <StyledTableCell component='th' colspan={10}>
                  <CircularLoading />
                </StyledTableCell>
              </StyledTableRow>
            )}
            {!isFetching &&
              contents &&
              contents.map((row, contentsIndex) => (
                <StyledTableRow key={contentsIndex}>
                  <StyledTableCell component='th'>
                    <Typography variant='caption' align='left'>
                      {type === 'Assign Analyst' || type === 'Setup Billing Cycle' ? row.client.account_number : ''}
                      {type === 'Approved Invoices' ||
                      type === 'Pending Invoices' ||
                      type === 'Invoice Submitted' ||
                      type === 'Client Invoices To Validate'
                        ? row.transaction
                          ? DateFormatterByAbbreviation(row.transaction.date)
                          : null
                        : null}
                      {type === 'TPA Monitoring' ? row.client.account_number : ''}
                      {type === 'TPA Replenishment' ? row.client.account_number : ''}
                    </Typography>
                  </StyledTableCell>
                  <StyledTableCell component='th'>
                    <Typography variant='caption' align='left'>
                      {type === 'Assign Analyst' || type === 'Setup Billing Cycle' ? row.client.account_name : ''}
                      {type === 'Approved Invoices' ||
                      type === 'Pending Invoices' ||
                      type === 'Invoice Submitted' ||
                      type === 'Client Invoices To Validate'
                        ? row.transaction
                          ? row.transaction.number
                          : null
                        : null}
                      {type === 'TPA Monitoring' ? row.client.account_name : ''}
                      {type === 'TPA Replenishment' ? row.client.account_name : ''}
                    </Typography>
                  </StyledTableCell>
                  <StyledTableCell component='th' align='left'>
                    <Typography variant='caption' align='left'>
                      {type === 'Assign Analyst' || type === 'Setup Billing Cycle' ? row.contract.type : ''}
                      {type === 'Approved Invoices' ||
                      type === 'Pending Invoices' ||
                      type === 'Invoice Submitted' ||
                      type === 'Client Invoices To Validate'
                        ? row.billing_client_contract[0]
                          ? row.billing_client_contract[0].client.account_name
                          : null
                        : null}
                      {type === 'TPA Monitoring' ? row.contract.type : ''}
                      {type === 'TPA Replenishment' ? row.contract.type : ''}
                    </Typography>
                  </StyledTableCell>
                  <StyledTableCell component='th'>
                    <Typography variant='caption' align='left'>
                      {type === 'Assign Analyst' || type === 'Setup Billing Cycle'
                        ? DateFormatterByAbbreviation(row.contract.effectivity)
                        : null}
                      {type === 'Client Invoices To Validate' ? row.submitted_by : null}
                      {type === 'Approved Invoices' ? row.approved_by : null}
                      {/* Date here should be date submitted, put date.modified as temp workaround */}
                      {type === 'Pending Invoices' ? DateFormatterByAbbreviation(row.date.modified) : null}
                      {/* Date here should be date submitted, put date.modified as temp workaround */}
                      {type === 'Invoice Submitted' ? DateFormatterByAbbreviation(row.date.modified) : null}
                      {type === 'TPA Replenishment'
                        ? row.contract.tpa && row.contract.tpa.rsd_usage && row.contract.tpa.rsd ? `${parseInt((row.contract.tpa.rsd_usage / row.contract.tpa.rsd) * 100)}%` : '0%' : null
                      }
                    </Typography>
                  </StyledTableCell>

                  {type === 'Approved Invoices' || type === 'Client Invoices To Validate' ? (
                    <StyledTableCell component='th'>
                      <Typography variant='caption' align='left'>
                        {/* Date here should be date approved, put date.modified as temp workaround */}
                        {type === 'Approved Invoices' ? DateFormatterByAbbreviation(row.date.modified) : null}
                        {/* Date here should be date submitted, put date.modified as temp workaround */}
                        {type === 'Client Invoices To Validate' ? DateFormatterByAbbreviation(row.date.modified) : null}
                      </Typography>
                    </StyledTableCell>
                  ) : null}
                  {type === 'TPA Monitoring' ? (
                    <StyledTableCell component='th'>
                      <Typography variant='caption' align='left'>
                        {type === 'TPA Monitoring'
                          ? row.invoice
                            ? DateFormatterByAbbreviation(row.invoice.date.created)
                            : null
                          : null}
                        {type === 'TPA Replenishment'
                          ? row.invoice
                            ? DateFormatterByAbbreviation(row.invoice.date.created)
                            : null
                          : null}
                      </Typography>
                    </StyledTableCell>
                  ) : null}

                  {type === 'Pending Invoices' ||
                  type === 'Approved Invoices' ||
                  type === 'Invoice Submitted' ||
                  type === 'Client Invoices To Validate'
                    ? (
                      <StyledTableCell component='th' align='right'>
                        <Typography variant='caption'>{CurrencyFormatter(row.summary.sub_total)}</Typography>
                      </StyledTableCell>
                    ) : null}

                  <StyledTableCell component='th' align='left'>
                    {type === 'Assign Analyst' && (
                      <Link to={`/clients/assign/${row._id}/${row.client.account_number}`}>
                        <Typography variant='caption' className='text-link underline'>
                          Setup
                        </Typography>
                      </Link>
                    )}

                    {type === 'Setup Billing Cycle' && (
                      <Link to={`/clients/analyze/${row._id}/${row.client.account_number}`}>
                        <Typography variant='caption' className='text-link underline'>
                          Setup
                        </Typography>
                      </Link>
                    )}

                    {type === 'Pending Invoices' && (
                      <Link to={`/billing/invoice/pending/${row.transaction.number}`}>
                        <Typography variant='caption' className='text-link underline'>
                          Edit
                        </Typography>
                      </Link>
                    )}

                    {type === 'Invoice Submitted' && (
                      <Link to={`/billing/invoice/forapproval/${row.transaction.number}`}>
                        <Typography variant='caption' className='text-link underline'>
                          View
                        </Typography>
                      </Link>
                    )}

                    {type === 'Client Invoices To Validate' && (
                      <Link to={`billing/client/invoice/validate/${row.transaction.number}`}>
                        <Typography variant='caption' className='text-link underline'>
                          Validate
                        </Typography>
                      </Link>
                    )}

                    {type === 'Approved Invoices' && (
                      <Link to={`/billing/invoice/approved/${row.transaction.number}`}>
                        <Typography variant='caption' className='text-link underline'>
                          View
                        </Typography>
                      </Link>
                    )}

                    {type === 'TPA Monitoring' && (
                      <Link to={'/'}>
                        <Typography variant='caption' className='text-link underline'>
                          View
                        </Typography>
                      </Link>
                    )}

                    {type === 'TPA Replenishment' && (
                      <Link to={`/billing/journal/transactions/${row.client.account_number}`}>
                        <Typography variant='caption' className='text-link underline'>
                          View
                        </Typography>
                      </Link>
                    )}
                  </StyledTableCell>
                </StyledTableRow>
              ))}
          </TableBody>
        </CustomEnhancedTable>
      </DialogContent>
    </Dialog>
  )
}
