/** React Libraries */
import React from 'react'
import { Link, useHistory } from 'react-router-dom'
/** Material UI Libraries */
import { Typography, Backdrop, CircularProgress } from '@material-ui/core'
import Box from '@material-ui/core/Box'
import Button from '@material-ui/core/Button'
import RemoveCircleIcon from '@material-ui/icons/RemoveCircle'
import ReplyIcon from '@material-ui/icons/Reply'
import VisibilityIcon from '@material-ui/icons/Visibility'
import TableCell from '@material-ui/core/TableCell'
import TableBody from '@material-ui/core/TableBody'
import TableRow from '@material-ui/core/TableRow'
/** RBAC */
/** Utilities */
import * as moment from 'moment'
import { CurrencyFormatter } from 'Utilities/TextFormatter'
/** Standard Components */
import ContentHeader from 'StandardComponents/ContentHeader/ContentHeader'
/** Shared Components */
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
/** Page Components */
import CustomEnhancedTable from '../../Components/CustomEnhancedTable/CustomEnhancedTable'
import HeaderDetails from '../../Components/HeaderDetails/HeaderDetails'
import FormDrawer from '../../Components/FormDrawer/FormDrawer'
import Void from '../../Components/Void/Void'
import Approve from 'StandardComponents/Approve/Approve'
import Disapprove from 'StandardComponents/Disapprove/Disapprove'
/** CSS */
import classes from './ClientInvoicesToValidatePage.module.scss'
/** Services */
import BillingTransactionService from 'Services/Api/Billing/Transactions/BillingTransactionService'
import InvoiceViewServices from 'Services/Api/Billing/Transactions/Invoice/InvoiceViewServices'
import BillingClientInvoicesServices from 'Services/Api/Billing/Transactions/Invoice/BillingClientInvoicesServices'

// Security
import RBAC from 'Security/RBAC'

const tableHeaders = [
  {
    id: 'pending-invoice-transaction-type',
    numeric: false,
    disablePadding: false,
    label: 'Transaction Type',
    center: true
  },
  {
    id: 'pending-invoice-member-account-id',
    numeric: false,
    disablePadding: false,
    label: 'Member Account ID',
    center: true
  },
  {
    id: 'pending-invoice-member-name',
    numeric: false,
    disablePadding: false,
    label: 'Member Name',
    center: true
  },
  {
    id: 'pending-invoice-description',
    numeric: false,
    disablePadding: false,
    label: 'Description',
    center: true
  },
  {
    id: 'pending-invoice-tax-type',
    numeric: false,
    disablePadding: false,
    label: 'Tax Type',
    center: true
  },
  {
    id: 'pending-invoice-amount',
    numeric: false,
    disablePadding: false,
    label: 'Amount',
    center: true
  },
  {
    id: '',
    numeric: false,
    disablePadding: false,
    label: '',
    center: true
  }
]

export default function InvoiceToBeRevised (props = {}) {
  PageTitleService()
  const history = useHistory()
  const [openDialogVoid, setOpenDialogVoid] = React.useState(false)
  const [openDialogApprove, setOpenDialogApprove] = React.useState(false)
  const [openDialogDisapprove, setOpenDialogDisapprove] = React.useState(false)
  const transaction_number = props.match.params.transaction_number
  const type = props.match.params.type

  const [pendingInvoices, setPendingInvoices] = React.useState({
    data: []
  })

  const [backdrop, setBackdrop] = React.useState(false)

  const [disabledSave, setDisabledSave] = React.useState(false)
  const initialValue = {
    body: [],
    count: 0,
    filters: null,
    header: [],
    limit: 10,
    order: true,
    orderBy: '_id',
    page: 1,
    sort: ''
  }

  const [list, setList] = React.useState(initialValue)
  const [params, setParams] = React.useState({})

  const [invoiceDetail, setInvoiceDetail] = React.useState({})
  React.useEffect(() => {
    // Promise.all([BillingClientInvoicesServices.getTobeRevisedInvoice({ transaction_number })]).then(values => {
    //   console.log(values)
    //   setPendingInvoices(prevState => ({
    //     ...prevState,
    //     data: values[0].data.data
    //   }))
    // })
    BreadcrumbsService.setSelectedItems(['dashboard', 'listClientInvoiceToValidate', `validateInvoice`])
    Promise.all([BillingClientInvoicesServices.getTobeRevisedInvoice({ transaction_number: transaction_number })]).then(values => {
      console.log(values)
      setPendingInvoices(prevState => ({
        ...prevState,
        data: values[0].data.data
      }))
    })
    // TODO API CALL FOR CURRENT  INVOICE TRANSACTIONS
    fetchData({})
  }, [])

  const fetchData = params => {
    const _params = {
      limit: list.limit,
      page: list.page,
      sort: `${list.orderBy}|${list.order ? 'asc' : 'desc'}`,
      filters: list.filters,
      transactionNumber: transaction_number,
      ...params
    }
    setParams(_params)

    // BillingClientInvoicesServices.getInvoiceDetails(_params).then(({ data: res }) => {
    //   const { results, count, limit, page } = res.data
    //   setList(i => ({
    //     ...i,
    //     body: results,
    //     count,
    //     limit,
    //     page,
    //     sort: _params.sort,
    //     orderBy: _params.orderBy ? _params.orderBy : list.orderBy
    //   }))
    // })

    InvoiceViewServices.getInvoiceDetails(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      const orderByValue = _params.orderBy ? _params.orderBy : list.orderBy
      const orderValue = typeof _params.order === 'boolean' ? _params.order : list.order
      setList(i => ({ ...i, body: results, count, limit, page, sort: _params.sort, orderBy: orderByValue, order: orderValue }))
    })
  }
  const transactionInfo = pendingInvoices.data
  const clientInfo = pendingInvoices.data.client
  const billingInfo = pendingInvoices.data.billing
  const contractInfo = pendingInvoices.data.contract
  const summaryInfo = pendingInvoices.data.summary
  const dateInfo = pendingInvoices.data.date
  const invoiceInfo = invoiceDetail.data
  function handleClose () {
    history.push('/')
  }

  const handleVoid = async () => {
    setBackdrop(true)
    await BillingClientInvoicesServices.assignSupervisor({ transaction_number: transaction_number })
    BillingClientInvoicesServices.routeInvoice({ transaction_number: transaction_number, action: 'void' })
    // TODO add enum that is same with transaction type values in api
    // await BillingTransactionService.void({ transactionNo: transaction_number, transactionType: 'invoice' })

    history.push('/')
  }

  const handleDisapprove = async () => {
    setBackdrop(true)
    await BillingClientInvoicesServices.assignSupervisor({ transaction_number: transaction_number })
    BillingClientInvoicesServices.routeInvoice({ transaction_number: transaction_number, action: 'disapprove' })
    // TODO add enum that is same with transaction type values in api
    // await BillingTransactionService.disapprove({ transactionNo: transaction_number, transactionType: 'invoice' })
    history.push('/')
  }

  const handleApprove = async () => {
    setBackdrop(true)
    await BillingClientInvoicesServices.assignSupervisor({ transaction_number: transaction_number })
    await BillingClientInvoicesServices.routeInvoice({ transaction_number: transaction_number, action: 'approve' })
    // TODO add enum that is same with transaction type values in api
    // await BillingTransactionService.approve({ transactionNo: transaction_number, transactionType: 'invoice' })
    history.push('/')
  }

  const handleApproveModal = () => {
    setOpenDialogApprove(true)
  }

  const handleVoidModal = () => {
    setOpenDialogVoid(true)
  }

  function handleDisapproveModal () {
    setOpenDialogDisapprove(true)
  }

  const pagination = {
    count: list ? list.count : 0,
    rowsPerPage: list ? list.limit : 0,
    page: list ? list.page : 0
  }

  const changeView = params => {
    fetchData(params)
  }

  return (
    <div>
      <HeaderDetails>
        <Typography id='header-details-corporate-name' variant='h5'>
          <Box fontWeight='fontWeightBold' m={1}>
            Validate Invoice
          </Box>
        </Typography>
      </HeaderDetails>
      <FormDrawer
        drawerHeight={'570px'}
        drawerContent={
          <div>
            <Typography variant='h6'>
              <Box fontWeight='fontWeightBold' m={1}>{clientInfo && clientInfo.account_name}</Box>
            </Typography>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-account-number-field' color='textSecondary'>
                ACCOUNT NO.
              </Typography>
              <Typography id='drawer-account-number-value'>
                <Box fontWeight='fontWeightBold' m={1}>{clientInfo && clientInfo.account_number}</Box>
              </Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-account-status-field' color='textSecondary'>
                ACCOUNT STATUS
              </Typography>
              <Typography id='drawer-account-status-value'>
                <Box fontWeight='fontWeightBold' m={1}>{clientInfo && clientInfo.account_status}</Box>
              </Typography>
            </div>
            <div className={classes['drawer-content']}>
              <Typography id='drawer-payment-term-field' color='textSecondary'>
                PAYMENT TERM
              </Typography>
              <Typography id='drawer-payment-term-value'>
                <Box fontWeight='fontWeightBold' m={1}>{contractInfo && contractInfo.payment_term}</Box>
              </Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-tax-type-field' color='textSecondary'>
                TAX TYPE
              </Typography>
              <Typography id='drawer-tax-type-value'>
                <Box fontWeight='fontWeightBold' m={1}>{clientInfo && clientInfo.tax_type}</Box>
              </Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-account-tin-field' color='textSecondary'>
                ACCOUNT TIN
              </Typography>
              <Typography id='drawer-account-tin-value'>
                <Box fontWeight='fontWeightBold' m={1}>{clientInfo && clientInfo.tin}</Box>
              </Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-billing-cyle-field' color='textSecondary'>
                BILLING CYCLE
              </Typography>
              <Typography id='drawer-billing-cyle-value'>
                <Box fontWeight='fontWeightBold' m={1}>{billingInfo && billingInfo.cycle.code + ' - ' + billingInfo.cycle.name}</Box>
              </Typography>
            </div>

            {/* <div className={classes['drawer-content']}>
              <Typography id='drawer-corporate-tin-field' color='textSecondary'>
                CORPORATE TIN
              </Typography>
              <Typography id='drawer-corporate-tin-value'>
                <Box fontWeight='fontWeightBold' m={1}>{clientInfo && clientInfo.tin}</Box>
              </Typography>
            </div> */}
            <div className={classes['drawer-content']}>
              <Typography id='drawer-contract-plan-type-field' color='textSecondary'>
                CONTRACT TYPE
              </Typography>
              <Typography id='drawer-contract-plan-type-value'>
                <Box fontWeight='fontWeightBold' m={1}>{contractInfo && contractInfo.type}</Box>
              </Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-effectivity-date-field' color='textSecondary'>
                EFFECTIVITY DATE
              </Typography>
              <Typography id='drawer-effectivity-date-value'>
                <Box fontWeight='fontWeightBold' m={1}>{moment(contractInfo && contractInfo.effectivity).format('MMM DD, YYYY')}</Box>
              </Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-expiration-date-field' color='textSecondary'>
                EXPIRATION DATE
              </Typography>
              <Typography id='drawer-expiration-date-value'>
                <Box fontWeight='fontWeightBold' m={1}>{moment(contractInfo && contractInfo.expiration).format('MMM DD, YYYY')}</Box>
              </Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-location-field' color='textSecondary'>
                LOCATION
              </Typography>
              <Typography id='drawer-location-value'>
                <Box fontWeight='fontWeightBold' m={1}>{clientInfo && clientInfo.location}</Box>
              </Typography>
            </div>
          </div>
        }
      >
        <div className={classes['export-view-billing-cycle-wrapper']}>
          <div className={classes['export-view-content']}>

            {RBAC.hasPolicy({or: ['BC-042']}) && (
              <Link id='export-link' to={`/`} className={classes['export-link']}>
                <ReplyIcon id='export-icon' className={classes['export-icon']} />
                Export
              </Link>
            )}
            <Link id='view-billing-link' to={`/`} className={classes['view-link']}>
              <VisibilityIcon id='export-icon' className={classes['export-icon']} />
              View Bill Run Inclusion
            </Link>
          </div>
        </div>
        <ContentHeader
          summaryFields={['Total Taxable Amount', 'Total Tax (VAT) Amount', 'Total Amount']}
          summaryValues={[
            summaryInfo && summaryInfo.sub_total,
            summaryInfo && summaryInfo.tax_amount,
            summaryInfo && summaryInfo.sub_total + summaryInfo.tax_amount
          ]}
        >
          <div className={classes['content-header-details']}>
            <div className={classes.fields}>
              <Typography id='content-details-invoice-number-field' variant='h6' color='textSecondary'>
                INVOICE NO.
              </Typography>
              <Typography id='content-details-trasaction-date-field' color='textSecondary'>
                TRANSACTION DATE
              </Typography>
              {/* <Typography id='content-details-trasaction-date-field' color='textSecondary'>
                PERIOD COVERED
              </Typography> */}
              <Typography id='content-details-ticket-number-field' color='textSecondary'>
                TICKET NO
              </Typography>
              {/* <Typography id='content-details-ticket-number-field' color='textSecondary'>
                REFERENCE NO
              </Typography> */}
            </div>
            <div className={classes.values}>
              <Typography id='content-details-invoice-number-value' variant='h6'>
                {transaction_number}
              </Typography>
              <Typography id='content-details-trasaction-date-value'>
                {moment(dateInfo && dateInfo.created).format('MMM DD, YYYY')}
              </Typography>
              {/* <Typography
                id='content-details-trasaction-date-value'
                style={{ width: '100px', 'white-space': 'nowrap', overflow: 'visible' }}
              >
                {moment(transactionInfo.transaction && transactionInfo.transaction.period.start).format('MMM DD, YYYY')}{' '}
                - {moment(transactionInfo.transaction && transactionInfo.transaction.period.end).format('MMM DD, YYYY')}
              </Typography> */}
              <Typography id='content-details-ticket-number-value' style={{ width: '100px' }}>
                {transactionInfo && transactionInfo.case_number}
              </Typography>
            </div>
          </div>
        </ContentHeader>
        <CustomEnhancedTable
          count={list.count}
          changeView={changeView}
          tableHeaders={tableHeaders}
          pagination={pagination}
          search
          sort
          fetchData={fetchData}
          params={params}
        >
          <TableBody>
            {list &&
              list.body.map((detail, detailIndex) => (
                <TableRow hover tabIndex={-1} key={detailIndex}>
                  <TableCell
                    // id={`${content[0]}-${contentsIndex}`}
                    key={detailIndex}
                    align='center'
                  >
                    {detail.billingCodeType}
                  </TableCell>
                  <TableCell
                    // id={`${content[0]}-${contentsIndex}`}
                    key={detailIndex}
                    align='center'
                  >
                    {detail.memberAccountId}
                  </TableCell>
                  <TableCell
                    // id={`${content[0]}-${contentsIndex}`}
                    key={detailIndex}
                    align='center'
                  >
                    {detail.memberName}
                  </TableCell>
                  <TableCell
                    // id={`${content[0]}-${contentsIndex}`}
                    key={detailIndex}
                    align='center'
                  >
                    {detail.description}
                  </TableCell>
                  <TableCell
                    // id={`${content[0]}-${contentsIndex}`}
                    key={detailIndex}
                    align='right'
                  >
                    {detail.taxTypeDescription}
                  </TableCell>
                  <TableCell
                    // id={`${content[0]}-${contentsIndex}`}
                    key={detailIndex}
                    align='right'
                  >
                    {CurrencyFormatter(detail.taxableAmount)}
                  </TableCell>
                  <TableCell
                    // id={`${content[0]}-${contentsIndex}`}
                    key={detailIndex}
                    align='right'
                  />
                </TableRow>
              ))}
          </TableBody>
        </CustomEnhancedTable>
        <div className={classes.button}>

          <Button
            id='close-button'
            variant='contained'
            color='primary'
            className={classes['button-close']}
            onClick={e => handleClose()}
          >
            Back
          </Button>

          <div className={classes['item-right-side']}>

            {RBAC.hasPolicy({or: ['BC-040']}) && (
              <Button
                id='void-button'
                variant='contained'
                color='primary'
                className={classes['button-negative']}
                disabled={disabledSave}
                onClick={e => handleVoidModal()}
              >
                Void
              </Button>
            )}

            {RBAC.hasPolicy({or: ['BC-039']}) && (
              <Button
                id='void-button'
                variant='contained'
                color='primary'
                className={classes['button-disapprove']}
                disabled={disabledSave}
                onClick={e => handleDisapproveModal()}
              >
                Disapprove
              </Button>
            )}

            {RBAC.hasPolicy({or: ['BC-039']}) && (
              <Button
                id='appprove-link'
                variant='contained'
                className={classes['button-positive']}
                onClick={e => handleApproveModal()}
              >
                Approve
              </Button>
            )}

          </div>
        </div>
      </FormDrawer>
      {openDialogApprove === true ? (
        <Approve
          page={'invoice'}
          openDialog={openDialogApprove}
          setOpenDialog={setOpenDialogApprove}
          submitFunction={() => handleApprove(transaction_number)}
          disapproveFunction={() => handleDisapprove(transaction_number)}
        />
      ) : (
        ''
      )}
      {openDialogDisapprove === true ? (
        <Disapprove
          page={'invoice'}
          openDisapproveDialog={openDialogDisapprove}
          setOpenDisapproveDialog={setOpenDialogDisapprove}
          submitFunction={() => handleDisapprove(transaction_number)}
        />
      ) : (
        ''
      )}
      {openDialogVoid === true ? (
        <Void
          page={'invoice'}
          openDialog={openDialogVoid}
          setOpenDialog={setOpenDialogVoid}
          submitFunction={() => handleVoid(transaction_number)}
        />
      ) : (
        ''
      )}
      {backdrop === true ? (
        <Backdrop className={classes.backdrop} open>
          <CircularProgress color='primary' />
        </Backdrop>
      )
        : (
          ''
        )}
    </div>
  )
}
