import Grid from '@material-ui/core/Grid'
import MenuItem from '@material-ui/core/MenuItem'
import React from 'react'
import Select from '@material-ui/core/Select'
import { FormHelperText, Typography } from '@material-ui/core'

// Style
import classes from './style.module.scss'

// Service
const numeral = require('numeral')

export default function SelectComponent (props) {
  const defaultFunction = function () {}
  let {
    disabled = false,
    onChange = defaultFunction,
    required = false,
    select = [],
    label = '',
    value = '',
    direction = 'column',
    defaultClass = null,
    labelWidth = null,
    showPlaceholder = true,
    error = null,
    multiple = false,
    ...other
    } = props

  const [multiline, setMultiline] = React.useState()
  const [_error, setError] = React.useState()

  React.useEffect(() => {
    setError(error)
  }, [error])

  return (
    <Grid className={classes.root} container direction={direction}>
      <Grid
        item
        xs={!labelWidth && direction === 'row' ? 2 : labelWidth ? labelWidth : false}
        className={`${direction === 'row' ? classes['text-padding'] : ''}`}
      >
        <Grid
          container
          direction={direction === 'row' ? 'column' : direction}
          justify={`${direction === 'row' ? 'space-around' : ''}`}
          className={`${direction === 'row' ? classes['vertical-container'] : ''}`}
        >
          <Grid item>
            <Typography variant='subtitle2' className={`${classes.label} ${disabled ? classes.disabled : ''} ${classes[defaultClass] || ''}`}>
              {label}
              {required ? ' *' : ''}
            </Typography>
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs className={`${classes.wrapper} ${_error ? classes.error : ''}`}>
        <Select
          className={`${classes['input-component']} 
            ${!value || value === '' || value === null ? classes.placeholder : ''}`}
          disabled={!!disabled}
          disableUnderline
          displayEmpty
          onChange={e => { onChange({ value: e.target.value }); setMultiline(); setError(); }}
          inputProps={{ 'aria-label': 'Without label' }}
          value={value || ''}
          onOpen={() => setMultiline(true)}
          onClose={() => setMultiline()}
          multiple={multiple}
          { ...other }
        >
          {select.length === 0 && (
            <MenuItem value='' disabled>Select {label}</MenuItem>
          )}
          {showPlaceholder && (
            <MenuItem value='' disabled>
              Select {label}
            </MenuItem>
          )}
          {!showPlaceholder && !multiline && (
            <MenuItem value='' disabled>
              Select {label}
            </MenuItem>
          )}
          {select.map((item, index) => (
            <MenuItem index={index} value={item.value} disabled={!!item.disabled}>
              <React.Fragment>
                {!multiline && item.name}
                {multiline && (
                  <React.Fragment>
                    {item.names && (
                      <Grid container direction='column'>
                        {item.names.map(name => (
                          <Grid item xs={12}>{name}</Grid>
                        ))}
                      </Grid>
                    )}
                    {!item.names && item.name}
                  </React.Fragment>
                )}
              </React.Fragment>
            </MenuItem>
          ))}
        </Select>
      </Grid>
      { _error && (
        <Grid item xs={12} className={classes['error-wrapper']}>
          <Grid container direction={direction}>
            { direction !== 'column' && (
              <Grid
                item
                xs={!labelWidth && direction === 'row' ? 2 : labelWidth ? labelWidth : false}
                className={`${direction === 'row' ? classes['text-padding'] : ''}`}
              >
                &nbsp;
              </Grid>
            )}
            <Grid item xs>
              <FormHelperText className={classes.error}>{_error}</FormHelperText>
            </Grid>
          </Grid>
        </Grid>
      )}
    </Grid>
  )
}
